import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";

import userProfile from "../../images/user-profile.PNG";
import { AuthContext } from "../context/authContext/AuthState";
import SettingsSidebar from "./SettingsSidebar"

const Sidebar = (props) => {
  const authCxt = useContext(AuthContext);

  useEffect(() => {
  }, [])
  const indicateActiveUrl = () => {
    let currentPage = props.location.pathname;
    let sidediv = document.querySelector(".dashboard-sidebar");
    if (sidediv) {
      let sidelinks = sidediv.querySelectorAll("a");
      if (sidelinks) {
        sidelinks.forEach((a) => {
          if (RegExp(a.pathname).test(currentPage)) {
            a.classList.add("active");
          }else if (RegExp(a.classList.contains("active"))) {
            a.classList.remove("active");
          }
        });
      }
    }
  };
  indicateActiveUrl()
  return  (
    <div className="col-md-3">
      <button id="dashboard-navigation" className="collapsible d-md-none">
        <span className="mr-3">
          <i className="fa fa-bars"></i>
        </span>
        Dashboard Navigation
      </button>
      <div className="dashboard-sidebar d-sm-none d-md-block d-lg-block">
        <div className="seller-info text-center">
          <div className="tinkoko-member">
            <i className="fa fa-star"></i> Member
          </div>
          <img className="img-fluid" src={userProfile} alt="" />
          <h5>
            {authCxt.user && authCxt.user.firstName} {authCxt.user &&authCxt.user.lastName}
          </h5>
          <h6>
            <span>
              {authCxt.user &&authCxt.user.state}, {authCxt.user &&authCxt.user.country}
            </span>{" "}
            | <span>{authCxt.user &&authCxt.user.phone}</span>
          </h6>
        </div>
        <div className="dashboard-sidebar-links">
          <ul>
            <li>
              <Link to="/seller/my-products">
                <i className="fa fa-list"></i> My Products
              </Link>
            </li>
            <li>
              <Link to="/seller/buy-now-sales">
                <i className="fa fa-shopping-basket"></i> Buy Now Sales
              </Link>
            </li>
            <li>
              <Link to="/seller/account" className="">
                <i className="fa fa-wallet"></i> Accounts
              </Link>
            </li>
            <li>
              <Link to="/seller/my-promotions">
                <i className="fa fa-chart-pie"></i> My Promotions
              </Link>
            </li>
            <li>
              <Link to="/seller/my-advertisements">
                <i className="fa fa-bullhorn"></i> My Advertisement
              </Link>
            </li>
            <li>
              <Link to="/seller/supports">
                <i className="fa fa-comment-dots"></i> Support / Feedback
              </Link>
            </li>
            <li>
              <Link to="/seller/my-statistics">
                <i className="fa fa-chart-bar"></i> My Statistics
              </Link>
            </li>
            <li>
              <Link to="/seller/settings">
                <i className="fa fa-cog"></i> Settings
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
