import React from 'react'

const MobilePhoneSettings = () => {
    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-phone"></i>
                    Mobile Phone Settings
                </h4>
            </div>

            <div className="row mobile-phone-settings-next justify-content-center">
                <div className="col-md-7">
                    <p>To change the number: enter the new phone number, click the continue button. After that you will receive an otp to the phone number. Input the pin to validate the number.</p>
                    <form action="">
                        <div className="form-row align-items-center">
                            <div className="col-md-12">
                                <div className="form-field">
                                    <label for="new-phone-number">Enter your new phone number</label>
                                    <input type="text" name="new-phone-number" id="new-phone-number" required/>
                                </div>
                            </div>
                            <div className="col-md-8">
                                <div className="form-field">
                                    <label for="otp">Enter OTP</label>
                                    <input type="text" name="otp" id="otp" required/>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <a href="#" className="reset-otp">Resend OTP <i className="fa fa-redo-alt"></i></a>
                           </div>
                        </div>
                        <button type="submit" className="submit-button">Submit</button>
                    </form>
                </div>
            </div>

        </div>
    </div>
    )
}

export default MobilePhoneSettings
