import React, { useState, useContext, useEffect } from "react";
import { SettingsContext } from "./SettingsState";
import { AuthContext } from "../../context/authContext/AuthState";

const SocialNetworks = () => {
  const settingsCxt = useContext(SettingsContext);
  const authCxt = useContext(AuthContext);
  const [facebook, setFacebook] = useState("");
  const [twitter, setTwitter] = useState("");
  const [instagram, setInstagram] = useState("");
  const [linkedin, setLinkedin] = useState("");

  useEffect(() => {
    console.log(settingsCxt, " data inside the settings context ");
    getModifySocialNetwork();
  }, []);

  const modifySocialNetworks = () => {
    if (facebook || twitter || instagram || linkedin) {
      settingsCxt.modifySocialNetwork(getModifySocialNetwork, {
        twitter,
        facebook,
        instagram,
        linkedin,
      });
    } else {
      console.log(
        "sorry you need to provide at least one social network handle"
      );
    }
  };

  const getModifySocialNetwork = () => {
    const xhr = new XMLHttpRequest();
    xhr.open("GET", "/api/seller/settings/social-network", true);
    xhr.onload = () => {
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (xhr.responseText) {
          let socials = JSON.parse(xhr.responseText);
          console.log(socials.success.msg.socialNetwork, " all the socials ");
          if (socials.success) {
            // if (socials.success.msg) {
            //   setFacebook(socials.success.msg.socialNetwork.facebook);
            //   setInstagram(socials.success.msg.socialNetwork.instagram);
            //   setTwitter(socials.success.msg.socialNetwork.twitter);
            //   setLinkedin(socials.success.msg.socialNetwork.linkedin);
            // }
          }
          console.log(xhr.responseText, " all the response test ");
        }
      }
    };

    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };
  return (
    <div className="col-md-9">
      <div className="settings-main-content">
        <div className="settings-header">
          <h4>
            <i className="fa fa-clone"></i>
            Social network
          </h4>
        </div>

        <div className="row social-network justify-content-center">
          <div className="col-md-8">
            <form action="">
              <div className="form-field">
                <label htmlFor="facebook">Facebook</label>
                <div className="network-field">
                  <i className="fab fa-facebook-square icon facebook"></i>
                  <input
                    type="text"
                    name="facebook"
                    defaultValue={facebook}
                    id="facebook"
                    required
                    onChange={(el) => setFacebook(el.target.value)}
                  />
                </div>
              </div>
              <div className="form-field">
                <label htmlFor="twitter">Twitter</label>
                <div className="network-field">
                  <i className="fab fa-twitter-square icon twitter"></i>
                  <input
                    type="text"
                    name="twitter"
                    defaultValue={twitter}
                    id="twitter"
                    required
                    onChange={(el) => setTwitter(el.target.value)}
                  />
                </div>
              </div>
              <div className="form-field">
                <label htmlFor="instagram">Instagram</label>
                <div className="network-field">
                  <i className="fab fa-instagram icon instagram"></i>
                  <input
                    type="text"
                    name="instagram"
                    defaultValue={instagram}
                    id="instagram"
                    required
                    onChange={(el) => setInstagram(el.target.value)}
                  />
                </div>
              </div>
              <div className="form-field">
                <label htmlFor="linkedin">Linkedin</label>
                <div className="network-field">
                  <i className="fab fa-linkedin icon linkedin"></i>
                  <input
                    type="text"
                    defaultValue={linkedin}
                    name="linkedin"
                    id="linkedin"
                    required
                    onChange={(el) => setLinkedin(el.target.value)}
                  />
                </div>
              </div>
              <button
                type="button"
                className="apply-changes"
                onClick={modifySocialNetworks}
              >
                Apply Changes
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SocialNetworks;
