import React from "react";
import {
  Route,
  BrowserRouter as Router,
  Switch,
  Link,
  Redirect,
} from "react-router-dom";
import Sidebar from "../../common/Sidebar";
import PrivateRoute from "../../routing/PrivateRoute";
import BuyNowSales from "./BuyNowSales";
import BuyNowState from "./BuyNowState";
import OrderDetail from "./OrderDetail";

const BuyNow = (props) => {
  return (
    <BuyNowState>
      <Sidebar {...props} />
      <Switch>
        <PrivateRoute
          path="/seller/buy-now-sales"
          exact
          component={BuyNowSales}
        />
        <PrivateRoute
          component={OrderDetail}
          exact
          path="/seller/buy-now-sales/orders/:orderid"
        />
      </Switch>
    </BuyNowState>
  );
};

export default BuyNow;
