import React, { useState } from "react";
import { useEffect } from "react";
import { useContext } from "react";
import { AdsContext } from "./AdsState";
import ProceedToPayment from "./ProceedToPayment";
import * as urlStringObject from "../../utils/urlStringObject";
import { AuthContext } from "../../context/authContext/AuthState";

const CreateAdvertisement = (props) => {
  const [campaignTypes, setCampaignTypes] = useState([]);
  const authCxt = useContext(AuthContext)
  const adsCxt = useContext(AdsContext);
  useEffect(() => {
    getAdstTypes(localStorage.token);
  }, []);

  const setAdvert = () => {
    const dataForm = new FormData();
    dataForm.append("adbudget", adsCxt.state.adTotal);
    dataForm.append("adtype", adsCxt.state.selectedType._id);
    dataForm.append("adimage", adsCxt.state.campaignImage);
    dataForm.append("adduration", adsCxt.state.duration);
    let xhr = new XMLHttpRequest();
    xhr.open("POST", "/api/seller/my-adverts", true);
    xhr.onload = () => {
      console.log(xhr.responseText, " setting ads responsetext ");
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  const getAdstTypes = (token) => {
    let xhr = new XMLHttpRequest();
    xhr.open("GET", "/api/admin/advertisement-types", true);
    xhr.onload = () => {
      if (xhr.responseText) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText);
          if (res) {
            if (res.success) {
              let adsTypes = res.success.adsTypes;
              if (Array.isArray(adsTypes)) {
                if (adsTypes) {
                  if (adsTypes.length > 0) {
                    adsCxt.setValue({ adsTypes: res.success });
                    adsCxt.setValue({ selectedType: adsTypes[0] });
                    adsCxt.setValue({ adDuration: adsTypes[0].days });
                  }
                }
              }
              console.log(
                JSON.parse(xhr.responseText),
                " ads state has been setted  "
              );
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };
  return !adsCxt.state.processPayment ? (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header adverts-top">
          <h4 className="">
            <a href="my-advertisements.html">
              <i className="fa fa-arrow-alt-circle-left"></i>
              Create Advert
            </a>
          </h4>
        </div>

        <div className="row justify-content-center">
          <div className="col-md-8 ">
            <div className="create-advert-form">
              <form action="">
                <p className="red-text">
                  Select from different types of assets to start a campaign.
                  Different adverts are positioned at strategic in Tinkoko
                  Market
                </p>
                <div className="form-field">
                  <i className="fa fa-chevron-down arrow-down"></i>
                  <label for="campaign-type">Campaign Type</label>
                  <select
                    name="campaign-type"
                    id="campaign-type"
                    onChange={(e) => {
                      adsCxt.state.adsTypes.adsTypes &&
                        adsCxt.setValue({
                          selectedType:
                            adsCxt.state.adsTypes.adsTypes[e.target.value],
                        });
                    }}
                  >
                    {adsCxt.state.adsTypes.adsTypes &&
                      adsCxt.state.adsTypes.adsTypes.map((t, key) => (
                        <>
                          <option
                            defaultChecked={key === 0 ? true : false}
                            onLoad={(e) => {
                              key === 0 && adsCxt.setValue({ selectedType: t });
                            }}
                            data-key={key}
                            value={key}
                            key={key}
                          >
                            {t.name}
                          </option>
                        </>
                      ))}
                  </select>
                  {adsCxt.state.selectedType && (
                    <>
                      <img
                        className="img-fluid skyscraper-advert"
                        width={`${adsCxt.state.selectedType.width}px`}
                        height={`${adsCxt.state.selectedType.height}px`}
                        style={{
                          width: adsCxt.state.selectedType.width,
                          height: adsCxt.state.selectedType.height,
                        }}
                        src={adsCxt.state.selectedType.sample_image}
                        alt=""
                        id="advert-image"
                      />
                      <p className="dimension">
                        Dimensions : {adsCxt.state.selectedType.width}px by{" "}
                        {adsCxt.state.selectedType.height}px
                      </p>
                    </>
                  )}
                </div>
                <div className="form-field">
                  <h6>Upload banner (Max size of 5mb)</h6>
                  <p>
                    Provide a clear high quality image optimised to stand at 5mb
                    max
                  </p>
                  <label for="upload-banner" className="custom-file-upload">
                    <i className="fa fa-plus"></i>
                    Upload Image (jpg, png, gif)
                  </label>
                  <input
                    type="file"
                    name="upload-banner"
                    id="upload-banner"
                    accept="image/*"
                    onInput={(e) => {
                      adsCxt.setValue({ campaignImage: e.target.files[0] });
                      if (window.File && window.FileList && window.FileReader) {
                        var file = e.target.files[0]; //FileList object
                        var picReader = new FileReader();
                        picReader.addEventListener("load", function (event) {
                          var picFile = event.target;
                            adsCxt.setValue({ adbanner:  picFile.result})
                        });
                        picReader.readAsDataURL(file);

                      }
                    }}
                  />
                  {adsCxt.state.campaignImage &&
                    adsCxt.state.campaignImage.name}
                </div>
                <div className="payment-prices">
                  {adsCxt.state.adsTypes.durations &&
                    adsCxt.state.adsTypes.durations.map((d, key) => (
                      <div className="single-payment-price" key={key}>
                        <span>
                          N
                          {adsCxt.state.selectedType &&
                            adsCxt.state.selectedType.price * parseInt(d.days)}
                        </span>
                        <div className="price">
                          <span>{`${d.days}`} days</span>
                          <input
                            type="radio"
                            name="days"
                            id={`${d.days}days`}
                            onClick={() => {
                              adsCxt.setValue({
                                adTotal:
                                  adsCxt.state.selectedType.price *
                                  parseInt(d.days),
                              });
                              adsCxt.setValue({
                                duration: d.days,
                              });
                            }}
                          />
                          <label for={`${d.days}days`}></label>
                        </div>
                      </div>
                    ))}
                </div>
                <div className="proceed">
                  <a
                    href="/"
                    className="proceed-to-payment"
                    onClick={async (e) => {
                      e.preventDefault();
                      if (adsCxt.state.adTotal && adsCxt.state.adImage) {
                        
                        setAdvert()

                      }
                    }}
                  >
                    Proceed to Payment
                  </a>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <ProceedToPayment />
  );
};

export default CreateAdvertisement;
