import React, { useState, useEffect } from 'react'
import { useContext } from 'react'
import {AuthContext} from '../../context/authContext/AuthState'
import { SettingsContext } from './SettingsState'

const SecuritySettings = () => {
    const authCxt = useContext(AuthContext)
    const settingsCxt = useContext(SettingsContext)
    const [twofa, setTwofa] =useState({})

    useEffect(()=>{
        if (authCxt.user) {
            let tfa = authCxt.user.TwoFactorAuth
            if (tfa) {
                // let count = Object.keys(tfa).length
                if (tfa.email) {
                    setTwofa({twofaemail:tfa.email})
                }else if (tfa.email) {
                    setTwofa({twofaphone:tfa.phone})
                }
                // console.log(count, ' count count')
                // while (authCxt.user[/.+/] && count ) {
                //     count--
                //     console.log(authCxt.user[/.+/], ' this is it ')
                // }
            }
        }
    }, [])
     
    const modifySecurity = () => {
        console.log(twofa, ' this is the two fa ')
        if (twofa) {
            settingsCxt.modifySecurity(twofa)
        }
    }

    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-shield-alt"></i>
                    Security Settings
                </h4>
            </div>

            <div className="row security-settings">
                <div className="col-md-7">
                    <div className="security-settings-container">
                        <h6>2 Factor Authentication</h6>
                        <p>2 factor authentication is a multi-layer security measure to enable the security
                            of your account. It is highly recommended but optional.</p>
                        <p>Used to authenticate password change, unrecognised logins from devices.</p>
                        <button className="enable-button" data-toggle="modal"
                            data-target="#enable-two-factor">Enable</button>
                        <div className="modal enable-two-factor fade text-center" id="enable-two-factor"
                            tabindex="-1" role="dialog" aria-labelledby="enable-two-factor"
                            aria-hidden="true">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                            <div className="modal-dialog modal-dialog-centered" role="document">
                                <div className="modal-content">
                                    <div className="modal-body">
                                        <h5>Enable 2FA</h5>
                                        <form action="">
                                            <div className="form-field">
                                                <label htmlFor="">Use Email Address</label>
                                                <div className="radio-input-field">
                                                    <input type="radio" name="choice" value="email" id="email" checked={twofa.email && true} onChange={()=>setTwofa({twofaemail:authCxt.user && authCxt.user.email})}/>
                                                    <label htmlFor="email"></label>
                                                    <input type="email" name="email-address" id="email-address" className="input-field" defaultValue={authCxt.user&& authCxt.user.email} placeholder={authCxt.user&& authCxt.user.email} disabled/>
                                                </div>
                                            </div>
                                            <div className="form-field">
                                                <label htmlFor="">Use Phone</label>
                                                <div className="radio-input-field">
                                                    <input type="radio" name="choice" value="phone" id="phone" checked={twofa.phone && true} onChange={()=>setTwofa({twofaphone:authCxt.user && authCxt.user.phone})}/> 
                                                    <label htmlFor="phone"></label>
                                                    <input type="number" defaultValue={authCxt.user&& authCxt.user.phone} name="phone-number" id="phone-number" className="input-field" placeholder={authCxt.user&& authCxt.user.phone} disabled/>
                                                </div>
                                            </div>
                                            <button type="button" onClick={modifySecurity} className="enable-button">ACTIVATE</button>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
    )
}

export default SecuritySettings
