import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import { AuthContext } from "../../../context/authContext/AuthState";
import { RegularPostContext } from "./CreateRegularProduct";
import { errorBorder } from "../../../utils/errorBorder";
import {AlertContext} from "../../../context/alertContext/AlertState"

const RegularProductDetail = () => {
  const authCxt = useContext(AuthContext);
  const { setPstate, pState } = useContext(RegularPostContext);
  const { setAlert} = useContext(AlertContext);

  const [tlengthCounter, setTlengthCounter] = useState(0);
  const [dlengthCounter, setDlengthCounter] = useState(0);
  const [amountDistributions, setAmountDistributions] = useState([]);

  useEffect(() => {
    authCxt.loadUser();
    getAmountDistribution();
    authCxt.user && setPstate({ businessName: authCxt.user.businessName });
    authCxt.user && setPstate({ phone: authCxt.user.phone });
    // eslint-disable-next-line
  }, []);

  const validateForm = () => {
    if (pState.title) {
      if (pState.description) {
        if (pState.price) {
          if (pState.amountDistribution) {
            if (pState.businessName) {
              if (pState.phone) {
                submitPost();
              } else {
                errorBorder("#phone", "Enter phone number");
                console.log("please enter your registered Phone number");
              }
            } else {
              console.log(
                "You need to provide and already registered business name "
              );
            }
          } else {
            console.log("Enter amount in distribution");
            errorBorder("#amount-distribution", "Enter amount in distribution");
          }
        } else {
          console.log("Enter product price ");
          errorBorder("#fixed-price-value", "Enter product price");
        }
      } else {
        errorBorder("#description", "Enter product  description");
        console.log("enter product description here ");
      }
    } else {
      errorBorder("#title", "enter enter product title ");
      // errline.appendChild(para)
    }
  };
  // SUBMIT POST TO BACKEND SERVER
  const submitPost = () => {
    // region,
    // country,
    // category,
    // subCategory,
    // productImages,
    // amountDistribution,
    // price,
    // negotiable,
    // businessName,
    // phone,
    // title,
    // description

    // pcat
    // pregion
    // pcountry
    // ptitle
    // pdesc
    // pprice
    // pamntdist
    // psphone
    // psname
    // pscompany

    const dataForm = new FormData();
    dataForm.append("pcat", pState.category);
    dataForm.append("pregion", pState.region);
    dataForm.append("pcountry", pState.country);
    dataForm.append("ptitle", pState.title);
    dataForm.append("pdesc", pState.description);
    dataForm.append("pprice", pState.price);
    dataForm.append("pamntdist", pState.amountDistribution);
    dataForm.append("psphone", pState.phone);
    dataForm.append("psname", pState.businessName);
    dataForm.append("pscompany", pState.businessName);
    for (let i = 0; i < pState.productImages.length; i++) {
      dataForm.append("pimg", pState.productImages[i].file);
    }
    if (pState.productVideo) {
      dataForm.append("pvid", pState.productVideo);
    }

    let xhr = new XMLHttpRequest();
    xhr.open("POST", "/api/seller/my-product/regular", true);
    xhr.onload = () => {
      console.log(xhr.response);
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (xhr.responseText) {
          let res = JSON.parse(xhr.responseText, " regular prduct error");
          if (res.success) {
            setPstate({ isSubmitted: true });
            // isSubmitted(true)
          }
        }
      }
      if (xhr.responseText) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText)
          if (res.error) {
            if (res.error.msg) {
              setAlert("danger", res.error.msg)
            }
          }else if(res.success){
            setAlert("success", "Done!")

          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  // GET ALL AVAILABLE DISTRIBUTION  FROM THE SERVER
  const getAmountDistribution = async () => {
    try {
      let res = await axios.get("/api/seller/amount-distribution");
      if (res.data) {
        if (res.data.success) {
          setAmountDistributions(res.data.success);
        }
      }
    } catch (error) {
      setAmountDistributions([]);
    }
  };

  //  SET FIX PRICE DURING PRODUCT CREATION
  function fixedPriceValue(val) {
    let pricevalue = document.getElementById("fixed-price-value");
    setPstate({ price: pricevalue.value });
    // setPrice(pricevalue.value);
  }

  return (
    <>
      <fieldset className="active current">
        {/* <!-- TITLE FIELD --> */}
        <div className="title-field">
          <div className="d-flex justify-content-end">
            <span className="short">Keep it short</span>
            <span className="count">
              <span id="title-count">{tlengthCounter}</span> / 60
            </span>
          </div>
          <input
            type="text"
            name="title"
            id="title"
            onChange={(e) => {
              setTlengthCounter(e.target.value.length);
              setPstate({ title: e.target.value });
              // setTitle(e.target.value);
            }}
            placeholder="Title*"
            maxLength="60"
          />
        </div>
        {/* <!-- POST DESCRIPTION --> */}
        <div className="description-field">
          <label
            htmlFor="description"
            className="d-flex justify-content-between"
          >
            Description and features*
            <span className="count">
              <span id="description-count">{dlengthCounter}</span> / 2000
            </span>
          </label>
          <textarea
            name="description"
            id="description"
            cols="30"
            rows="8"
            maxLength="2000"
            onChange={(e) => {
              setDlengthCounter(e.target.value.length);
              // setDescription(e.target.value);
              setPstate({ description: e.target.value });
            }}
          ></textarea>
        </div>
        {/* <!-- price and amount distribution --> */}
        <div className="form-row price-box align-items-center">
          <div className="col-md-6">
            <h6 className="price-tag">Price*</h6>
            <div className="price-field mb-4">
              <input
                type="radio"
                name="price"
                id="contact-price"
                onClick={() => setPstate({ price: "Contact for price " })}
              />
              <label htmlFor="contact-price" className="label">
                Contact for price
              </label>
            </div>
            <div className="price-field">
              <input
                type="radio"
                name="price"
                id="fixed-price"
                checked
                onChange={(el) => fixedPriceValue(el.target.value)}
              />
              <label htmlFor="fixed-price" className="label mt-3"></label>
              <div className="create-floating-label w-100">
                <input
                  type="number"
                  name=""
                  id="fixed-price-value"
                  onChange={(el) => setPstate({ price: el.target.value })}
                />
                <label htmlFor="">Price*</label>
                <span className="naira">&#8358;</span>
              </div>
            </div>
            <div className="checkbox-field">
              <input
                type="checkbox"
                name="negotiable"
                id="negotiable"
                className="checkbox"
                onClick={() => {
                  // let neg = negotiable;
                  // setNegotiable(!neg);
                  setPstate({ negotiable: !pState.negotiable });
                  pState.negotiable === false
                    ? setPstate({
                        price: document.querySelector("#fixed-price-value")
                          .value,
                      })
                    : setPstate({ price: pState.price });
                }}
              />
              <label htmlFor="negotiable" className="label">
                Negotiable
              </label>
            </div>
          </div>
          <div className="col-md-6 amount-distribution-field">
            <div className="form-field amount-distribution">
              <label htmlFor="">Amount Distribution*</label>
              <select
                onChange={(el) =>
                  setPstate({ amountDistribution: el.target.value })
                }
                name=""
                id="amount-distribution"
              >
                <option value="">Select</option>
                {amountDistributions &&
                  amountDistributions.map((amt, key) => (
                    <option key={key} value={amt.name}>
                      {amt.name}
                    </option>
                  ))}
              </select>
              <i className="fa fa-chevron-down arrow-down"></i>
            </div>
          </div>
        </div>
        {/* <!-- Telephone and name --> */}
        <div className="form-row">
          <div className="col-md-6">
            <div className="create-floating-label">
              <input
                type="number"
                name=""
                id="phone"
                value={authCxt.user && authCxt.user.phone}
                readOnly
              />
              <label htmlFor="">Your Phone</label>
            </div>
          </div>
          <div className="col-md-6">
            <div className="create-floating-label">
              <input
                type="text"
                name=""
                id=""
                value={authCxt.user && authCxt.user.businessName}
                readOnly
              />
              <label htmlFor="">Name*</label>
            </div>
          </div>
        </div>
        <div className="tip">Promote this ad to reach more people</div>
      </fieldset>

      <input
        type="button"
        className="post-btn"
        style={{ display: "block" }}
        onClick={validateForm}
        value="Create Post"
      />
      <div className="info" style={{ display: "block" }}>
        By clicking on submit post, you accept the Terms of use, confirm that
        you will abide by the safety tips, and declare that posting does not
        include any prohibited items.
      </div>
    </>
  );
};

export default RegularProductDetail;
