import React from 'react'

const ChangePassword = () => {
    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-key"></i>
                    Manage Password
                </h4>    
            </div>

            <div className="row change-password justify-content-center">
                <div className="col-md-8">
                    <form action="">
                        <div className="form-field">
                            <label htmlfor="current-password">Current password*</label>
                            <input type="text" name="current-password" id="current-password" required/>
                        </div>
                        <div className="form-field">
                            <label htmlfor="new-password">New password*</label>
                            <input type="text" name="new-password" id="new-password" required/>
                        </div>
                        <div className="form-field">
                            <label htmlfor="confirm-new-password">Confirm new password*</label>
                            <input type="number" name="confirm-new-password" id="confirm-new-password" required/>
                        </div>
                        <div className="form-field">
                            <label htmlfor="security-pin">Security PIN*</label>
                            <input type="number" name="security-pin" id="security-pin" required/>
                        </div>
                        <button type="submit" className="submit-button">Change Password</button>
                    </form>
                </div>
            </div>
            
        </div>
    </div>
    )
}

export default ChangePassword
