import React, { createContext, useReducer } from 'react'
import PromoReducer from  "./PromoReducer"

export const PromoContext = createContext()
const {Provider} = PromoContext

const PromoState = (props) => {
    const initialState = {
        myPromotions:[],
        promoTypes: [],
        posts:[],
        durations:[], 
        selectedPost:null, 
        selectedType:null,
        totalBudget:0,
        duration:0
    }



      // set promo 
  const setPromotion = (token) => {
    if (!token) return;
    let dataForm = new FormData()
    dataForm.append("productid", state.selectedPost)
    dataForm.append("promobudget",state.totalBudget)
    dataForm.append("promotype", state.selectedType)
    dataForm.append("promoduration", state.selectedDuration)    
    // productid
    // promobudget
    // promotype
    // promoduration
    // promostatus

    let xhr = new XMLHttpRequest();
    xhr.open("POST", `/api/seller/my-promotions`, true);
    xhr.onload = () => {
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            console.log(xhr.responseText, " hello the response text ")
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send(dataForm);
  };

    const [state, dispatch] = useReducer(PromoReducer, initialState)
    
    const setValue = (obj={}) =>{
        return dispatch({type:"SET_STATE", payload:obj})
    }
    return (
        <Provider value={{setValue, state, setPromotion}}>
            {props.children}
        </Provider>
    )
}

export default PromoState
