import React from "react";
import Header from "../../common/Header";
import Footer from "../../common/Footer";
import Sidebar from "../../common/Sidebar";

const MyStatistics = () => {
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="statistics-header">
          <h4 className="bold-green-text">My Statistics</h4>
        </div>
        <div className="form-container d-flex">
          <div className="switch">
            <label>
              <input type="checkbox" checked="checked" id="visitors" />
              <span className="lever"></span>
            </label>
            <div className="text-center grey-text">Visitors</div>
          </div>
          <div className="switch phone-switch">
            <label>
              <input type="checkbox" checked="checked" id="phone-views" />
              <span className="lever"></span>
            </label>
            <div className="text-center grey-text">Phone views</div>
          </div>
          <div className="switch chat-switch">
            <label>
              <input type="checkbox" checked="checked" id="chats" />
              <span className="lever"></span>
            </label>
            <div className="text-center grey-text">Chats</div>
          </div>
          <div className="create-floating-label">
            <input type="text" name="selectdate" id="selectdate" />
            <label for="selectdate">Select Date</label>
          </div>
          <div className="select-field">
            <select name="" id="">
              <option value="">Day</option>
              <option value="">Week</option>
              <option value="">Month</option>
            </select>
            <i className="fa fa-caret-down arrow-down"></i>
          </div>
        </div>
        <canvas id="myChart"></canvas>
        <div className="statistics-box">
          <div className="box visitors">
            <h6>110</h6>
            <p>Visitors</p>
          </div>
          <div className="box phone-views">
            <h6>18</h6>
            <p>Phone view</p>
          </div>
          <div className="box chats">
            <h6>5</h6>
            <p>Chats</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MyStatistics;
