import React from 'react'

const BankAccountDetails = () => {
    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-calculator"></i>
                    Account Details
                </h4>    
            </div>

            <div className="row account-details justify-content-center">
                <div className="col-md-8">
                    <form action="">
                        <div className="form-field">
                            <label htmlFor="account-name">Account Name</label>
                            <input type="text" name="account-name" id="account-name" placeholder="SID235465657676" />
                        </div>
                        <div className="form-field">
                            <label htmlFor="bank-name">Bank Name</label>
                            <input type="text" name="bank-name" id="bank-name" placeholder="Guaranty Trust Bank PLC" />
                        </div>
                        <div className="form-field">
                            <label htmlFor="account-number">Account Number</label>
                            <input type="number" name="account-number" id="account-number" placeholder="012434345456"/>
                        </div>
                    </form>
                </div>
            </div>
            
        </div>
    </div>
    )
}

export default BankAccountDetails
