import React, { useContext, useState, useEffect } from "react";

import userProfile from "../../images/user-profile.PNG";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/authContext/AuthState";

const SettingsSidebar = () => {
  const authCxt = useContext(AuthContext);

  return (
    
    <div className="col-md-3">
      <section className="settings-section">
      <button id="settings-navigation" className="collapsible d-md-none">
        <span className="mr-3">
          <i className="fa fa-bars"></i>
        </span>
        Open Settings Menu
      </button>
      
      <div className="settings-sidebar d-md-block d-lg-block">
        <Link
          to="/seller/my-products"
          className="back-button"
          >
          <i className="fa fa-arrow-alt-circle-left"></i> Settings
        </Link>
        <div className="settings-sidebar-one">
          <ul>
            <li>
              <Link to="/seller/settings/my-profile">My Profile</Link>
            </li>
            <li>
              <Link to="/seller/settings/bank-account-details">
                Bank Account Details
              </Link>
            </li>
            <li>
              <Link to="/seller/settings/social-networks">Social networks</Link>
            </li>
            <li>
              <Link to="/seller/settings/change-password">Change password</Link>
            </li>
          </ul>
        </div>
        <div className="settings-sidebar-two">
          <ul>
            <li>
              <Link to="/seller/settings/mobile-phone">
                Mobile Phone Settings
              </Link>
            </li>
            <li>
              <Link to="/seller/settings/t-messenger">
                T-messenger Settings
              </Link>
            </li>
            <li>
              <Link to="/seller/settings/notifications">
                Notification Settings
              </Link>
            </li>
            <li>
              <Link to="/seller/settings/security">Security Settings</Link>
            </li>
            <li>
              <Link to="/seller/settings/manage-users">Manage Users</Link>
            </li>
          </ul>
        </div>
        <div className="logout">
          <ul>
            <li>
              <Link to="">Logout</Link>
            </li>
          </ul>
        </div>
      </div>
          </section>
    </div>
  );
};

export default SettingsSidebar
