import React from 'react'

const TMessengerSettings = () => {
    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-comments"></i>
                    T-Messenger Settings
                </h4>
            </div>

            <div className="row tmessenger-settings">
                <div className="col-md-7">
                    <form action="">
                        <div className="switch">
                            <label>
                                <input type="checkbox" name="receive-messages" id="receive-messages"/>
                                <span className="lever"></span>
                                Receive Messages
                            </label>
                        </div>
                    </form>
                    <p>T-messenger chat helps your customers to get in touch with you through message. Enable this option to get messages from customers.</p>
                    <h6>If you don't reply to your customer for a while, chats will be disabled automatically (existing chats will stay active).</h6>
                </div>
            </div>

        </div>
    </div>
    )
}

export default TMessengerSettings
