import React, { useEffect, useContext } from "react";
import { BuyNowContext } from "./BuyNowState";
import { AuthContext } from "../../context/authContext/AuthState";
import OrderComments from "./OrderComments";

const OrderDetail = (props) => {
  const authCxt = useContext(AuthContext);
  const bnCxt = useContext(BuyNowContext);
  useEffect(() => {
    if (authCxt.token) {
      bnCxt.getSingleOrder(authCxt.token, props.match.params.orderid);
    }
  }, []);
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header">
          <h4 className="buy-now-sales-header font-weight-bold">
            Buy Now Sales
            <span>
              <i className="fa fa-play play-icon"></i>
            </span>
            <span className="details">Order details</span>
          </h4>
          <h6 className="order-number"> {bnCxt.state.singleOrder && `Order details for Order N0 BID135454 ${bnCxt.state.singleOrder._id}`} </h6>
        </div>
        {/* <!-- TRANSACTION DETAILS --> */}

        {bnCxt.state.singleOrder && (
          <div className="row mb-4">
            <div className="col-md-4 customer-information">
              <h6>Customer information</h6>
              <div className="single-detail">
                <p>Date:</p>
                <p>11 Jan 2020 14:56</p>
              </div>
              <div className="single-detail">
                <p>Customer:</p>
                {/* <p>{bnCxt.state.singleOrder.buyer_first_name} {bnCxt.state.singleOrder.buyer_last_name}</p> */}
              </div>
              <div className="single-detail">
                <p>Phone Number:</p>
                <p>{bnCxt.state.singleOrder.buyer_phone}</p>
              </div>
            </div>
            <div className="col-md-4 transaction-details">
              <h6>Transaction details</h6>
              <div className="single-detail">
                <p>Item Price:</p>
                <p>{bnCxt.state.singleOrder.total_price}</p>
              </div>
              <div className="single-detail">
                <p>Tinkoko Commission:</p>
                <p>-45,000.00</p>
              </div>
              <div className="single-detail">
                <p>Shipping Cost Contribution:</p>
                <p>0.00</p>
              </div>
              <div className="single-detail">
                <p>Shipping Cost</p>
                <p>8,000</p>
              </div>
              <div className="single-detail">
                {/* <p>Product ID:</p> */}
                <p>Number Of Products:</p>
                <p>
                  {bnCxt.state.singleOrder.items &&
                    bnCxt.state.singleOrder.items.length}
                </p>
              </div>
              <div className="single-detail">
                <p>Payment Method:</p>
                <p>{}</p>
              </div>
              <div className="single-detail">
                <p>Tax amount:</p>
                <p>824.89</p>
              </div>
              <div className="single-detail">
                <p>Voucher Total:</p>
                <p>0</p>
              </div>
              <div className="single-detail">
                <p>Voucher Code:</p>
                <p>N/A</p>
              </div>
              <div className="single-detail">
                <p>Grand Total:</p>
                <p>458,824.89</p>
              </div>
            </div>
            <div className="col-md-4 billing-and-shipping-address">
              <div className="billing-address">
                <h6>Billing Address</h6>
                <p className="name">
                  {bnCxt.state.singleOrder.first_name}{" "}
                  {bnCxt.state.singleOrder.buyer_last_name}
                </p>
                <p className="address">{bnCxt.state.singleOrder.buyer_address}</p>
              </div>
              <div className="shipping-address">
                <h6>Shipping/Fulfillment Address</h6>
                <p className="name">
                  {bnCxt.state.singleOrder.first_name}{" "}
                  {bnCxt.state.singleOrder.buyer_last_name}
                </p>
                <p className="address">{bnCxt.state.singleOrder.buyer_address}</p>
              </div>
            </div>
          </div>
        )}

        {/* <!-- ITEMS CONTAINER --> */}
        <div className="items-container mb-4">
          <div className="items-header">
            <h6>Items</h6>
            <a href="#" onClick={(e)=>{
              e.preventDefault()
              window.print()
            }}>
              <i className="fa fa-print"></i> 
              Print Shipping Labels
            </a>
          </div>
          <iframe src="" frameborder="0"></iframe>
          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Product ID</th>
                    <th scope="col">Tinkoko PID</th>
                    <th scope="col">Products</th>
                    <th scope="col">Price</th>
                    <th scope="col">Status</th>
                    <th scope="col">Shipment Method</th>
                  </tr>
                </thead>
                <tbody>
                  {bnCxt.state.singleOrder.items &&
                    Array.isArray(bnCxt.state.singleOrder.items) &&
                    bnCxt.state.singleOrder.items.map((p, key) => (
                      <tr key={key}>
                        <td>{p._id}</td>
                        <td>{key + 1}</td>
                        <td>
                          {p.post_distribution} {p.post_title}
                        </td>
                        <td>{p.post_price}</td>
                        <td className="awaiting-confirmation">
                          {bnCxt.state.singleOrder.order_status}
                        </td>
                        <td className="drop-shipping">
                          {bnCxt.state.singleOrder.shipping_method}
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* <!-- ITEMS HISTORY CONTAINER --> */}
        <div className="items-history-container mb-4">
          <div className="items-history-header">
            <h6>Items History</h6>
          </div>

          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Product ID</th>
                    <th scope="col">Tinkoko PID</th>
                    <th scope="col">Status</th>
                    <th scope="col">Notes</th>
                    <th scope="col">Created at</th>
                    <th scope="col">Editor</th>
                  </tr>
                </thead>
                <tbody>
                {Array.isArray(bnCxt.state.singleOrder.history) ? bnCxt.state.singleOrder.history.map((h, key)=>(
                  <tr>
                    <td>{h.id}</td>
                <td>{h._id}</td>
                <td className="order-confirmed">{h.status}</td>
                <td>{h.note}</td>
                    <td>
                      {h.date} <span className="d-block">14:27pm</span>
                    </td>
                <td>{h.sender}</td>
                  </tr>
                )):<tr aria-rowspan="5" style={{border:"1px solid #ccc"}}> <td rowSpan="6">Sorry no history on this order yet</td></tr>}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* <!-- COMMENTS-CONTAINER --> */}
                <OrderComments />
      </div>
    </div>
  );
};

export default OrderDetail;
