import React from 'react'

const SetTest = () => {
    return (
        
        <section class="settings-section">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-3">
              <button id="settings-navigation" class="collapsible d-md-none">
                <span class="mr-3"><i class="fa fa-bars"></i></span>Open Settings
                Menu
              </button>
              <div class="settings-sidebar d-md-block d-lg-block">
                <a
                  href="../my-products/seller-products-all.html"
                  class="back-button"
                  ><i class="fa fa-arrow-alt-circle-left"></i> Settings</a
                >
                <div class="settings-sidebar-one">
                  <ul>
                    <li>
                      <a href="seller-settings-dashboard.html"> My Profile </a>
                    </li>
                    <li>
                      <a href="seller-settings-dashboard-account.html">
                        Bank Account Details
                      </a>
                    </li>
                    <li>
                      <a href="seller-settings-dashboard-social-networks.html">
                        Social networks
                      </a>
                    </li>
                    <li>
                      <a href="seller-settings-dashboard-change-password.html">
                        Change password
                      </a>
                    </li>
                  </ul>
                </div>
                <div class="settings-sidebar-two">
                  <ul>
                    <li>
                      <a
                        href="seller-settings-dashboard-mobile-phone-settings.html"
                      >
                        Mobile Phone Settings
                      </a>
                    </li>
                    <li>
                      <a
                        href="seller-settings-dashboard-tmessenger-settings.html"
                      >
                        T-messenger Settings
                      </a>
                    </li>
                    <li>
                      <a
                        href="seller-settings-dashboard-notification-settings.html"
                      >
                        Notification Settings
                      </a>
                    </li>
                    <li>
                      <a href="seller-settings-dashboard-security-settings.html">
                        Security Settings
                      </a>
                    </li>
                    <li>
                      <a href="seller-settings-dashboard-manage-users.html">
                        Manage Users
                      </a>
                    </li>
                  </ul>
                </div>
                <div class="logout">
                  <ul>
                    <li>
                      <a href="#">Logout</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
  
            <div class="col-md-9">
              <div class="settings-main-content">
                <div class="settings-header">
                  <h4>
                    <i class="fa fa-calculator"></i>
                    Account Details
                  </h4>
                </div>
  
                <div class="row account-details justify-content-center">
                  <div class="col-md-8">
                    <form action="">
                      <div class="form-field">
                        <label for="account-name">Account Name</label>
                        <input
                          type="text"
                          name="account-name"
                          id="account-name"
                          placeholder="SID235465657676"
                        />
                      </div>
                      <div class="form-field">
                        <label for="bank-name">Bank Name</label>
                        <input
                          type="text"
                          name="bank-name"
                          id="bank-name"
                          placeholder="Guaranty Trust Bank PLC"
                        />
                      </div>
                      <div class="form-field">
                        <label for="account-number">Account Number</label>
                        <input
                          type="number"
                          name="account-number"
                          id="account-number"
                          placeholder="012434345456"
                        />
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
}

export default SetTest
