import React, { useContext, useEffect } from "react";
import {
  Switch,
  Redirect,
} from "react-router-dom";

import AuthState, { AuthContext } from "../context/authContext/AuthState";
import PrivateRoute from "../routing/PrivateRoute";
import NotFound from "../common/NotFound";
import Settings from "../page/settings/Settings";
import BuyNowSales from "../page/buy-now-sales/BuyNowSales";
import Account from "./accounts/Account";
import MyStatistics from "../page/my-statistics/MyStatistics";
import Tmessenger from "./t-messenger/Tmessenger";
import MyProducts from "../page/myproducts/MyProducts";

import MessengerState from "../page/t-messenger/MessengerState";
import Notification from "../page/notifications/Notification";
import NotificationState from "../page/notifications/NotificationState";
import SupportFeedback from "../page/support-feedback/SupportFeedback";
import Supports from "../page/support-feedback/Supports";
import CreatePromotion from "../page/my-promotions/CreatePromotion";
import Promotions from "../page/my-promotions/Promotions";
import Advertisements from "../page/my-advertisment/Advertisements";
import BuyNowState from "../page/buy-now-sales/BuyNowState";
import CancelledOrders from "./buy-now-sales/CancelledOrders";
import OrderDetail from "./buy-now-sales/OrderDetail";
import SetTest from "./SetTest";
import BuyNow from "./buy-now-sales/BuyNow";

const Content = (props) => {
  const authCxt =  useContext(AuthContext)
  useEffect(() => {
    authCxt.loadUser()
  }, [authCxt.isAuthenticated,authCxt.isLoading])
  return  (
      <Switch>
        <PrivateRoute component={Account} exact path="/seller/account" />
        <PrivateRoute component={Settings} exact path="/seller/settings/*" />
        <PrivateRoute
          component={() => <Redirect to="/seller/settings/my-profile" />}
          exact
          path="/seller/settings"
        />
          <PrivateRoute
            component={BuyNow}
            exact
            path="/seller/buy-now-sales"
          />
        <PrivateRoute
          component={BuyNow}
          exact
          path="/seller/buy-now-sales/*"
        />
        <PrivateRoute exact component={MyStatistics} path="/seller/my-statistics" />
        <PrivateRoute exact component={Notification} path="/seller/notifications" />
        <PrivateRoute exact component={Promotions} path="/seller/my-promotions/*" />
        <PrivateRoute exact component={Promotions} path="/seller/my-promotions" />
        <PrivateRoute
          exact
          component={CreatePromotion}
          path="/seller/my-promotions/new"
        />
        <PrivateRoute exact component={Tmessenger} path="/seller/t-messenger" />
        <PrivateRoute exact component={Supports} path="/seller/supports" />
        <PrivateRoute exact component={Supports} path="/seller/supports/*" />
        <PrivateRoute
          exact
          component={Advertisements} 
          path="/seller/my-advertisements"
        />
        <PrivateRoute
          exact
          component={SetTest} 
          path="/seller/set-test"
        />
        <PrivateRoute
          exact
          component={Advertisements}
          path="/seller/my-advertisements/*"
        />
        <PrivateRoute exact component={MyProducts}  path="/seller/my-products" />
        <PrivateRoute exact component={MyProducts}  path="/seller/my-products/*" />
        <PrivateRoute exact component={()=>(<Redirect to="/seller/my-products" />)}  path="/seller" />
        <PrivateRoute exact component={NotFound}  path="/seller/*" />
      </Switch>
  ) 
};

export default Content;
