import React, { Fragment, useState, useContext, useEffect } from "react";
import { AuthContext } from "../../../context/authContext/AuthState";
import {} from "react";

const AllProduct = () => {
  const authCxt = useContext(AuthContext);
  const [posts, setPosts] = useState([]);
  const [status, setStaus] = useState(["active", "inactive"]);

  useEffect(() => {
    getMyProducts();
  }, []);

  const getMyProducts = () => {
    if (!authCxt.token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", "/api/seller/posts", true);
    xhr.onload = () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            if (Array.isArray(JSON.parse(xhr.responseText).success)) {
              let res = JSON.parse(xhr.responseText).success;
              if (res.length > 0) {
                setPosts(res);
              }
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };

  const resetStatus = (status, id) => {
    if (!authCxt.token) return;
    if (status === "active") {
      status = "inactive";
    } else if (status === "inactive") {
      status = "active";
    } else {
      return;
    }
    let dataform =  new FormData()
    dataform.append("status", status)
    dataform.append("id", id)
    let xhr = new XMLHttpRequest();
    xhr.open("POST", `/api/seller/posts/reset/status/${id}`, true);
    xhr.onload = () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            if (Object.keys(JSON.parse(xhr.responseText).success).length > 0) {
              let res = JSON.parse(xhr.responseText).success;
              console.log(res, " the res")
              if (res.post_status) {
                for (let i = 0; i < posts.length; i++) {
                  let post = posts[i];
                  if (post._id === res._id) {
                    post.post_status = res.post_status;
                    posts[i] = post
                    // setPosts();
                  }
                }
              }
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataform);
  };
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header">
          <h4>My Products</h4>
          <div className="statistics">
            <span className="active">
              <a href="seller-products-active.html">
                <i className="fa fa-check-circle"></i> Active (4)
              </a>
            </span>
            <span className="reviewing">
              <a href="seller-products-reviewing.html">
                <i className="fa fa-spinner"></i> Reviewing (1)
              </a>
            </span>
            <span className="declined">
              <a href="seller-products-declined.html">
                <i className="fa fa-times-circle"></i> Declined (1)
              </a>
            </span>
            <span className="all underline">
              <a href="seller-products-all.html">
                <i className="fa fa-check-square"></i> All (6)
              </a>
            </span>
          </div>
        </div>

        <div className="search-form">
          <div className="row">
            <div className="col-md-6 offset-md-6">
              <form action="">
                <div className="form-row align-items-baseline">
                  <div className="col-md-9">
                    <div className="input-container">
                      <i className="fa fa-search icon"></i>
                      <input
                        type="search"
                        name=""
                        id=""
                        className="input-field"
                      />
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="dropdown">
                      <button
                        className="dropdown-toggle"
                        type="button"
                        id="dropdownMenuButton"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        <i className="fa fa-filter"></i>
                      </button>
                      <div
                        className="dropdown-menu"
                        aria-labelledby="dropdownMenuButton"
                      >
                        <a className="dropdown-item" href="#">
                          All categories
                        </a>
                        <a className="dropdown-item" href="#">
                          Cereal crops
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>

        <div className="table-container">
          <div className="table-responsive">
            <table className="table table-borderless">
              <thead>
                <tr>
                  <th scope="col">Product ID</th>
                  <th scope="col">Name</th>
                  <th scope="col">Category</th>
                  <th scope="col">Post Type</th>
                  <th scope="col">Active</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody>
                {posts &&
                  Array.isArray(posts) &&
                  posts.map((post) => (
                    <Fragment>
                      <tr>
                        <th>{post._id}</th>
                        <td>{post.post_title}</td>
                        <td>Animal Farming</td>
                        <td className="buy-now">{post.post_type}</td>
                        <td>
                          {post.post_staus === "active" ||
                            ("inactive" ? (
                              <label className="activetoggle" for="togBtn">
                                <input
                                  type="checkbox"
                                  name=""
                                  id="togBtn"
                                  defaultChecked={
                                    post.post_status === "active" ? false : true
                                  }
                                  onChange={(e) => {
                                    resetStatus(post.post_status, post._id);
                                  }}
                                />
                                <div className="slider"></div>
                              </label>
                            ) : (
                              post.post_status
                            ))}
                        </td>
                        <td>
                          <div className="dropdown">
                            <button
                              className="edit-button dropdown-toggle"
                              type="button"
                              id="dropdownMenuButton"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                            >
                              Edit
                            </button>
                            <div
                              className="dropdown-menu"
                              aria-labelledby="dropdownMenuButton"
                            >
                              <a
                                className="dropdown-item"
                                href={`/seller/my-products/edit/${post.post_type}/${post._id}`}
                              >
                                Edit Product
                              </a>
                              <a
                                className="dropdown-item"
                                href={`/seller/my-products/manage-media/${post._id}`}
                              >
                                Manage Media
                              </a>
                              <a
                                className="dropdown-item"
                                href={`/seller/my-products/delete/${post._id}`}
                              >
                                Delete Product
                              </a>
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr>
                        <td className="view-stats" colspan="6">
                          <span>6 ads view (+2 today)</span>
                        </td>
                      </tr>
                    </Fragment>
                  ))}
                <tr>
                  {/* first row ends here  */}
                  <th>PID1372393</th>
                  <td>Maize Flour 25kg</td>
                  <td>Cereal Crops</td>
                  <td className="regular">Regular</td>
                  <td>
                    <label className="activetoggle" for="active">
                      <input type="checkbox" name="" id="active" checked />
                      <div className="slider"></div>
                    </label>
                  </td>
                  <td>
                    <div className="dropdown">
                      <button
                        className="edit-button dropdown-toggle"
                        type="button"
                        id="dropdownMenuButton"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Edit
                      </button>
                      <div
                        className="dropdown-menu"
                        aria-labelledby="dropdownMenuButton"
                      >
                        <a className="dropdown-item" href="#">
                          Edit Product
                        </a>
                        <a className="dropdown-item" href="#">
                          Manage Media
                        </a>
                        <a className="dropdown-item" href="#">
                          Delete Product
                        </a>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td className="view-stats" colspan="6">
                    <span>6 ads view (+2 today)</span>
                    <span>1 chat (+1 today)</span>
                    <span>1 phone view (+1 today)</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <div className="pagination d-flex justify-content-center align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
          <div>
            <label for="page" className="grey-text">
              Go to Page:
            </label>
            <input type="number" name="page" id="page" placeholder="Page" />
          </div>
          <ul>
            <li>
              <a href="#">
                <i className="fa fa-step-backward"></i>
              </a>
            </li>
            <li>
              <a href="#">
                <i className="fa fa-play rotate-icon"></i>
              </a>
            </li>
            <li>
              <a href="#">
                <i className="fa fa-play play-icon"></i>
              </a>
            </li>
            <li>
              <a href="#">
                <i className="fa fa-step-forward"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default AllProduct;
