import React, { useEffect, useContext, useState } from "react";
import Header from "../../common/Header";
import Footer from "../../common/Footer";
import Sidebar from "../../common/Sidebar";
import { AuthContext } from "../../context/authContext/AuthState";

const Account = (props) => {
  const authCxt = useContext(AuthContext);
  const [transactions, setTransactions] = useState([]);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  useEffect(() => {
    if (authCxt.token) {
      getTrasactionsHistory(authCxt.token);
    }
  }, [authCxt.token]);

  const getTrasactionsHistory = (token) => {
    if (!token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/seller/account/history`, true);
    xhr.onload = () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            let res = JSON.parse(xhr.responseText);
            if (Array.isArray(res.success)) {
              if (res.success.length > 0) {
                setTransactions([...res.success]);
              }
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };

  return (
    <>
      <Sidebar {...props}/>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="content-header">
            <h4>My Payment History</h4>
            <h6>
              Your Wallet Balance:{" "}
  <span className="bold-green-text">&#8358; {authCxt.user.wallet ? authCxt.user.wallet : 0}</span>
            </h6>
          </div>
          <div className="transactions d-flex justify-content-between">
            <p>Transactions</p>
            <p>Active Payout methods</p>
          </div>
          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Date</th>
                    <th scope="col">Description</th>
                    <th scope="col">Transaction Type</th>
                    <th scope="col">Status</th>
                    <th scope="col">Amount</th>
                  </tr>
                </thead>
                <tbody>
                  {transactions &&
                    transactions.map((txn, key) => (
                      <tr key={key}>
                        <td>
                          {txn.date}
                          {/* <span className="d-block">03:56pm</span> */}
                        </td>
                        <td>{txn.description}</td>
                        <td>{txn.transaction_type}</td>
                        <td className="processing">{txn.status}</td>
                        <td>&#8358; {txn.amount}</td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>

          <div className="pagination-number-of-items d-flex justify-content-between align-content-center align-items-baseline flex-wrap">
            <div className="pagination d-flex align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
              <div>
                <label htmlFor="page" className="grey-text">
                  Go to Page:
                </label>
                <input type="number" name="page" id="page" placeholder="Page" />
              </div>
              <ul>
                <li>
                  <a href="#">
                    <i className="fa fa-step-backward"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play rotate-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play play-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-step-forward"></i>
                  </a>
                </li>
              </ul>
            </div>
            <div className="number-per-page">
              <label htmlFor="">Items per page:</label>
              <select name="" id="">
                <option value="10">10</option>
              </select>
              <i className="fa fa-chevron-down withdraw-arrow arrow-down"></i>
            </div>
          </div>

          <div className="account-statement-container">
            <h6 className="account-statement">Account Statement</h6>
            <p className="mb-4">
              Download your Tinkoko market account statement
            </p>
            <div className="row justify-content-around">
              <div className="col-md-5 statement-form">
                <div className="form-field">
                  <label htmlFor="start-date">Start Date</label>
                  <input
                    type="date"
                    name="start-date"
                    id="start-date"
                    onChange={(e) => {
                      setStartDate(e.target.value);
                    }}
                  />
                </div>
                <div className="form-field">
                  <label htmlFor="end-date">End Date</label>
                  <input
                    type="date"
                    name="end-date"
                    id="end-date"
                    onChange={(e) => {
                      setEndDate(e.target.value);
                    }}
                  />
                </div>
                <button type="submit">Submit</button>
                <a href="#" className="download-statement-button">
                  <i className="fa fa-file-pdf"></i> Download Account Statement
                </a>
              </div>

              <div className="col-md-5 offset-md-1 payout-form">
                <h6 className="bank-info">Bank Name: Fidelity Bank</h6>
                <h6 className="bank-info">Account Number: 0132435545</h6>
                <h6 className="bank-info">BVN: 1213254545</h6>
                <form action="">
                  <p>
                    * To change your payout details, kindly{" "}
                    <span>
                      <a href="#">contact support</a>
                    </span>
                  </p>
                  <div className="form-field">
                    <label htmlFor="bank">Bank:</label>
                    <input type="text" name="bank" id="bank" />
                  </div>
                  <div className="form-field">
                    <label htmlFor="account-number">Account Number:</label>
                    <input
                      type="number"
                      name="account-number"
                      id="account-number"
                    />
                  </div>
                  <div className="form-field">
                    <label htmlFor="account-name">Account Name:</label>
                    <input type="text" name="account-name" id="account-name" />
                  </div>
                  <button type="submit">Submit</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Account;
