import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/authContext/AuthState";
import { SettingsContext } from "./SettingsState";

const MyProfile = () => {
  const authCxt = useContext(AuthContext);
  const settingsCxt = useContext(SettingsContext)
  const [fname, setFname] =  useState('')
  const [lname, setLname] =  useState('')
  const [bname, setBname] =  useState('')
  const [baddress, setBaddress] =  useState('')
  const [email, setEmail] =  useState('')
  const [city, setCity] =  useState('')
  const [state, setState] =  useState('')
  const [country, setCountry] =  useState('')

  useEffect(() => {
    setProfileData()
  }, []);

  
  const setProfileData = () =>{
    if (authCxt.user) {
      if (authCxt.user) {
        setFname(authCxt.user.firstName)
        setLname(authCxt.user.lastName)
        setBname(authCxt.user.businessName)
        setCity(authCxt.user.city)
        setState(authCxt.user.state)
        setCountry(authCxt.user.country)
        setEmail(authCxt.user.email)
        setBaddress(authCxt.user.businessAddress)
      }
    }
    
  }

  const modfityProfie = () =>{
    if (fname) {
      if (lname) {
        if (city) {
          if (state) {
            if (country) {
              settingsCxt.modifyProfile({fname, lname, city, state, country, baddress, bname})
            }
          }
        }
      }
    }
  }
 
  return (
    <div className="col-md-9">
      <div className="settings-main-content">
        <div className="settings-header">
          <h4>
            <i className="fa fa-id-card"></i>
            Profile
          </h4>
        </div>

        <div className="row profile-settings justify-content-center">
          <div className="col-md-7">
            <form action="">
              <div className="floating-label">
                <div className="count">
                  <span id="first-name-count">
                    {fname.length}
                  </span>{" "}
                  / 20
                </div>
                <input
                  type="text"
                  name="firstname"
                  id="firstname"
                  defaultValue={fname}
                  onChange={(el)=>setFname(el.target.value)}
                />
                <label htmlFor="firstname">First Name*</label>
              </div>
              <div className="floating-label">
                <div className="count">
                  <span id="last-name-count">
                    {lname.length}
                  </span>{" "}
                  / 20
                </div>
                <input
                  type="text"
                  name="lastname"
                  id="lastname"
                  onChange={(el)=>setLname(el.target.value)}
                  defaultValue={lname}
                />
                <label htmlFor="lastname">Last Name*</label>
              </div>
              <div className="floating-label disabled-field">
                <div className="count">
                  <span id="email-count">{email.length}</span> /
                  100
                </div>
                <input
                  type="text"
                  name="email"
                  id="email"
                  readOnly
                  defaultValue={email}
                />
                <label htmlFor="email">Email*</label>
              </div>
              <div className="form-field">
                <label
                  htmlFor="seller-username"
                  className="d-flex justify-content-between"
                >
                  Seller username
                  <span
                    data-toggle="popover"
                    data-container="body"
                    data-placement="top"
                    data-content="This is your seller unique identification number as a Tinkoko member. This cannot be changed."
                  >
                    <i className="fa fa-question-circle"></i>
                  </span>
                </label>
                <input
                  type="text"
                  name="seller-username"
                  id="seller-username"
                  defaultValue={authCxt.user._id}
                  // defaultValue="SID233434"
                  readOnly
                />
              </div>
              <div className="form-field">
                <label
                  htmlFor="business-name"
                  className="d-flex justify-content-between"
                >
                  Business name
                  <span
                    data-toggle="popover"
                    data-container="body"
                    data-placement="top"
                    data-content="Your business name cannot be changed."
                  >
                    <i className="fa fa-question-circle"></i>
                  </span>
                </label>
                <input
                  type="text"
                  name="business-name"
                  id="business-name"
                  defaultValue={bname}
                  readOnly
                />
              </div>
              <div className="form-field">
                <label htmlFor="business-address">Business address</label>
                <input
                  type="text"
                  name="business-address"
                  id="business-address"
                  defaultValue={baddress}
                  onChange={(el)=>setBaddress(el.target.value)}
                />
              </div>
              <div className="form-row">
                <div className="col-md-6">
                  <div className="form-field">
                    <i className="fa fa-chevron-down arrow-down"></i>
                    <label htmlFor="city">City</label>
                    <select name="city" id="city">
                      <option defaultValue={city}
                        onChange={(el)=>setCity(el.target.value)}
                      >
                        {city}
                      </option>
                    </select>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="form-field">
                    <i className="fa fa-chevron-down arrow-down"></i>
                    <label htmlFor="state">State</label>
                    <select name="state" id="state">
                      <option defaultValue={state}
                        onChange={(el)=>setState(el.target.value)}
                       >
                        {state}
                      </option>
                    </select>
                  </div>
                </div>
              </div>
              <div className="form-field">
                <i className="fa fa-chevron-down arrow-down"></i>
                <label htmlFor="country">Country</label>
                <select name="country" id="country">
                  <option defaultValue={country}
                    onChange={(el)=>setCountry(el.target.value)}
                  >
                    {country}
                  </option>
                </select>
              </div>
              <button type="button" onClick={modfityProfie} className="apply-changes">
                Apply Changes
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MyProfile;
