import React, { createContext, useReducer, useEffect, useState } from "react";
import BuyNowReducer from "./BuyNowReducer";

export const BuyNowContext = createContext()
const {Provider}  = BuyNowContext 

const BuyNowState = (props) => {
  const initialState = {
      orders:[],
      pendingOrders:[],
      shippedOrders:[],
      cancelledOrders:[],
      completedOrders:[],
      singleOrder:{}
  };
  const [state, dispatch] = useReducer(BuyNowReducer, initialState);

  const getOrders = (token) => {
    let xhr = new XMLHttpRequest();
    xhr.open("GET", "/api/seller/orders", true);
    xhr.onload = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
            if (xhr.responseText) {
                if (JSON.parse(xhr.responseText)) {
                    let res = JSON.parse(xhr.responseText)
                    if (Array.isArray(res.success) && res.success.length > 0) {
                        console.log(res, " the xhr response text ")
                        return dispatch({type:"SET_STATE", payload:{orders:[...res.success]}})
                    }
                }
            }
        }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };
  const getSingleOrder = (token, orderid) => {
    let xhr = new XMLHttpRequest();
    xhr.open("GET",`/api/seller/orders/${orderid}` , true);
    xhr.onload = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
            if (xhr.responseText) {
                if (JSON.parse(xhr.responseText)) {
                    let res = JSON.parse(xhr.responseText)
                    if ( Object.keys(res.success).length > 0) {
                        console.log(res, " the xhr response text ")
                        return dispatch({type:"SET_STATE", payload:{singleOrder:res.success}})
                    }
                }
            }
        }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };
  const confirmOrder = (token, orderid) => {
      let dataForm = new FormData()
      dataForm.append("orderid", orderid)
    let xhr = new XMLHttpRequest();
    xhr.open("POST",`/api/seller/orders/confirm` , true);
    xhr.onload = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
            if (xhr.responseText) {
                if (JSON.parse(xhr.responseText)) {
                    console.log(xhr.responseText,"the oreder response text in here ")
                    // let res = JSON.parse(xhr.responseText)
                    // if ( Object.keys(res.success).length > 0) {
                    //     console.log(res, " the xhr response text ")
                    //     return dispatch({type:"SET_STATE", payload:{singleOrder:res.success}})
                    // }
                }
            }
        }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send(dataForm);
  };
  return <Provider value={{ state, getOrders,getSingleOrder, confirmOrder }}>{props.children}</Provider>;
};

export default BuyNowState;
