import React from 'react'
import { useContext } from 'react'
import { useState } from 'react'

const NotificationSettings = () => {
    // tinkokoPushNotification
    // premiumPackages
    // yoursubscriptions
    // sMSinfonotifications
    // ordernotifications
    // accountsnotifications
    const [tinkokoPushNotification, setTinkokoPushNotification] = useState(null)
    const [premiumPackages, setPremiumPackages] = useState(null)
    const [yoursubscriptions, setYoursubscriptions] = useState(null)
    const [smsinfonotifications, setSMSinfonotifications] = useState(null)
    const [ordernotifications, setOrdernotifications] = useState(null)
    const [accountsnotifications, setAccountsnotifications] = useState(null)
    return (
        <div className="col-md-9">
        <div className="settings-main-content">
            <div className="settings-header">
                <h4>
                    <i className="fa fa-concierge-bell"></i>
                    Notification Settings
                </h4>
            </div>



            <div className="row justify-content-center notification-settings">
                <div className="col-md-7">
                    <form action="">
                        <div className="push-notification d-flex justify-content-between">
                            <p>Tinkoko Push Notification</p>
                            <div className="switch">
                                <label>
                                    <input type="checkbox"  />
                                    <span className="lever"></span>
                                </label>
                            </div>
                        </div>
                    </form>
                    <div className="other-notifications">
                        <div className="header">
                            <h6>Email Notifications</h6>
                        </div>
                        <form action="">
                            <div className="single-notification">
                                <p>Premium Packages</p>
                                <div className="switch">
                                    <label>
                                        <input type="checkbox" />
                                        <span className="lever"></span>
                                    </label>
                                </div>
                            </div>
                            <div className="single-notification">
                                <p>Your subscriptions</p>
                                <div className="switch">
                                    <label>
                                        <input type="checkbox" />
                                        <span className="lever"></span>
                                    </label>
                                </div>
                            </div>
                            <div className="single-notification">
                                <p>SMS info notifications</p>
                                <div className="switch">
                                    <label>
                                        <input type="checkbox" />
                                        <span className="lever"></span>
                                    </label>
                                </div>
                            </div>
                            <div className="single-notification">
                                <p>Order notifications</p>
                                <div className="switch">
                                    <label>
                                        <input type="checkbox" />
                                        <span className="lever"></span>
                                    </label>
                                </div>
                            </div>
                            <div className="single-notification">
                                <p>Accounts notifications</p>
                                <div className="switch">
                                    <label>
                                        <input type="checkbox" />
                                        <span className="lever"></span>
                                    </label>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

        </div>
    </div>
    )
}

export default NotificationSettings
