import React from "react";
const ProductSubmitted = () => {
  return (
    <>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="success-message-box">
            <div className="success-message">
              <i className="fa fa-check-circle icon"></i>
              <h6>Your post have been successfully submitted for review.</h6>
              <p>
                Our moderation team are reviewing your post. You will receive an
                email shortly after review. Thank you.
              </p>
            </div>
          </div>
          <div className="promotions">
            <h6>Promotions</h6>
            <p>
              A popular marketplace like Tinkoko has thousands of buyers and
              sellers. So how can you make your products stand out and reach
              even further?
            </p>
            <a href="#">Learn more</a>
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductSubmitted;
