import React, { useContext, useEffect, useState } from "react";
import Header from "../../common/Header";
import Footer from "../../common/Footer";
import Sidebar from "../../common/Sidebar";
import BuyNowState, { BuyNowContext } from "./BuyNowState";
import { AuthContext } from "../../context/authContext/AuthState";
import OrderDetailModal from "./OrderDetailModal";

const BuyNowSales = (props) => {
  const authCxt = useContext(AuthContext);
  const bnCxt = useContext(BuyNowContext);
  const [orderStatus] = useState({
    pending: "Awaiting Confirmation",
    confirmed: "Order Confirmed",
  });

  useEffect(() => {
    if (authCxt.token) {
      bnCxt.getOrders(authCxt.token);
    }
  }, []);
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header">
          <h4>Orders Overview</h4>
          <div className="statistics">
            <span className="all underline">
              <a href="seller-buy-now-sales-all.html">
                <i className="fa fa-check-square"></i> All (
                {bnCxt.state.orders.length})
              </a>
            </span>
            <span className="reviewing">
              <a href="seller-buy-now-sales-pending.html">
                <i className="fa fa-spinner"></i> Pending (
                {bnCxt.state.pendingOrders.length})
              </a>
            </span>
            <span className="reviewing">
              <a href="seller-buy-now-sales-shipped.html">
                <i className="fa fa-shipping-fast"></i>
                Shipped ({bnCxt.state.shippedOrders.length})
              </a>
            </span>
            <span className="declined">
              <a href="seller-buy-now-sales-cancelled.html">
                <i className="fa fa-times-circle"></i>
                Cancelled ({bnCxt.state.cancelledOrders.length})
              </a>
            </span>
            <span className="active">
              <a href="seller-buy-now-sales-completed.html">
                <i className="fa fa-check-circle"></i>
                Completed ({bnCxt.state.completedOrders.length})
              </a>
            </span>
          </div>
        </div>

        <div className="search-form">
          <div className="row">
            <div className="col-md-6">
              <h6>
                <i className="fa fa-wallet"></i> Wallet :{" "}
                <span>&#8358; {authCxt.user.wallet ? authCxt.user.wallet : 0}</span>
              </h6>
            </div>
            <div className="col-md-4 offset-md-2">
              <form action="">
                <div className="form-row align-items-baseline">
                  <div className="col-md-12">
                    <div className="input-container">
                      <i className="fa fa-search icon"></i>
                      <input
                        type="search"
                        name=""
                        id=""
                        className="input-field"
                      />
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>

        <div className="table-container">
          <div className="table-responsive">
            <table className="table table-bordered">
              <thead>
                <tr>
                  <th scope="col">Order ID</th>
                  <th scope="col">Order Confirmation Date</th>
                  <th scope="col">Payment method</th>
                  <th scope="col">Price</th>
                  <th scope="col">Status</th>
                  <th scope="col">Shipment Method</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>

              <tbody>
                {bnCxt.state.orders &&
                  bnCxt.state.orders.map((o, key) => (
                    <tr key={key}>
                      <td
                        className="order-id"
                        data-toggle="modal"
                        data-target="#order-quick-view"
                      >
                        {o._id}
                      </td>
                      <td>
                        {o.date_placed}
                        <span className="d-block">11:56pm</span>
                      </td>
                      <td>{o.payment_method}</td>
                      <td>{o.total_price}</td>
                      <td className="awaiting-confirmation">
                        {orderStatus[o.order_status]
                          ? orderStatus[o.order_status]
                          : o.order_status}
                      </td>
                      <td>{o.shipping_method}</td>
                      <td>
                        {o.order_status === "pending" && (
                          <a
                            href="#"
                            onClick={(e) => {
                              e.preventDefault();
                              bnCxt.confirmOrder(authCxt.token, o._id)
                            }}
                            className="confirm-order"
                          >
                            <i className="fa fa-check-circle"></i>
                            Confirm Order
                          </a>
                        )}
                        {o.order_status === "confirmed" && (
                          <a
                            href="#"
                            onClick={(e) => {
                              e.preventDefault();
                            }}
                            className="set-as-ready-to-ship"
                          >
                            <i className="fa fa-shipping-fast"></i> Set to ready
                            to ship
                          </a>
                        )}
                        <a
                          href={`/seller/buy-now-sales/orders/${o._id}`}
                          className="view"
                        >
                          <i className="fa fa-eye"></i> View
                        </a>
                      </td>
                      <OrderDetailModal order={o} />
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
        </div>

        <div className="row">
          <div className="col-md-6 offset-md-3 pagination d-flex justify-content-center align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
            <div>
              <label for="page" className="grey-text">
                Go to Page:
              </label>
              <input type="number" name="page" id="page" placeholder="Page" />
            </div>
            <ul>
              <li>
                <a href="#">
                  <i className="fa fa-step-backward"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="fa fa-play rotate-icon"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="fa fa-play play-icon"></i>
                </a>
              </li>
              <li>
                <a href="#">
                  <i className="fa fa-step-forward"></i>
                </a>
              </li>
            </ul>
          </div>
          <div className="col-md-3 mt-4">
            <div className="number-per-page">
              <label for="">Items per page:</label>
              <select name="" id="">
                <option value="10">10</option>
              </select>
              <i className="fa fa-chevron-down arrow-down"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BuyNowSales;
