import React, { useEffect, useContext } from "react";
import { Link } from "react-router-dom";
import AlertResponse from "../alert/AlertResponse";
import { AuthContext } from "../context/authContext/AuthState";

const Header = () => {
  const authCxt = useContext(AuthContext);
  useEffect(() => {
    //   <button onclick="scrollToTop()" id="scrollToTop" title="Go to top">
    //   <i className="fa fa-arrow-up"></i>
    // </button>
  }, []);

  return (
    <>
      <header>
        <div className="container-fluid">
          <nav className="navbar navbar-expand-lg navbar-light bg-transparent">
            <a className="navbar-brand" href="#">
              Tinkoko
            </a>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span>
                <i className="fa fa-bars"></i>
              </span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
              <ul className="navbar-nav">
                <li className="nav-item">
                  <a className="nav-link" href="#">
                    Timeline
                  </a>
                </li>
              </ul>
              <ul className="navbar-nav ml-auto">
                {authCxt.user ? (
                  <>
                    <li className="bell-icon">
                      <a href="notifications.html">
                        <i className="fa fa-bell"></i>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" href="/seller/t-messenger">
                        <span className="messenger-icon">
                          <i className="fa fa-comment-alt"></i>
                        </span>{" "}
                        T-Messenger
                      </a>
                    </li>
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link dropdown-toggle"
                        href="#"
                        id="navbarDropdown"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        <i className="fa fa-user"></i> My Account
                      </a>
                      <div
                        className="dropdown-menu "
                        aria-labelledby="navbarDropdown"
                      >
                        <a className="dropdown-item" href="#">
                          Profile
                        </a>
                        <a className="dropdown-item" href="#">
                          Settings
                        </a>
                        <a className="dropdown-item" href="#">
                          Support
                        </a>
                        <a className="dropdown-item" href="#">
                          Favorites
                        </a>
                        <a
                          className="dropdown-item"
                          href="#"
                          onClick={(e) => {
                            e.preventDefault();
                            authCxt.Logout();
                          }}
                        >
                          Logout
                        </a>
                      </div>
                    </li>
                  </>
                ):(
                  <li className="nav-item">
                  <Link
                    className="nav-link post-product"
                    to="/login"
                  >
                    Login
                  </Link>
                </li>
                )}
                <li className="nav-item">
                  <Link
                    className="nav-link post-product"
                    to="/seller/my-products/create-product/"
                  >
                    Post Your Products
                  </Link>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </header>
      <AlertResponse />
    </>
  );
};

export default Header;
