import React, { useState, useContext, useEffect } from "react";
import { AdsContext } from "./AdsState";
import { Redirect } from "react-router-dom";
import { AuthContext } from "../../context/authContext/AuthState";
import * as urlStringObject from "../../utils/urlStringObject"


const ProceedToPayment = (props) => {
  const authCxt = useContext(AuthContext);
  const adsCxt = useContext(AdsContext);
  useEffect(() => {
    console.log(props, "the props ")
    // if (props.match.params) {
    //   if (props.match.params.newad) {
    //     urlStringObject.decodeData(props.match.params.newad).then(data=>{
    //       console.log(data, "the url string object ")
    //       if (data) {
    //         adsCxt.setValue({...data})
    //       }
    //     })
    //   }
    // }
    
  }, [])
// param : newad

  useEffect(() => {}, []);
  const setAdvert = () => {
    const dataForm = new FormData();
    dataForm.append("adbudget", adsCxt.state.adTotal);
    dataForm.append("adtype", adsCxt.state.selectedType._id);
    dataForm.append("adimage", adsCxt.state.campaignImage);
    dataForm.append("adduration", adsCxt.state.duration);
    let xhr = new XMLHttpRequest();
    xhr.open("POST", "/api/seller/my-adverts", true);
    xhr.onload = () => {
      console.log(xhr.responseText, " setting ads responsetext ");
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header adverts-top">
          <h4>
            <a href="my-advertisements-create-advert.html">
              <i className="fa fa-arrow-alt-circle-left"></i> Subscribe to
              Campaign
            </a>
          </h4>
        </div>

        <div className="row justify-content-center">
          <div className="col-md-7">
            <div className="subscribe-to-campaign">
              <div className="media">
                <i className="fa fa-credit-card"></i>
                <div className="media-body">
                  <h6>Advert Subscription</h6>
                </div>
              </div>
              <div className="subscription-box">
                <div className="header">
                  <h6>
                    Subscription for{" "}
                    {adsCxt.state.selectedType &&
                      adsCxt.state.selectedType.name}{" "}
                    advert campaign
                  </h6>
                </div>
                <div className="total d-flex justify-content-between">
                  <h6>Total</h6>
                  <h6>N {adsCxt.state.adTotal && adsCxt.state.adTotal}</h6>
                </div>
              </div>
              <div className="use-remaining-balance">
                <label className="switch">
                  <input type="checkbox" />
                  <span className="slider round"></span>
                </label>
                <h6>
                  <span className="mr-2">&#8358; {authCxt.user.wallet ? authCxt.user.wallet : 0}</span>
                  Use remaining balance
                  <span className="ml-2">
                    <i className="fa fa-exclamation-circle"></i>
                  </span>
                </h6>
              </div>
              <div className="subscribe-box">
                <button
                  className="subscribe-button"
                  // data-toggle="modal"
                  // data-target="#advert-preview-modal"
                  onClick={()=>{
                    setAdvert()
                  }}
                >
                  Subscribe <span>N {adsCxt.state.adTotal}</span>
                </button>
                {/* <button
                  className="subscribe-button"
                  data-toggle="modal"
                  data-target="#advert-preview-modal"
                >
                  Subscribe <span>N {adsCxt.state.adTotal}</span>
                </button> */}
                <span className="discount">N25,000.00 OFF!</span>
              </div>
              <div className="advert-preview-modal">
                <div
                  className="modal fade"
                  id="advert-preview-modal"
                  tabindex="-1"
                  role="dialog"
                  aria-hidden="true"
                >
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                  <div
                    className="modal-dialog modal-lg modal-dialog-centered"
                    role="document"
                  >
                    <div className="modal-content">
                      <div className="modal-body">
                        <h5>Advert Campaign Subscription</h5>
                        <p className="amount">25,000 NGN</p>
                      </div>
                      <div className="payment-method">
                        <ul className="nav nav-tabs" id="myTab" role="tablist">
                          <li className="nav-item">
                            <a
                              className="nav-link active"
                              id="home-tab"
                              data-toggle="tab"
                              href="#cash-payment"
                              role="tab"
                              aria-controls="cash-payment"
                              aria-selected="true"
                            >
                              <span>
                                <i className="fa fa-money-bill-alt"></i>
                              </span>
                              Cash Payment
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              className="nav-link"
                              id="profile-tab"
                              data-toggle="tab"
                              href="#online-payment"
                              role="tab"
                              aria-controls="online-payment"
                              aria-selected="false"
                            >
                              <span>
                                <i className="fa fa-qrcode"></i>
                              </span>
                              Online Payment
                            </a>
                          </li>
                        </ul>
                        <div className="tab-content" id="myTabContent">
                          <div
                            className="tab-pane fade show active"
                            id="cash-payment"
                            role="tabpanel"
                            aria-labelledby="cash-payment-tab"
                          >
                            <div className="single-div">
                              <h6>Payment with cash via any bank branch</h6>
                              <p>
                                Save these details and pay at the nearest bank
                                branch
                              </p>
                            </div>
                            <div className="single-div">
                              <h6>
                                1. Go to the bank teller and provide the
                                following information:
                              </h6>
                              <p>Platform: eBillsPay</p>
                              <p>Merchant: TINKOKO</p>
                              <p>Invoice: 86507822520</p>
                              <p>Amount: 6,999.00 NGN</p>
                            </div>
                            <div className="single-div">
                              <h6>2. Payment details</h6>
                              <p>Amount: 25,000.00 NGN</p>
                              <p>Fee: 100.00 NGN</p>
                              <p>Total: 25,100 NGN</p>
                            </div>
                            <div className="single-div">
                              <span>
                                All payments are debited automatically
                              </span>
                              <span>
                                Please be sure that the bank teller enters
                                correct invoice number
                              </span>
                              <span>Keep your payment receipt</span>
                            </div>
                            <p className="encrypted">
                              Your transaction details are encrypted and are
                              never shared with third parties
                            </p>
                          </div>
                          <div
                            className="tab-pane fade"
                            id="online-payment"
                            role="tabpanel"
                            aria-labelledby="online-payment-tab"
                          >
                            Online payment details goes in here
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProceedToPayment;
