import React, {
  DOMAttributes,
  useState,
  useEffect,
  createContext,
  useReducer,
} from "react";
import { useContext } from "react";
import { AuthContext } from "../../../context/authContext/AuthState";
import { Link } from "react-router-dom";
import { AlertContext } from "../../../context/alertContext/AlertState";
import JoblistingReducer from "./JoblistingReducer";
import ProductSubmitted from "./ProductSubmitted";
import JoblistingDetail from "./JoblistingDetail";

export const JoblistingContext = createContext();
const { Provider } = JoblistingContext;

const CreateJobLIstingProduct = () => {
  const initialState = {
    category: null,
    subCategory: null,
    region: null,
    title: null,
    description: null,
    businessIndustry: null,
    type: null,
    businessFunction: null,
    role: null,
    minQualification: null,
    yearsOfExperience: {},
    specialization: null,
    skills: null,
    maxAge: null,
    preferredGender: null,
    applicationDealine: null,
    advanceListing: [],
    jobImages: null,
    salaryFrom: null,
    salaryTo: null,

    businessName: null,
    country: null,
    isSubmitted: false,
    nextSection: false,
    tab: ["current active", ""],
  };
  const [state, dispatch] = useReducer(JoblistingReducer, initialState);

  useEffect(() => {
    authCxt.loadUser();
  }, []);

  const authCxt = useContext(AuthContext);
  const alertCxt = useContext(AlertContext);
  const [submitted, setSubmitted] = useState(false);

  // SET PRODUCT STATE
  const setJstate = (prop = {}) => {
    dispatch({ type: "SET-STATE", payload: prop });
  };
  return (
    <Provider value={{ jState: state, setJstate }}>
      <>{state.isSubmitted ? <ProductSubmitted /> : <JoblistingDetail />}</>
    </Provider>
  );
};

export default CreateJobLIstingProduct;
