import React, { useState, useReducer, createContext, useEffect } from "react";
import AdsReducer from "./AdsReducer";

export const AdsContext = createContext();
const { Provider } = AdsContext;
const AdsState = (props) => {
  const initialState = {
    adsTypes: [],
    adbanner:"",
    adImage: {},
    duration: 0,
    adTotal: 0,
    selectedType: {},
    campaignImage: {},
    adDuration:0,
    processPayment: false,
    myAds:[]
  };
  const [state, dispatch] = useReducer(AdsReducer, initialState);

  useEffect(() => {}, []);

  const setValue = (obj = {}) => {
    dispatch({ type: "SET_STATE", payload: obj });
  };

  return <Provider value={{ setValue, state }}>{props.children}</Provider>;
};

export default AdsState;
