import React, { useContext, useState, useEffect } from "react";
import hornIconImg from "../../../images/horn-icon.PNG";
import { AdsContext } from "./AdsState";
import { AuthContext } from "../../context/authContext/AuthState";

const MyAdvertisements = () => {
  const adsCxt = useContext(AdsContext);
  const authCxt = useContext(AuthContext);
  useEffect(() => {
    authCxt.loadUser();
    getMyAds();
  }, [authCxt.token]);

  const getMyAds = () => {
    if (!authCxt.token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", "/api/seller/my-adverts", true);
    xhr.onload = () => {
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            let res = JSON.parse(xhr.responseText);
            if (res.success) {
              if (Array.isArray(res.success)) {
                adsCxt.setValue({ myAds: res.success });
                console.log(res, " the my ads response text ");
              }
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };
  return (
    <>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="content-header">
            <h4>My Tinkoko Adverts</h4>
          </div>

          <div className="adverts-header">
            <div className="row">
              <div className="col-md-4">
                <img className="img-fluid" src={hornIconImg} alt="" />
              </div>
              <div className="col-md-6">
                <p>
                  Advertising on Tinkoko Market gives you the perfect
                  opportunity for creating and effective brand / product
                  awareness for your business. With different strategic
                  locations to place your ads both on mobile and desktop.
                </p>
              </div>
            </div>
          </div>

          <div className="create-new-advert">
            <div className="row">
              <div className="col-md-6">
                <a href="/seller/my-advertisements/new">New advert campaign</a>
              </div>
              <div className="col-md-4 offset-md-2">
                <form action="">
                  <div className="form-row align-items-baseline">
                    <div className="col-md-12">
                      <div className="input-container">
                        <i className="fa fa-search icon"></i>
                        <input
                          type="search"
                          name=""
                          id=""
                          className="input-field"
                        />
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>

          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Advert ID</th>
                    <th scope="col">Type of Advert</th>
                    <th scope="col">Date Started</th>
                    <th scope="col">End date</th>
                    <th scope="col">Impressions</th>
                    <th scope="col">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {Array.isArray(adsCxt.state.myAds ) &&
                    adsCxt.state.myAds.map((ad, key) => (
                      <tr key={key}>
                        <td>{ad._id}</td>
                        <td>{ad.type}</td>
                        <td>
                          {ad.start_date}{" "}
                          <span className="d-block">03:56pm</span>
                        </td>
                        <td>
                          {ad.end_date} <span className="d-block">03:56pm</span>
                        </td>
                        <td></td>
                        <td className="active-advert">{ad.status}</td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>

          <div className="pagination-number-of-items d-flex justify-content-between align-content-center align-items-baseline flex-wrap">
            <div className="pagination d-flex align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
              <div>
                <label htmlFor="page" className="grey-text">
                  Go to Page:
                </label>
                <input type="number" name="page" id="page" placeholder="Page" />
              </div>
              <ul>
                <li>
                  <a href="#">
                    <i className="fa fa-step-backward"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play rotate-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play play-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-step-forward"></i>
                  </a>
                </li>
              </ul>
            </div>
            <div className="number-per-page promotion-arrow">
              <label htmlFor="">Items per page:</label>
              <select name="" id="">
                <option value="10">10</option>
              </select>
              <i className="fa fa-chevron-down arrow-down"></i>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default MyAdvertisements;
