import React, { useContext, useEffect, useState } from "react";
import { SupportContext } from "./SupportState";
import { AuthContext } from "../../context/authContext/AuthState";

const SupportFeedback = () => {
  const authCxt = useContext(AuthContext);
  const supportCxt = useContext(SupportContext);
  const { setValue, state } = supportCxt;

  useEffect(() => {
    getPost();
  }, [authCxt.token]);

  const getPost = () => {
    if (authCxt.token) {
      let xhr = new XMLHttpRequest();
      xhr.open("GET", `/api/seller/supports`, true);
      xhr.onload = () => {
        if (xhr.status === 200 && xhr.readyState === 4) {
          if (xhr.responseText) {
            if (JSON.parse(xhr.responseText)) {
              let res = JSON.parse(xhr.responseText).success;
              if (res) {
                if (Object.keys(res).length > 0) {
                  console.log(res, "the entire support I have created ");
                  setValue({ ...res });
                  setValue({ supports: [ ...res] });
                }
              }
            }
          }
        }
      };
      xhr.setRequestHeader("x-auth-user", authCxt.token);
      xhr.send();
    }
  };

  return (
    <>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="claim-feedback">
            <ul className="nav nav-tabs" id="myTab" role="tablist">
              <li className="nav-item">
                <a
                  className="nav-link active"
                  id="support-tab"
                  data-toggle="tab"
                  href="#support"
                  role="tab"
                  aria-selected="true"
                >
                  Support
                </a>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link"
                  id="feedback-tab"
                  data-toggle="tab"
                  href="#feedback"
                  role="tab"
                  aria-selected="false"
                >
                  Feedback
                </a>
              </li>
            </ul>
            <div className="tab-content" id="myTabContent">
              <div
                className="tab-pane fade show active"
                id="support"
                role="tabpanel"
              >
                <div className="open-a-ticket">
                  <div className="row">
                    <div className="col-md-6">
                      <a href="/seller/supports/open-ticket">Open a ticket</a>
                    </div>
                  </div>
                </div>

                <div className="table-container">
                  <div className="table-responsive">
                    <table className="table table-bordered">
                      <thead>
                        <tr>
                          <th scope="col">Ticket ID</th>
                          <th scope="col">Subject</th>
                          <th scope="col">Description</th>
                          <th scope="col">Opened on</th>
                          <th scope="col">Status</th>
                          <th scope="col">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {state.supports.map((support, key) => (
                          <tr key={key}>
                            <td>{support.ticket_number}</td>
                            <td>{support.reason}</td>
                            <td>{support.comment}</td>
                            <td>
                              {support.date_created}{" "}
                              <span className="d-block">
                                {new Date(support.date_created).getHours()}:
                                {new Date(support.date_created).getMinutes()}
                              </span>
                            </td>
                            <td className="under-review-advert">
                              {support.status}
                            </td>
                            <td>
                              <a
                                href="claim-feedback-claim-thread.html"
                                className="view"
                              >
                                <i className="fa fa-eye"></i> Open
                              </a>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>

                <div className="pagination-number-of-items d-flex justify-content-between align-content-center align-items-baseline flex-wrap">
                  <div className="pagination d-flex align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
                    <div>
                      <label for="page" className="grey-text">
                        Go to Page:
                      </label>
                      <input
                        type="number"
                        name="page"
                        id="page"
                        placeholder="Page"
                      />
                    </div>
                    <ul>
                      <li>
                        <a href="#">
                          <i className="fa fa-step-backward"></i>
                        </a>
                      </li>
                      <li>
                        <a href="#">
                          <i className="fa fa-play rotate-icon"></i>
                        </a>
                      </li>
                      <li>
                        <a href="#">
                          <i className="fa fa-play play-icon"></i>
                        </a>
                      </li>
                      <li>
                        <a href="#">
                          <i className="fa fa-step-forward"></i>
                        </a>
                      </li>
                    </ul>
                  </div>
                  <div className="number-per-page promotion-arrow">
                    <label for="">Items per page:</label>
                    <select name="" id="">
                      <option value="10">10</option>
                    </select>
                    <i className="fa fa-chevron-down arrow-down"></i>
                  </div>
                </div>
              </div>
              <div className="tab-pane fade" id="feedback" role="tabpanel">
                <div className="feedback">
                  <h6 className="header-text">
                    Feedbacks and alerts from Tinkoko
                  </h6>
                  <p className="header-paragraph">
                    Stay tuned for relevant information and announcements
                    tailored for you.
                  </p>
                  <div className="alerts-container">
                    <div className="single-alert">
                      <span className="icons">
                        <i className="fa fa-bell"></i>
                        <i className="fa fa-link"></i>
                      </span>
                      <span className="remove">
                        <i className="fa fa-times"></i>
                      </span>
                      <a href="#">Learn how to create an effective post</a>
                    </div>
                    <div className="single-alert">
                      <span className="icons">
                        <i className="fa fa-bell"></i>
                      </span>
                      <span className="remove">
                        <i className="fa fa-times"></i>
                      </span>
                      <p>
                        Dear John Doe, kindly update your store with more
                        products and sell to a larger audience.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SupportFeedback;
