import React, { createContext, useReducer, useContext, useEffect } from "react";
import EditReducer from "./EditReducer";
import {AuthContext} from "../../../context/authContext/AuthState"

export const EditContext = createContext();
const { Provider } = EditContext;

export const EditState = (props) => {
  const authCxt = useContext(AuthContext)
  useEffect(() => {
    getCategories(authCxt.token)
  }, [authCxt.token])
  const initialState = {
    post:{},
    categories:[],
    subCategories:[],
    regions:[],
    productImages:[],
    productVideo:{},
    // advance_listing:[],
    tab: ["current active", ""],
    section:["current", "next"],
    isSubmitted: false,
    nextSection: false,
    showSection:["block", "none"]

  };
  const [state, dispatch] = useReducer(EditReducer, initialState);
  const setValue = (obj = {}) => {
    dispatch({type:"SET_STATE", payload:{...obj}});
  };
  const getCategories = (token) =>{
      if ( token) {
        let xhr = new XMLHttpRequest()
        xhr.open("GET", `/api/seller/product-categories`, true)
        xhr.onload = () =>{
          if (xhr.status === 200 && xhr.readyState === 4) {
            if (xhr.responseText) {
              if (JSON.parse(xhr.responseText)) {
                let res = JSON.parse(xhr.responseText).success
                if (res) {
                  if (Object.keys(res).length > 0) {
                    setValue({categories:{...res}})
                  }
                }
              }
            }
          }
        }
        xhr.setRequestHeader("x-auth-user", token)
        xhr.send()
  
      }
  }
  const getCountries = (token) =>{
      if ( token) {
        let xhr = new XMLHttpRequest()
        xhr.open("GET", `/api/seller/product-categories`, true)
        xhr.onload = () =>{
          if (xhr.status === 200 && xhr.readyState === 4) {
            if (xhr.responseText) {
              if (JSON.parse(xhr.responseText)) {
                let res = JSON.parse(xhr.responseText).success
                if (res) {
                  if (Array.isArray(res)) {
                    setValue({categories:[...res]})
                  }
                }
              }
            }
          }
        }
        xhr.setRequestHeader("x-auth-user", token)
        xhr.send()
  
      }
  }
  return <Provider value={{setValue,state }}>{props.children}</Provider>;
};

export default EditState;
