import React, { useState, useEffect } from "react";
import axios from "axios";
import {Link} from "react-router-dom"

import postItem from "../../../../images/post-item.PNG";
import postJob from "../../../../images/post-job.PNG";

const CreateProduct = () => {
  const [categories, setCategories] = useState([]);
  useEffect(() => {
    productCategories();
  }, []);

  const productCategories = async () => {
    try {
      let res = await axios.get("/api/seller/product-categories");
      if (res.data) {
        setCategories(res.data);
      }
    } catch (error) {
      setCategories([]);
    }
  };
  return (
    <>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="create-product">
            <h5 className="header">
              Let Us help you create your post. Choose an option below:
            </h5>
            <div className="row">
              <div className="col-md-6">
                <div className="post-item">
                  <img className="img-fluid" src={postItem} alt="" />
                  <Link to="/seller/my-products/create-product/regular">
                    Sell an item or service (Regular Post){" "}
                    <span className="float-right">
                      <i className="fa fa-chevron-right"></i>
                    </span>
                 </Link>
                  <Link to="/seller/my-products/create-product/buynow">
                    Sell an item (Buy now){" "}
                    <span className="float-right">
                      <i className="fa fa-chevron-right"></i>
                    </span>
                 </Link>
                </div>
              </div>
              <div className="col-md-6">
                <div className="post-job">
                  <img className="img-fluid" src={postJob} alt="" />
                  <Link to="/seller/my-products/create-product/joblisting">
                    Post a job offering or work request{" "}
                    <span className="float-right">
                      <i className="fa fa-chevron-right"></i>
                    </span>
                  </Link>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="quick-rules">
                  <h5>Quick Rules</h5>
                  <h6>
                    All product types posted on Tinkoko market place must follow
                    our rules
                  </h6>
                  <ul>
                    <li>
                      Make sure you post in the correct category of your product
                      or job-offering.
                    </li>
                    <li>Do not post the same ad more than once.</li>
                    <li>
                      Do not upload images with watermarks(phone number, bold
                      texts overlay etc).
                    </li>
                    <li>
                      Do not post ads containing multiple items unless it is a
                      package deal.
                    </li>
                    <li>
                      Do not put your email or phone numbers in the title or
                      description.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CreateProduct;
