import React, { useContext, useEffect, useState } from "react";
import { Route, Switch } from "react-router-dom";
import SettingsSidebar from "../../common/SettingsSidebar";
import AuthState, { AuthContext } from "../../context/authContext/AuthState";
import MyProfile from "./MyProfile";
import BankAccountDetails from "./BankAccountDetails";
import SocialNetworks from "./SocialNetworks";
import ChangePassword from "./ChangePassword";
import MobilePhoneSettings from "./MobilePhoneSettings";
import TMessengerSettings from "./TMessengerSettings";
import NotificationSettings from "./NotificationSettings";
import SecuritySettings from "./SecuritySettings";
import SettingsState from "./SettingsState";

const Settings = (props) => {
  const authCxt = useContext(AuthContext);
  useEffect(() => {
    authCxt.loadUser();
    console.log(props, "all the props on the settings area ");
    //eslint-disable-next-line
  }, []);
  return (
      <SettingsState>
        <SettingsSidebar />
        <Switch>
          <Route
            exact
            path="/seller/settings/my-profile"
            component={MyProfile}
          />
          <Route exact path="/seller/settings" component={MyProfile} />

          <Route
            path="/seller/settings/bank-account-details"
            component={BankAccountDetails}
          />
          <Route
            path="/seller/settings/social-networks"
            component={SocialNetworks}
          />
          <Route
            path="/seller/settings/change-password"
            component={ChangePassword}
          />
          <Route
            path="/seller/settings/mobile-phone"
            component={MobilePhoneSettings}
          />
          <Route
            path="/seller/settings/t-messenger"
            component={TMessengerSettings}
          />
          <Route
            path="/seller/settings/notifications"
            component={NotificationSettings}
          />
          <Route
            path="/seller/settings/security"
            component={SecuritySettings}
          />
        </Switch>
      </SettingsState>
    // </section>
  );
};

export default Settings;
