import React, { useEffect, useState, useContext } from "react";
import { Link, Route } from "react-router-dom";
import axios from "axios";
import { BuyNowPostContext } from "../create-product/CreateBuyNowProduct";
import { AuthContext } from "../../../context/authContext/AuthState";
import { errorBorder } from "../../../utils/errorBorder";
import { EditContext } from "./EditState";
import EditRegularPostNext from "./EditRegularPostNext";
import ProductSubmitted from "./ProductSubmitted";
const EditRegularPost = (props) => {
  // const { setPstate, Pstate } = useContext(BuyNowPostContext);
  const authCxt = useContext(AuthContext);
  const bnCxt = useContext(EditContext);
  const { post } = bnCxt.state;
  const [postid, setPostid] = useState(props.match.params.postid);
  const [categories, setCategories] = useState([]);
  const [subCategories, setSubCategories] = useState([]);
  const [regions, setRegions] = useState([]);
  const [countries, setCountries] = useState([]);
  const [previewImages, setPreviewImages] = useState([]);
  const [productImages, setProductImages] = useState([]);
  const [videoPreview, steVideoPreivew] = useState("");

  useEffect(() => {
    getPost();
    productCategories();
    getRegions();
    getCountries();
  }, [authCxt.token]);
  useEffect(() => {
    productSubCategories(bnCxt.state.post_category);
  }, [bnCxt.state._id, categories.length]);

  const getPost = () => {
    if (postid && authCxt.token) {
      let xhr = new XMLHttpRequest();
      xhr.open("GET", `/api/seller/regular/${postid}`, true);
      xhr.onload = () => {
        if (xhr.status === 200 && xhr.readyState === 4) {
          if (xhr.responseText) {
            if (JSON.parse(xhr.responseText)) {
              let res = JSON.parse(xhr.responseText).success;
              if (res) {
                if (Object.keys(res).length > 0) {
                  bnCxt.setValue({ ...res });
                  bnCxt.setValue({ post: { ...res } });
                }
              }
            }
          }
        }
      };
      xhr.setRequestHeader("x-auth-user", authCxt.token);
      xhr.send();
    }
  };

  // EXTRACT IMAGE FROM THE FILE INPUT FORM TO SET THE PREVIEW IMAGE STATE
  function readImage(event) {
    if (window.File && window.FileList && window.FileReader) {
      var files = event.target.files; //FileList object
      for (let i = 0; i < files.length; i++) {
        if (i <= 10) {
          var file = files[i];
          if (!file.type.match("image")) continue;
          bnCxt.setValue({
            productImages: [...bnCxt.state.productImages, { file }],
          });
          //   setProductImages([...productImages, { file }]);
          var picReader = new FileReader();
          picReader.addEventListener("load", function (event) {
            var picFile = event.target;
            Array.isArray(bnCxt.state.post_images) &&
              bnCxt.state.post_images.length <= 10 &&
              bnCxt.setValue({
                post_images: [
                  ...(Array.isArray(bnCxt.state.post_images) &&
                    bnCxt.state.post_images),
                  picFile.result,
                ],
              });
          });
          picReader.readAsDataURL(file);
        }
      }

      event.target.value = "";
    } else {
      console.log("Browser not support");
    }
  }

  // CANCLE AN IMAGE
  function cancelImage(img, key) {
    let previews = [];
    for (let j = 0; j < bnCxt.state.post_images.length; j++) {
      let pr = bnCxt.state.post_images[j];
      if (j !== key) {
        previews.push(pr);
      }
    }
    bnCxt.setValue({ post_images: previews });
    for (let i = 0; i < bnCxt.state.productImages.length; i++) {
      var picReader = new FileReader();
      picReader.addEventListener("load", function (event) {
        var picFile = event.target;
        if (picFile.result == img) {
          bnCxt.setValue({
            post_images: bnCxt.state.productImages.filter(
              (a) => a.file !== bnCxt.state.productImages[i].file
            ),
          });
        }
      });
      if (bnCxt.state.productImages[i]) {
        if (bnCxt.state.productImages[i].file) {
          picReader.readAsDataURL(bnCxt.state.productImages[i].file);
        }
      }
    }
    if (Array.isArray(bnCxt.state.post.post_images)) {
      if (bnCxt.state.post.post_images.some((v) => v == img)) {
        let xhr = new XMLHttpRequest();
        xhr.open("POST", `/api/seller/my-product/buy-now/delete/image`, true);
        xhr.onload = () => {
          if (xhr.status === 200 && xhr.readyState === 4) {
            if (xhr.responseText) {
              if (JSON.parse(xhr.responseText)) {
                let res = JSON.parse(xhr.responseText).success;
                console.log(res, "the image has been deleted");
              }
            }
          }
        };
        xhr.setRequestHeader("x-auth-user", authCxt.token);
        let dataform = new FormData();
        dataform.append("img", img);
        dataform.append("postid", bnCxt.state.post._id);
        xhr.send(dataform);
      }
    }
  }

  //  ADD VIDEO TO PRODUCT CREATION
  function addVideo() {
    document.getElementById("choose-video").click();
  }

  // DISPLAY VIDEOS ON PRODUCT VIDEO PREVIEW ZONE
  function videoDisplayPreview(event) {
    let file = event.target.files[0];
    let blobURL = URL.createObjectURL(file);
    bnCxt.setValue({ post_video: blobURL });
    bnCxt.setValue({ productVideo: file });
  }

  const chooseImage = () => {
    let d = document.querySelector("#choose-image");
    d.click();
  };

  // GET ALL AVAILABLE REGION FROM THE SERVER
  const getRegions = async () => {
    try {
      let res = await axios.get("/api/seller/product-regions");
      if (res.data) {
        if (res.data.success) {
          setRegions(res.data.success);
        }
      }
    } catch (error) {
      setRegions([]);
    }
  };
  // GET ALL AVAILABLE COUNTRIES FROM THE SERVER
  const getCountries = async () => {
    try {
      let res = await axios.get("/api/seller/product-countries");
      if (res.data) {
        if (res.data.success) {
          setCountries(res.data.success);
        }
      }
    } catch (error) {
      setCountries([]);
    }
  };

  // GET ALL PORDUCT CATEGORIES FROM SERVER
  const productCategories = async () => {
    try {
      let res = await axios.get("/api/seller/product-categories");
      if (res.data) {
        if (res.data.success) {
          setCategories(res.data.success);
        }
      }
    } catch (error) {
      setCategories([]);
    }
  };

  // GET ALL PRODUCT SUBCATEGORIES FROM SERVER

  const productSubCategories = async (catname) => {
    const fromCat = categories.filter((cat, key) => cat.name === catname);
    // setSubCategories(fromCat[0].subCategories);
    if (Array.isArray(fromCat)) {
      if (fromCat.length > 0) {
        console.log(fromCat, " the whole categories ");
        console.log("this sub category should be setted now ");
        if (fromCat.length > 0) {
          bnCxt.setValue({ post_subcategory: fromCat[0].subCategories[0] });
        }
      }
    }
  };

  const validateForm = () => {
    if (bnCxt.state.post_category) {
      if (bnCxt.state.post_images.length) {
        if (bnCxt.state.post_region) {
          if (bnCxt.state.post_country) {
            bnCxt.setValue({
              section: [bnCxt.state.section[1], bnCxt.state.section[0]],
            });
            bnCxt.setValue({ tab: [bnCxt.state.tab[1], bnCxt.state.tab[0]] });
            bnCxt.setValue({
              showSection: [
                bnCxt.state.showSection[1],
                bnCxt.state.showSection[0],
              ],
            });
          } else {
            errorBorder(
              "#country",
              "Please select the country you are posting to"
            );
          }
        } else {
          errorBorder("#region", "Please select a region for your product");
        }
      } else {
        errorBorder(".choose-image", "Please insert one or more images ");
      }
    } else {
      errorBorder("#product-category", "enter product category ");
    }
  };

  return bnCxt.state.isSubmitted ? (
    <ProductSubmitted />
  ) : (
    <div class="col-md-9">
      <div class="dashboard-main-content">
        <div class="create-post-regular">
          <h6 class="header">Regular Post - Update</h6>
          <p class="text">
            Regular products posts are products the seller controls and takes
            charge of the delivery with the highest professional standards.
          </p>
          <ul id="section-tabs">
            <li class={bnCxt.state.tab[0]}>Step 2: About Product</li>
            <li class={bnCxt.state.tab[1]}>Step 3: Product Details</li>
          </ul>
          <div class="note">
            Note that all Buy now products must be fulfilled and customer
            satisfied before payment is released to a seller.
          </div>
          <form id="create-post" action="#">
            <div id="fieldsets">
              <fieldset
                class={bnCxt.state.section[0]}
                style={{ display: bnCxt.state.showSection[0] }}
              >
                {/* <!-- CHOOSE CATEGORY --> */}
                <div class="form-row">
                  <div class="col-md-6">
                    <div class="create-floating-label">
                      <select
                        name=""
                        id="product-category"
                        onChange={(el) => {
                          bnCxt.setValue({ category: el.target.value });
                          productSubCategories(el.target.value);
                        }}
                        onSelect={(el) => {
                          console.log("new selection has happened ");
                          bnCxt.setValue({ category: el.target.value });
                          productSubCategories(el.target.value);
                        }}
                      >
                        <option value=""> Select Category</option>
                        {categories.map((cat, key) => (
                          <option
                            key={key}
                            value={cat.name}
                            selected={
                              cat.name == bnCxt.state.post_category && true
                            }
                          >
                            {cat.name}{" "}
                          </option>
                        ))}
                      </select>
                      <label for="">Category*</label>
                      <i class="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div class="col-md-6">
                    <div class="create-floating-label">
                      <select
                        name=""
                        id="product-subcategory"
                        onChange={(el) =>
                          bnCxt.setValue({ category: el.target.value })
                        }
                      >
                        <option
                          selected={true}
                          value={bnCxt.state.post_subcategory}
                        >
                          {bnCxt.state.post_subcategory}
                        </option>
                        {subCategories.map((subcat, key) => (
                          <option key={key} value={subcat}>
                            {subcat}
                          </option>
                        ))}
                      </select>
                      <label for="">Subcategory*</label>
                      <i class="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                </div>
                {/* <!-- ADD PHOTOS --> */}
                <div className="add-photos">
                  <h6>Add Photos (Maximum size for each photo is 3mb)</h6>
                  <p className="note">
                    Add at least 1 photo and a maximum of 10 photos (Supported
                    formats: *.jpg, *.gif and *.png)
                  </p>
                  <p>
                    First Picture is the title picture. You can change the order
                    of photos. Just grab your photos and drag.
                  </p>
                  <div className="image-upload">
                    <div className="choose-image">
                      <Link className="choose" onClick={() => chooseImage()}>
                        <i className="fa fa-plus plus-icon"></i>
                        <i className="fa fa-image image-icon"></i>
                      </Link>
                      <input
                        type="file"
                        id="choose-image"
                        multiple
                        accept="image/*"
                        onInput={(event) => readImage(event)}
                      />
                      <div className="preview-images-zone">
                        {Array.isArray(bnCxt.state.post_images) &&
                          bnCxt.state.post_images.map((img, key) => (
                            <div
                              key={key}
                              className={"preview-image preview-show-" + key}
                            >
                              <div
                                className="image-cancel"
                                onClick={() =>
                                  cancelImage(
                                    img,
                                    key,
                                    "preview-image preview-show-" + key
                                  )
                                }
                                data-no={key}
                              >
                                x
                              </div>
                              <div className="image-zone">
                                <img id="pro-img-num " src={img} />
                              </div>
                            </div>
                          ))}
                      </div>
                    </div>
                  </div>
                </div>
                {/* <!-- ADD VIDEO --> */}
                <div className="add-video">
                  <h6>Add Video (Maximum size is 5mb and 60 seconds)</h6>
                  <p>Only one optional video is allowed</p>
                  <div className="video-upload mb-5">
                    <div className="choose-video">
                      <a href="#!" onClick={addVideo} className="choose">
                        <i className="fa fa-plus plus-icon"></i>
                        <i className="fa fa-video image-icon"></i>
                      </a>
                      <input
                        type="file"
                        id="choose-video"
                        accept="video/*"
                        onInput={(event) => videoDisplayPreview(event)}
                      />

                      <div className="preview-zone">
                        <div className="video-container">
                          {bnCxt.state.post_video && (
                            <video
                              style={{ display: "block" }}
                              controls
                              className="lightbox-vid"
                              src={bnCxt.state.post_video}
                              type="video/mp4"
                            >
                              Your browser does not support HTML5 video.
                            </video>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                {/* <!-- Region and country --> */}
                <div className="form-row">
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="region"
                        onChange={(e) =>
                          bnCxt.setValue({ region: e.target.value })
                        }
                      >
                        {regions &&
                          regions.map((val, key) => (
                            <option
                              value={val.name}
                              label={val.name}
                              key={key}
                              onLoad={() => {
                                // val.name === bnCxt.state.post_region
                              }}
                              selected={
                                val.name === bnCxt.state.post_region && true
                              }
                            />
                          ))}
                      </select>
                      <label htmlFor="">Region*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="country"
                        onChange={(e) =>
                          bnCxt.setValue({ country: e.target.value })
                        }
                      >
                        {countries &&
                          countries.map((val, key) => (
                            <option
                              value={val.name}
                              label={val.name}
                              key={key}
                              selected={
                                val.name === bnCxt.state.post_country && true
                              }
                            />
                          ))}
                      </select>
                      <label htmlFor="">Country*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                </div>
              </fieldset>
              <EditRegularPostNext />
              <a
                class="post-btn"
                style={{ display: bnCxt.state.showSection[0] }}
                id="next"
                onClick={(ev) => {
                  ev.preventDefault();
                  validateForm();
                }}
              >
                Next
              </a>
              <input type="submit" class="post-btn" value="Update Post" />
              <div class="info">
                By clicking on submit post, you accept the Terms of use, confirm
                that you will abide by the safety tips, and declare that posting
                does not include any prohibited items.
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default EditRegularPost;
