import React, { useState, useContext, useEffect } from "react";
import { AuthContext } from "../../context/authContext/AuthState";
import { PromoContext } from "./PromoState";

// promoTypes
// posts
// durations
// selectedPost
// selectedType
// totalBudget
// duration

const CreatePromotion = () => {
  const authCxt = useContext(AuthContext);
  const promoCxt = useContext(PromoContext);

  const [] = useState();

  useEffect(() => {
    authCxt.loadUser();
    if (authCxt.token) {
      getPosts(authCxt.token);
      getPromoTypes(authCxt.token);
      getDurations(authCxt.token);
    }
  }, [authCxt.token]);

  const getPosts = (token) => {
    if (!authCxt.token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/posts?author=${authCxt.user._id}`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, " the entire response text ");
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            console.log(res.success, "the posts arrays ")
            if (Array.isArray(res.success.posts)) {
              promoCxt.setValue({ posts: [...res.success.posts] });
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };


  const getDurations = (token) => {
    if (!authCxt.token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/seller/campaign-durations`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, " the entire response text ");
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            promoCxt.setValue({ durations: res.success });
            promoCxt.setValue({ selectedDuration: res.success[0]._id });
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };
  //  app.use('/api/seller/promotion-types
  // app.use('/api/seller/campaign-durations',

  const getPromoTypes = (token) => {
    if (!authCxt.token) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/seller/promotion-types`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, " the entire response text ");
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (JSON.parse(xhr.responseText)) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            // return console.log(res.success, " the promoted success story ")
            promoCxt.setValue({ promotTypes: res.success });
            promoCxt.setValue({promoType:res.success[0]._id})
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", token);
    xhr.send();
  };
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="row">
          <div className="col-md-6 select-post-for-promotion">
            <h6 className="header">Select your post for promotions</h6>
            <div className="search-form">
              <div className="row">
                <div className="col-md-9 offset-md-3">
                  <form action="">
                    <div className="form-row align-items-baseline">
                      <div className="col-md-12">
                        <div className="input-container">
                          <i className="fa fa-search icon"></i>
                          <input
                            type="search"
                            name=""
                            id=""
                            className="input-field"
                          />
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>

            {promoCxt.state.posts &&
              promoCxt.state.posts.length > 0 &&
              promoCxt.state.posts.map((p, key) => (
                <div
                  className="single-post"
                  key={key}
                  id={`item${key}`}
                  draggable={true}
                  data-id={p._id}
                  onDragStart={(ev) => {
                    ev.dataTransfer.setData("text", ev.target.id);
                  }}
                >
                  <div className="row">
                    <div className="col-md-4">
                      {Array.isArray(p.post_images) && (
                        <img
                          className="img-fluid"
                          src={p.post_images[0]}
                          alt=""
                        />
                      )}
                    </div>
                    <div className="col-md-8">
                      <div className="name">
                        <h5>{p.post_title}</h5>
                        <div className="price-cont d-flex justify-content-between">
                          <h6>N {p.post_price}</h6>
                          <span className="buy-now">
                            <i className="fa fa-shipping-fast"></i>{" "}
                            {p.post_type}
                          </span>
                        </div>
                        <div className="category-cont d-flex justify-content-between">
                          <p>{p.post_category}</p>
                          <p>Created on {p.post_date}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            <div className="mt-3 text-center">
              <ul className="products-pagination align-items-center">
                <li>
                  <a href="#">
                    <i className="fa fa-step-backward"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play rotate-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play play-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-step-forward"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>

          <div className="col-md-6">
            <div className="make-your-product">
              <h6 className="header">Make your products speak out better!</h6>
              <p className="get">
                Get up to 10x more responses on your ads by applying promotions
              </p>
              <div
                id="dropzone"
                onDrop={(ev) => {
                  ev.preventDefault();
                  var data = ev.dataTransfer.getData("text");
                  let id = document.getElementById(data)
                  if (id) {                    
                    let eldiv = ev.target.appendChild(id);
                    promoCxt.setValue({selectedPost:eldiv.dataset.id})
                    console.log(eldiv.dataset.id, " the entire data set id here ")
                  }

                }}
                onDragOver={(ev) => {
                  ev.preventDefault();
                }}
              ></div>

              {promoCxt.state.promotTypes &&
                promoCxt.state.promotTypes.map((p, key) => (
                  <div className="promoted-ads" key={key} >
                    <div className="media">
                      <img className="img-fluid" src={p.sample_image} alt="" />
                      <div className="media-body">
                        <h6>{p.name}</h6>
                        <p>{p.description}</p>
                        <div className="checkbox-field text-right">
                          <input
                            type="checkbox"
                            name="featured-ad"
                            id="featured-ad"
                            className="checkbox"
                            defaultChecked={key === 0 ? true : false}
                            onClick={()=>{
                              promoCxt.setValue({selectedType:p._id })
                            }}
                            required
                          />
                          <label
                            htmlFor="featured-ad"
                            className="label"
                          ></label>
                        </div>
                      </div>
                    </div>
                    <div className="payment-prices">
                      {promoCxt.state.durations &&
                        promoCxt.state.durations.map((d, key) => (
                          <div className="single-payment-price" key={key}>
                            <span>
                              N {parseInt(p.price) * parseInt(d.days)}
                            </span>
                            <div className="price">
                              <span>{d.days} days</span>
                              <input
                                type="radio"
                                name="p-days"
                                id={`p-${d.days}days`}
                                defaultChecked={key === 0 ? true : false}
                                onClick={()=>{
                                  promoCxt.setValue({selectedDuration:d.days})
                                  promoCxt.setValue({totalBudget:parseInt(d.days) * parseInt(p.price)})
                                }}
                              />
                              <label htmlFor={`p-${d.days}days`}></label>
                            </div>
                          </div>
                        ))}
                    </div>
                  </div>
                ))}

              <div className="ads-total">
                <h6>Ads Total</h6>
                <div className="price">
                  <p>14 days promotion</p>
                  <p>N 25,000</p>
                </div>
                <div className="price">
                  <p>7 days hero slider</p>
                  <p>N 14,000</p>
                </div>
              </div>
              <div className="use-remaining-balance">
                <label className="switch">
                  <input type="checkbox" />
                  <span className="slider round"></span>
                </label>
                <h6>
                              <span className="mr-2">{authCxt.user.wallet}</span>
                  Use remaining balance
                  <span className="ml-2">
                    <i className="fa fa-exclamation-circle"></i>
                  </span>
                </h6>
              </div>
              <div className="subscribe-box">
                <button
                  className="subscribe-button"
                  // data-toggle="modal"
                  // data-target="#post-promotion-modal"
                  onClick={()=>{
                    promoCxt.setPromotion(authCxt.token)
                  }}
                >
                  Subscribe 
                  {/* <span className="old-price">N 17,000.00</span>{" "}
                  <span className="pl-3">0.00</span> */}
                </button>
                {/* <span className="discount">N17,000.00 OFF!</span> */}
              </div>
              <div className="post-promotion-modal">
                <div
                  className="modal fade"
                  id="post-promotion-modal"
                  tabIndex="-1"
                  role="dialog"
                  aria-hidden="true"
                >
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                  <div
                    className="modal-dialog modal-lg modal-dialog-centered"
                    role="document"
                  >
                    <div className="modal-content">
                      <div className="modal-body">
                        <h5>Post Promotion Subscription</h5>
                        <p className="amount">6,999 NGN</p>
                      </div>
                      <div className="payment-method">
                        <ul className="nav nav-tabs" id="myTab" role="tablist">
                          <li className="nav-item">
                            <a
                              className="nav-link active"
                              id="home-tab"
                              data-toggle="tab"
                              href="#cash-payment"
                              role="tab"
                              aria-controls="cash-payment"
                              aria-selected="true"
                            >
                              <span>
                                <i className="fa fa-money-bill-alt"></i>
                              </span>
                              Cash Payment
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              className="nav-link"
                              id="profile-tab"
                              data-toggle="tab"
                              href="#online-payment"
                              role="tab"
                              aria-controls="online-payment"
                              aria-selected="false"
                            >
                              <span>
                                <i className="fa fa-qrcode"></i>
                              </span>
                              Online Payment
                            </a>
                          </li>
                        </ul>
                        <div className="tab-content" id="myTabContent">
                          <div
                            className="tab-pane fade show active"
                            id="cash-payment"
                            role="tabpanel"
                            aria-labelledby="cash-payment-tab"
                          >
                            <div className="single-div">
                              <h6>Payment with cash via any bank branch</h6>
                              <p>
                                Save these details and pay at the nearest bank
                                branch
                              </p>
                            </div>
                            <div className="single-div">
                              <h6>
                                1. Go to the bank teller and provide the
                                following information:
                              </h6>
                              <p>Platform: eBillsPay</p>
                              <p>Merchant: TINKOKO</p>
                              <p>Invoice: 86507822520</p>
                              <p>Amount: 6,999.00 NGN</p>
                            </div>
                            <div className="single-div">
                              <h6>2. Payment details</h6>
                              <p>Amount: 6,999.00 NGN</p>
                              <p>Fee: 100.00 NGN</p>
                              <p>Total: 7,099 NGN</p>
                            </div>
                            <div className="single-div">
                              <span>
                                All payments are debited automatically
                              </span>
                              <span>
                                Please be sure that the bank teller enters
                                correct invoice number
                              </span>
                              <span>Keep your payment receipt</span>
                            </div>
                            <p className="encrypted">
                              Your transaction details are encrypted and are
                              never shared with third parties
                            </p>
                          </div>
                          <div
                            className="tab-pane fade"
                            id="online-payment"
                            role="tabpanel"
                            aria-labelledby="online-payment-tab"
                          >
                            Online payment details goes in here
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreatePromotion;
