import React from "react";
import PrivateRoute from "../../routing/PrivateRoute";
import NotFound from "../../common/NotFound";
import { Route, Switch } from "react-router-dom";
import ApprovedProducts from "./view-products/ApprovedProducts";
import AllProduct from "./view-products/AllProduct";
import ReviewingProducts from "./view-products/ReviewingProducts";
import DeclinedProducts from "./view-products/DeclinedProducts";
import CreateProduct from "./create-product/CreateProduct";
import CreateRegularProduct from "./create-product/CreateRegularProduct";
import CreateBuyNowProduct from "./create-product/CreateBuyNowProduct";
import CreateJoblistingProduct from "./create-product/CreateJoblistingProduct";
import EditBuynowPost from "./edit-product/EditBuynowPost";
import EditRegularPost from "./edit-product/EditRegularPost";
import EditJoblistingPost from "./edit-product/EditJoblistingPost";
import EditState from "./edit-product/EditState";
import Sidebar from "../../common/Sidebar"
const MyProducts = (props) => {
  return (
    <EditState>
      <Sidebar {...props} />
      <Switch>
        <PrivateRoute exact path="/seller/my-products" component={AllProduct} />
        <PrivateRoute
          component={CreateProduct}
          exact
          path="/seller/my-products/create-product"
        />
        <PrivateRoute
          component={CreateBuyNowProduct}
          exact
          path="/seller/my-products/create-product/buynow"
        />
        <PrivateRoute
          exact
          path="/seller/my-products/create-product/regular"
          component={CreateRegularProduct}
        />
        <PrivateRoute
          component={CreateJoblistingProduct}
          exact
          path="/seller/my-products/create-product/joblisting"
        />
        <PrivateRoute
          component={EditRegularPost}
          exact
          path="/seller/my-products/edit/regular/:postid"
        />
        <PrivateRoute
          component={EditBuynowPost}
          exact
          path="/seller/my-products/edit/buynow/:postid"
        />
        <PrivateRoute
          component={EditJoblistingPost}
          exact
          path="/seller/my-products/edit/jobs/:postid"
        />
        <PrivateRoute
          exact
          path="/seller/my-products/approved"
          component={ApprovedProducts}
        />
        <PrivateRoute
          exact
          path="/seller/my-products/reviewing"
          component={ReviewingProducts}
        />
        <PrivateRoute
          exact
          path="/seller/my-products/declined"
          component={DeclinedProducts}
        />
        <PrivateRoute exact path="/seller/my-products/*" component={NotFound} />
      </Switch>
    </EditState>
  );
};

export default MyProducts;
