import React, { useContext, useEffect } from "react";
import Header from "../../common/Header";
import Footer from "../../common/Footer";
import userProfileImg from "../../../images/user-profile.PNG";
import { NotificationContext } from "./NotificationState";
import { AuthContext } from "../../context/authContext/AuthState";
import { useState } from "react";

const Notification = () => {
  const authCxt = useContext(AuthContext);
  const noteCxt = useContext(NotificationContext);
  const [notes, setNotes] = useState([]);
  useEffect(() => {
    authCxt.loadUser();
    if (authCxt.token) {
      getNotifications();
    }
  }, [authCxt.token]);

  const getNotifications = () => {
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/notifications`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, "all notifications gotten successfully");
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            let res = JSON.parse(xhr.responseText).success;
            if (res) {
              console.log(res, " the entire notification response text ");
              noteCxt.setValue({ notifications: [res] });
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };
  const markNotificationAsRead = (id) => {
    console.log(id, "id is now")
    if (!id) return;
    let xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/notifications/update-status/${id}`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, "all notifications gotten successfully");
      if (xhr.status === 200 && xhr.readyState === 4) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            let res = JSON.parse(xhr.responseText).success;
            console.log(res, "the response text ");
            if (Array.isArray(JSON.parse(xhr.responseText))) {
              if (res) {
                console.log(res, " the entire notification response text ");
                noteCxt.setValue({ notifications: [...res] });
              }
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };

  return (
    <>
      {/* <section className="default-container"> */}
      <div className="container">
        <div className="row">
          <div className="col-md-10 offset-md-1 notification-container">
            <div className="white-background">
              <div className="header">
                <h4>Notifications</h4>
              </div>
              <div className="mark-all-as-read">
                <form action="">
                  <div className="checkbox-field">
                    <input type="checkbox" id="mark-all" className="checkbox" />
                    <label for="mark-all" className="label"></label>
                  </div>
                  <button type="submit">Mark all as read</button>
                </form>
              </div>
              <div className="notifications-box">
                {/* Notifications List starts here  */}
                {noteCxt.state.notifications &&
                  noteCxt.state.notifications.map((data, key) => (
                    <div className="media" key={key}>
                      <div className="checkbox-field">
                        <input
                          type="checkbox"
                          id="notif5"
                          className="checkbox"
                          onChange={(e) => {
                            console.log(e, "the mark change event ")
                            if (e.target.checked) {
                              markNotificationAsRead(data._id)
                            }
                          }}
                        />
                        <label for="notif5" className="label"></label>
                      </div>
                      <img className="img-fluid" src={userProfileImg} alt="" />
                      <div className="media-body">
                        <h6>{data.message}</h6>
                        <p>{data.dateCreated}</p>
                      </div>
                    </div>
                  ))}
              </div>

              {/* Notifications ends here  */}
            </div>
          </div>
        </div>
      </div>
      {/* </section> */}
    </>
  );
};

export default Notification;
