import React, { useState, useEffect } from "react";
import {
  Route,
  Link,
  Switch,
  Redirect,
} from "react-router-dom";
import Header from "../../common/Header";
import Footer from "../../common/Footer";
import Sidebar from "../../common/Sidebar";
import MyAdvertisements from "./MyAdvertisements";
import CreateAdvertisement from "./CreateAdvertisement";
import AdsState, { AdsContext } from "./AdsState";
import ProceedToPayment from "./ProceedToPayment";
import { useContext } from "react";
// !adsCxt.state.adTotal ? <Redirect to="/seller/my-advertisements/new/" /> :
const Advertisements = (props) => {
  return (
    <AdsState>
      <Sidebar {...props} />
        <Switch>
          <Route
            exact
            component={MyAdvertisements}
            path="/seller/my-advertisements"
          />
          <Route
            exact
            component={CreateAdvertisement}
            path="/seller/my-advertisements/new"
          />
          <Route
            exact
            component={ProceedToPayment}
            path="/seller/my-advertisements/new/:newad"
          />
        </Switch>
    </AdsState>
  );
};

export default Advertisements;
