import React, {
  DOMAttributes,
  useState,
  useEffect,
  createContext,
  useReducer,
} from "react";
import { useContext } from "react";
import { AuthContext } from "../../../context/authContext/AuthState";
import { Link } from "react-router-dom";
import { AlertContext } from "../../../context/alertContext/AlertState";
import RegularPostReducer from "./RegularPostReducer";
import ProductSubmitted from "./ProductSubmitted";
import RegularProductAbout from "./RegularProductAbout";
import RegularProductDetail from "./RegularProductDetail";

export const RegularPostContext = createContext();
const { Provider } = RegularPostContext;

const CreateRegularProduct = () => {
  const initialState = {
    phone: null,
    businessName: null,
    description: null,
    title: null,
    price: null,
    negotiable: false,
    amountDistribution: null,
    region: null,
    country: null,
    category: null,
    subCategory: null,
    productImages: [],
    productVideo: {},
    isSubmitted: false,
    nextSection: false,
    tab: ["current active", ""],
  };
  const [state, dispatch] = useReducer(RegularPostReducer, initialState);

  useEffect(() => {
    authCxt.loadUser();
  }, []);

  const authCxt = useContext(AuthContext);
  const alertCxt = useContext(AlertContext);
  const [submitted, setSubmitted] = useState(false);

  // SET PRODUCT STATE
  const setPstate = (prop = {}) => {
    dispatch({ type: "SET-STATE", payload: prop });
  };
  return (
    <Provider value={{ pState: state, setPstate }}>
      <>
        {state.isSubmitted ? (
          <ProductSubmitted />
        ) : (
          <div className="col-md-9">
            <div className="dashboard-main-content">
              <div className="create-post-regular">
                <h6 className="header">Create a Post - Regular</h6>
                <p className="text">
                  Regular products posts are products the seller controls and
                  takes charge of the delivery with the highest professional
                  standards
                </p>
                <ul id="section-tabs">
                  <li className={`${state.tab[0]}`}>Step 2: About Product</li>
                  <li className={`${state.tab[1]}`}>Step 3: Product Details</li>
                </ul>
                <form id="create-post" action="#">
                  <div id="fieldsets">
                    {!state.nextSection ? (
                      <RegularProductAbout />
                    ) : (
                      <RegularProductDetail />
                    )}
                  </div>
                </form>
              </div>
            </div>
          </div>
        )}
      </>
    </Provider>
  );
};

export default CreateRegularProduct;
