import React from 'react'

const DeclinedProducts = () => {
    return (
        <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="content-header">
            <h4>My Products</h4>
            <div className="statistics">
              <span className="active">
                <a href="seller-products-active.html"
                  ><i className="fa fa-check-circle"></i> Active (4)</a
                >
              </span>
              <span className="reviewing">
                <a href="seller-products-reviewing.html"
                  ><i className="fa fa-spinner"></i> Reviewing (1)</a
                >
              </span>
              <span className="declined underline">
                <a href="seller-products-declined.html"
                  ><i className="fa fa-times-circle"></i> Declined (1)</a
                >
              </span>
              <span className="all">
                <a href="seller-products-all.html"
                  ><i className="fa fa-check-square"></i> All (6)</a
                >
              </span>
            </div>
          </div>

          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-borderless">
                <thead>
                  <tr>
                    <th scope="col">Product ID</th>
                    <th scope="col">Name</th>
                    <th scope="col">Category</th>
                    <th scope="col">Post Type</th>
                    <th scope="col" className="rejected-reason">
                      Rejected Reasons
                    </th>
                    <th scope="col">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th>PID1372393</th>
                    <td>Pig Farm 50 acres...</td>
                    <td>Animal Farming</td>
                    <td className="buy-now">Buy Now</td>
                    <td>Such products are illegal.</td>
                    <td><a href="#" className="delete-button">Delete</a></td>
                  </tr>
                  <tr>
                    <th>PID1372393</th>
                    <td>Maize Flour 25kg</td>
                    <td>Cereal Crops</td>
                    <td className="regular">Regular</td>
                    <td>
                      You have 1 mistake to edit in your advert. Please,
                      check and correct: Recommended sub-category is Maize.
                    </td>
                    <td><a href="#" className="delete-button">Delete</a></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

          <div
            className="pagination d-flex justify-content-center align-items-center align-content-center flex-md-row flex-sm-column flex-wrap"
          >
            <div>
              <label for="page" className="grey-text">Go to Page:</label>
              <input
                type="number"
                name="page"
                id="page"
                placeholder="Page"
              />
            </div>
            <ul>
              <li>
                <a href="#"><i className="fa fa-step-backward"></i></a>
              </li>
              <li>
                <a href="#"><i className="fa fa-play rotate-icon"></i></a>
              </li>
              <li>
                <a href="#"><i className="fa fa-play play-icon"></i></a>
              </li>
              <li>
                <a href="#"><i className="fa fa-step-forward"></i></a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    )
}

export default DeclinedProducts
