import React from 'react'
import { createContext, useReducer, useEffect } from 'react'
import SettingsReducer from './SettingsReducer'
import {AuthContext} from '../../context/authContext/AuthState'
import { useContext } from 'react'

export const SettingsContext = createContext()
const {Provider} = SettingsContext

const SettingsState = ({children}) => {
const authCxt = useContext(AuthContext)
    useEffect(() => {
        console.log(authCxt.token, ' the token now ')
    }, [])
    const initialState = {
        changesApplied: false
    }

const [state, dispatch] = useReducer(SettingsReducer, initialState)

// MODIFY PROFILE

const modifyProfile = (data) =>{
    let dataform =  new FormData()
    dataform.append('fname', data.fname)
    dataform.append('lname', data.lname)
    dataform.append('baddress', data.baddress)
    dataform.append('bname', data.bname)
    dataform.append('city', data.city)
    dataform.append('state', data.state)
    dataform.append('country', data.country)
    console.log(data,'data from the settings state')
        let xhr = new XMLHttpRequest()
        xhr.open('POST', '/api/seller/settings/profile', true)
        xhr.onload = () =>{
          if (xhr.readyState===4 && xhr.status == 200) {
            console.log(xhr.responseText, ' all the my profile response text ')
          }
        }
    
        xhr.setRequestHeader('x-auth-user', authCxt.token)
        xhr.send(dataform)
      }
// MODIFY TWO FACTOR AUTHENTICATION

const modifySecurity = (data={}) =>{
    let propkey = Object.keys(data)
    let dataform =  new FormData()
    dataform.append([propkey[0]], data[propkey[0]])
    
        let xhr = new XMLHttpRequest()
        xhr.open('POST', '/api/seller/settings/enable-2fa', true)
        xhr.onload = () =>{
          if (xhr.readyState===4 && xhr.status == 200) {
            console.log(xhr.responseText, ' all the my security ')
          }
        }
    
        xhr.setRequestHeader('x-auth-user', authCxt.token)
        xhr.send(dataform)
      }


// MODIFY NOTIFICATION

const modifyNotification = (data={}) =>{
    let propkey = Object.keys(data)
    let dataform =  new FormData()
    dataform.append([propkey[0]], data[propkey[0]])
    
        let xhr = new XMLHttpRequest()
        xhr.open('POST', '/api/seller/settings/notifications', true)
        xhr.onload = () =>{
          if (xhr.readyState===4 && xhr.status == 200) {
            console.log(xhr.responseText, ' all the my security ')
          }
        }
    
        xhr.setRequestHeader('x-auth-user', authCxt.token)
        xhr.send(dataform)
      }

// MODIFY SOCIAL NETWORK
const modifySocialNetwork = async (fn, data= {}) => {
    console.log(data, ' this is al the data ')
    let  dataform = new FormData()
    dataform.append('facebook', data.facebook) 
    dataform.append('instagram', data.instagram) 
    dataform.append('twitter', data.twitter) 
    dataform.append('linkedin', data.linkedin) 
    let xhr = new XMLHttpRequest()
    xhr.open('POST', '/api/seller/settings/social-network', true)
    xhr.onload = () =>{
        if(xhr.readyState == 4 && xhr.status == 200 ){
            console.log(xhr.responseText, ' social media modification responstext')
            fn()
        }
    }
    xhr.setRequestHeader('x-auth-user',authCxt.token)
    xhr.send(dataform)
}
    return (
        <Provider value={{modifySocialNetwork,modifyProfile,modifySecurity, newState: "Just a new state"}}>
            {children}
        </Provider>
    )
}

export default SettingsState
