import React, {useState, useEffect, useContext} from 'react'
import axios from "axios";
import { AuthContext } from "../../../context/authContext/AuthState";
import { errorBorder } from "../../../utils/errorBorder";
import { EditContext } from './EditState';

const EditRegularPostNext = () => {

  const authCxt = useContext(AuthContext);
  const bnCxt = useContext(EditContext);
  const [tlengthCounter, setTlengthCounter] = useState(0);
  const [dlengthCounter, setDlengthCounter] = useState(0);
  const [amountDistributions, setAmountDistributions] = useState([]);
  const [dropOffLocations, setDropOffLocations] = useState([]);

  useEffect(() => {
    getAmountDistribution();
    getDropOffLocations();
    authCxt.user && bnCxt.setValue({ business_name: authCxt.user.businessName });
    authCxt.user && bnCxt.setValue({ post_author_phone: authCxt.user.phone });
  }, [authCxt.token]);

  useEffect(() => {
    if (bnCxt.state.post._id && authCxt.token) {
      setTlengthCounter(bnCxt.state.post_title.length)
      setDlengthCounter(bnCxt.state.post_description.length)
      bnCxt.setValue({business_name:authCxt.user.businessName})
    }
  }, [bnCxt.state.post._id, authCxt.token])


  const validateForm = () => {
    if (bnCxt.state.post_title) {
      if (bnCxt.state.post_description) {
        if (bnCxt.state.post_price) {
          if (bnCxt.state.post_distribution) {
            if (bnCxt.state.business_name) {
              if (bnCxt.state.post_author_phone) {
                submitPost();
              } else {
                errorBorder("#phone", "Enter phone number");
                console.log("please enter your registered Phone number");
              }
            } else {
              console.log(
                "You need to provide and already registered business name "
              );
            }
          } else {
            console.log("Enter amount in distribution");
            errorBorder("#amount-distribution", "Enter amount in distribution");
          }
        } else {
          console.log("Enter product price ");
          errorBorder("#fixed-price-value", "Enter product price");
        }
      } else {
        errorBorder("#description", "Enter product  description");
        console.log("enter product description here ");
      }
    } else {
      errorBorder("#title", "enter enter product title ");
      // errline.appendChild(para)
    }
  };
  // SUBMIT POST TO BACKEND SERVER
  const submitPost = () => {
    // region,
    // country,
    // category,
    // subCategory,
    // productImages,
    // amountDistribution,
    // price,
    // negotiable,
    // businessName,
    // phone,
    // title,
    // description

    // pcat
    // pregion
    // pcountry
    // ptitle
    // pdesc
    // pprice
    // pamntdist
    // psphone
    // psname
    // pscompany
    const dataForm = new FormData();
    dataForm.append("pcat", bnCxt.state.post_category);
    dataForm.append("psubcat", bnCxt.state.post_subcategory);
    dataForm.append("pregion", bnCxt.state.post_region);
    dataForm.append("pcountry", bnCxt.state.post_country);
    dataForm.append("ptitle", bnCxt.state.post_title);
    dataForm.append("pdesc", bnCxt.state.post_description);
    dataForm.append("pprice", bnCxt.state.post_price);
    dataForm.append("pamntdist", bnCxt.state.post_distribution);
    dataForm.append("psphone", bnCxt.state.post_author_phone);
    dataForm.append("psname", bnCxt.state.business_name);
    dataForm.append("pscompany", bnCxt.state.business_name);
    dataForm.append("pdroploc", bnCxt.state.product_dropoff_location);
    for (let i = 0; i < bnCxt.state.productImages.length; i++) {
      dataForm.append("pimg", bnCxt.state.productImages[i].file);
    }
    if (bnCxt.state.productVideo) {
      dataForm.append("pvid", bnCxt.state.productVideo);
    }

    let xhr = new XMLHttpRequest();
    xhr.open("POST", `/api/seller/my-product/regular/edit/${bnCxt.state._id}`, true);
    xhr.onload = () => {
      console.log(xhr.responseText, " the entire response text ")
      if (xhr.status == 200 && xhr.readyState == 4) {
        if (xhr.responseText) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            bnCxt.setValue({ isSubmitted: true });
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  // GET ALL AVAILABLE DISTRIBUTION  FROM THE SERVER
  const getAmountDistribution = async () => {
    try {
      let res = await axios.get("/api/seller/amount-distribution");
      if (res.data) {
        if (res.data.success) {
          setAmountDistributions(res.data.success);
        }
      }
    } catch (error) {
      setAmountDistributions([]);
    }
  };

  // GET DROP OFF LOCATIONS
  const getDropOffLocations = async () => {
    try {
      let res = await axios.get("/api/seller/dropoff-locations");
      if (res.data) {
        if (res.data.success) {
          setDropOffLocations(res.data.success);
        }
      }
    } catch (error) {
      setDropOffLocations([]);
    }
  };

  //  SET FIX PRICE DURING PRODUCT CREATION
  function fixedPriceValue(val) {
    let pricevalue = document.getElementById("fixed-price-value");
    bnCxt.setValue({ post_price: pricevalue.value });
    // setPrice(pricevalue.value);
  }

  return (
    <>
      <fieldset className="active current" style={{ paddingBottom: "20px ", display:bnCxt.state.showSection[1] }}>
        {/* <!-- TITLE FIELD --> */}
        <div className="title-field">
          <div className="d-flex justify-content-end">
            <span className="short">Keep it short</span>
            <span className="count">
              <span id="title-count">{tlengthCounter}</span> / 60
            </span>
          </div>
          <input
            type="text"
            name="title"
            id="title"
            defaultValue={bnCxt.state.post_title}
            onChange={(e) => {
              setTlengthCounter(e.target.value.length);
              bnCxt.setValue({ post_title: e.target.value });
              // setTitle(e.target.value);
            }}
            placeholder="Title*"
            maxLength="60"
          />
        </div>
        {/* <!-- POST DESCRIPTION --> */}
        <div className="description-field">
          <label
            htmlFor="description"
            className="d-flex justify-content-between"
          >
            Description and features*
            <span className="count">
              <span id="description-count">{dlengthCounter}</span> / 2000
            </span>
          </label>
          <textarea
            name="description"
            id="description"
            cols="30"
            rows="8"
            maxLength="2000"
            defaultValue={bnCxt.state.post_description}
            onChange={(e) => {
              setDlengthCounter(e.target.value.length);
              // setDescription(e.target.value);
              bnCxt.setValue({ post_description: e.target.value });
            }}
          ></textarea>
        </div>
        {/* <!-- price and amount distribution --> */}
        <div className="form-row price-box align-items-center">
          <div className="col-md-6">
            <h6 className="price-tag">Price*</h6>
            <div className="price-field mb-4">
              <input
                type="radio"
                name="price"
                id="contact-price"
                onClick={() => bnCxt.setValue({ post_price: "Contact for price " })}
                defaultValue={parseInt(bnCxt.state.post_price)}
              />
              <label htmlFor="contact-price" className="label">
                Contact for price
              </label>
            </div>
            <div className="price-field">
              <input
                type="radio"
                name="price"
                id="fixed-price"
                defaultChecked={true}
                onChange={(el) => fixedPriceValue(el.target.value)}
              />
              <label htmlFor="fixed-price" className="label mt-3"></label>
              <div className="create-floating-label w-100">
                <input
                  type="number"
                  name=""
                  id="fixed-price-value"
                  defaultValue={bnCxt.state.post_price}
                  // defaultValue="3833"
                  onChange={(el) => bnCxt.setValue({ post_price: el.target.value })}
                />
                <label htmlFor="">Price*</label>
                <span className="naira">&#8358;</span>
              </div>
            </div>
            <div className="checkbox-field">
              <input
                type="checkbox"
                name="negotiable"
                id="negotiable"
                className="checkbox"
                defaultValue={bnCxt.state.post_negotiable}
                onClick={() => {
                  // let neg = negotiable;
                  // setNegotiable(!neg);
                  bnCxt.setValue({ post_negotiable: !bnCxt.state.negotiable });
                  bnCxt.state.negotiable == false
                    ? bnCxt.setValue({
                        post_price: document.querySelector("#fixed-price-value")
                          .value,
                      })
                    : bnCxt.setValue({ post_price: bnCxt.state.post_price });
                }}
              />
              <label htmlFor="negotiable" className="label">
                Negotiable
              </label>
            </div>
          </div>
          <div className="col-md-6 amount-distribution-field">
            <div className="form-field amount-distribution">
              <label htmlFor="">Amount Distribution*</label>
              <select
                onChange={(el) =>
                  bnCxt.setValue({ post_distribution: el.target.value })
                }
                name=""
                id="amount-distribution"
              >
                <option value="">Select</option>
                {amountDistributions &&
                  amountDistributions.map((amt, key) => (
                    <option key={key} value={amt.name}
                      selected={amt.name == bnCxt.state.post_distribution && true}
                    >
                      {amt.name}
                    </option>
                  ))}
              </select>
              <i className="fa fa-chevron-down arrow-down"></i>
            </div>
          </div>
        </div>
        <div className="tip">Promote this ad to reach more people</div>
      </fieldset>

      <input
        type="button"
        className="post-btn"
        style={{ display: bnCxt.state.showSection[1], marginTop: "20px" }}
        onClick={validateForm}
        value="Update Post"
      />
    </>
  )
}

export default EditRegularPostNext
