import React from 'react'

const OrderComments = () => {
    return (
        <div className="comments-container">
          <div className="comments-container-header d-flex justify-content-between">
            <h6>Comments</h6>
            <button
              className="add-comment"
              data-toggle="modal"
              data-target="#add-comment"
            >
              <i className="fa fa-plus-square"></i> Add Comment
            </button>
            <div
              className="modal add-comment-modal fade"
              id="add-comment"
              tabindex="-1"
              role="dialog"
              aria-labelledby="apply-advanced"
              aria-hidden="true"
            >
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
              <div
                className="modal-dialog modal-lg modal-dialog-centered"
                role="document"
              >
                <div className="modal-content">
                  <div className="modal-body">
                    <h5>Add a comment to this Order Thread</h5>
                    <form action="">
                      <div className="form-field">
                        <label for="comment">Comment</label>
                        <textarea
                          name="comment"
                          id="comment"
                          cols="30"
                          rows="7"
                          required
                        ></textarea>
                      </div>
                      <button className="submit-comment">Submit comment</button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="comments-messages">
            <div className="media">
              <div className="media-body">
                <h6>
                  Created by <span>Tinkoko Agent</span> | Created at 22nd June
                  2020 8:25am
                </h6>
                <h6>
                  <span>CID09340934093054034</span> - Customer claims the
                  tomatoes was not complete when they counted it in modus.
                </h6>
                <button className="reply">Reply</button>

                <div className="media pl-4">
                  <div className="media-body left-border">
                    <h6>
                      Created by <span>James Doe</span> | Created at 22nd June
                      2020 8:25am
                    </h6>
                    <h6>My own is part comment is here now now now</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    )
}

export default OrderComments
