import React, { useState, useEffect } from "react";

const OrderDetailModal = ({ order }) => {
  return order ? (
    <div
      className="modal order-quick-view fade"
      id="order-quick-view"
      tabindex="-1"
      role="dialog"
      aria-labelledby="apply-advanced"
      aria-hidden="true"
    >
      <button
        type="button"
        className="close"
        data-dismiss="modal"
        aria-label="Close"
      >
        <span aria-hidden="true">&times;</span>
      </button>
      <div
        className="modal-dialog modal-lg modal-dialog-centered"
        role="document"
      >
        <div className="modal-content">
          <div className="modal-body">
            <h5>Order {order._id} - Quick View</h5>
            <div className="row">
              <div className="col-md-6 customer-information">
                <h6>Customer information</h6>
                <div className="single-detail">
                  <p>Date:</p>
                  <p>{order.date_placed} 14:56</p>
                </div>
                <div className="single-detail">
                  <p>Customer:</p>
                  <p>{order.buyer_first_name}</p>
                </div>
                <div className="single-detail">
                  <p>Phone Number:</p>
                  <p>{order.buyer_phone}</p>
                </div>
              </div>
              <div className="col-md-6 transaction-details">
                <h6>Transaction details</h6>
                <div className="single-detail">
                  <p>Item Price:</p>
                  <p>{order.total_price}</p>
                </div>
                <div className="single-detail">
                  <p>Tinkoko Commission:</p>
                  <p>-45,000.00</p>
                </div>
                <div className="single-detail">
                  <p>Shipping Cost Contribution:</p>
                  <p>0.00</p>
                </div>
                <div className="single-detail">
                  <p>Shipping Cost</p>
                  <p>8,000</p>
                </div>
                <div className="single-detail">
                  <p>Number Of Product:</p>
                  <p>{order.items.length}</p>
                </div>
                <div className="single-detail">
                  <p>Payment Method:</p>
                  <p>{order.payment_method}</p>
                </div>
                <div className="single-detail">
                  <p>Tax amount:</p>
                  <p>824.89</p>
                </div>
                <div className="single-detail">
                  <p>Voucher Total:</p>
                  <p>0</p>
                </div>
                <div className="single-detail">
                  <p>Voucher Code:</p>
                  <p>N/A</p>
                </div>
                <div className="single-detail">
                  <p>Grand Total:</p>
                  <p>{order.total_price}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <div>No detail found </div>
  );
};

export default OrderDetailModal;
