import React, { useEffect, useState, useReducer, createContext } from "react";
import SupportReducer from "./SupportReducer";

export const SupportContext = createContext();
const { Provider } = SupportContext;
export const SupportState = (props) => {
  const initialState = {
    feedbacks: [],
    supports:[],
    reasons: [],
    issue: null,
    comment: null,
    files: [],
  };
  const [state, dispatch] = useReducer(SupportReducer, initialState);

  const setValue = (obj = {}) => {
    dispatch({ type: "SET_STATE", payload: { ...obj } });
  };

  return (
    <Provider
      value={{
        setValue,
        state,
      }}
    >
      {props.children}
    </Provider>
  );
};

export default SupportState;
