import React, {
  DOMAttributes,
  useState,
  useEffect,
  createContext,
  useReducer,
} from "react";
import { useContext } from "react";
import { AuthContext } from "../../../context/authContext/AuthState";
import { Link } from "react-router-dom";
import { AlertContext } from "../../../context/alertContext/AlertState";
import BuyNowPostReducer from "./BuyNowPostReducer";
import ProductSubmitted from "./ProductSubmitted";
import BuyNowProductAbout from "./BuyNowProductAbout";
import BuyNowProductDetail from "./BuyNowProductDetail";

export const BuyNowPostContext = createContext();
const { Provider } = BuyNowPostContext;

const CreateBuyNowProduct = () => {
  const initialState = {
    phone: null,
    businessName: null,
    description: null,
    title: null,
    price: null,
    negotiable: false,
    amountDistribution: null,
    region: null,
    country: null,
    category: null,
    subCategory: null,
    productImages: [],
    productVideo: {},
    dropOffLocation: null,
    isSubmitted: false,
    nextSection: false,
    tab: ["current active", ""],
  };
  const [state, dispatch] = useReducer(BuyNowPostReducer, initialState);

  useEffect(() => {
    authCxt.loadUser();
  }, []);

  const authCxt = useContext(AuthContext);
  const alertCxt = useContext(AlertContext);
  const [submitted, setSubmitted] = useState(false);

  // SET PRODUCT STATE
  const setPstate = (prop = {}) => {
    dispatch({ type: "SET-STATE", payload: prop });
  };
  return (
    <Provider value={{ pState: state, setPstate }}>
      {state.isSubmitted ? (
        <ProductSubmitted />
      ) : (
        <div className="col-md-9">
          <div className="dashboard-main-content">
            <div className="create-post-regular">
              <h6 className="header">Create a Post - Buy Now</h6>
              <p className="text">
                Buy now posts are products sold in partnership with Tinkoko
                Logistics and enables a buyer make a purchase instantly on our
                website by simply adding this product to cart and checking out.
              </p>
              <ul id="section-tabs">
                <li className={`${state.tab[0]}`}>Step 2: About Product</li>
                <li className={`${state.tab[1]}`}>Step 3: Product Details</li>
              </ul>
              <div className="note">
                Note that all Buy now products must be fulfilled and customer
                satisfied before payment is released to a seller.
              </div>
              <form id="create-post" action="#">
                <div id="fieldsets">
                  {!state.nextSection ? (
                    <BuyNowProductAbout />
                  ) : (
                    <BuyNowProductDetail />
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
    </Provider>
  );
};

export default CreateBuyNowProduct;
