import React, { useEffect, useState, useContext } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { AuthContext } from "../../../context/authContext/AuthState";
import { errorBorder } from "../../../utils/errorBorder";
import { EditContext } from "./EditState";
import Loader from "../../../common/Loader";
import ProductSubmitted from "./ProductSubmitted";

const EditJoblistingPost = (props) => {
  const authCxt = useContext(AuthContext);
  const jobCxt = useContext(EditContext);

  const [postid, setPostid] = useState(props.match.params.postid);
  const [countries, setCountries] = useState([]);
  const [regions, setRegions] = useState([]);
  const [previewImages, setPreviewImages] = useState(null);
  const [categories, setCategories] = useState([]);
  const [subCategories, setSubCategories] = useState([]);
  const [tlengthCounter, setTlengthCounter] = useState(0);
  const [dlengthCounter, setDlengthCounter] = useState(0);
  const [minimumQualifications, setMinimumQualifications] = useState([]);
  const [yearsOfExperience, setYearsOfExperience] = useState([]);
  const [businessFunctions, setBusinessFunctions] = useState([]);
  const [businessIndustries, setBusinessIndustries] = useState([]);
  const [jobTypes, setJobTypes] = useState([]);

  useEffect(() => {
    getPost();
    jobCategories();
    getRegions();
    getCountries();
    getMinQualification();
    getYearsOfExperience();
    getBusinessIndustries();
    getBusinessFunctions();
    getJobTypes();
  }, [authCxt.token]);

  useEffect(() => {
    if (jobCxt.state._id && authCxt.token) {
      setTlengthCounter(jobCxt.state.post_title.length);
      setDlengthCounter(jobCxt.state.post_description.length);
      jobCxt.setValue({ business_name: authCxt.user.businessName });
      if (categories.length > 0) {
        console.log("categoreis is now more than 0 ")
        jobSubcategories(jobCxt.state.post_category);
      }
    }
  }, [jobCxt.state.post._id, authCxt.token, categories.length]);

  // EXTRACT IMAGE FROM THE FILE INPUT FORM TO SET THE PREVIEW IMAGE STATE
  function readImage(event) {
    if (window.File && window.FileList && window.FileReader) {
      var files = event.target.files; //FileList object
      var file = files[0];
      if (file.type.match("image")) {
        jobCxt.setValue({ jobImages: file });
        var picReader = new FileReader();
        picReader.addEventListener("load", function (event) {
          var picFile = event.target;
          jobCxt.setValue({ post_images: [picFile.result] });
        });
        picReader.readAsDataURL(file);

        event.target.value = "";
      }
    } else {
      console.log("Browser not support");
    }
  }

  // CANCLE AN IMAGE
  function cancelImage(img, key) {
    setPreviewImages(null);
    jobCxt.setValue({
      jobImages: null,
    });
    if (Array.isArray(jobCxt.state.post_images)) {
      console.log(img,"about to cancel image ")
      if (img == jobCxt.state.post_images[0]) {
        jobCxt.setValue({post_images:[]})
        let xhr = new XMLHttpRequest();
        xhr.open("POST", `/api/seller/jobs/delete/image`, true);
        xhr.onload = () => {
          if (xhr.status === 200 && xhr.readyState === 4) {
            if (xhr.responseText) {
              if (JSON.parse(xhr.responseText)) {
                let res = JSON.parse(xhr.responseText).success;
                console.log(res, "the image has been deleted");
              }
            }
          }
        };
        xhr.setRequestHeader("x-auth-user", authCxt.token);
        let dataform = new FormData();
        dataform.append("img", img);
        dataform.append("postid", jobCxt.state._id);
        xhr.send(dataform);
      }
    }
  }

  const chooseImage = () => {
    let d = document.querySelector("#job-photo");
    d.click();
  };

  // GET ALL AVAILABLE REGION FROM THE SERVER
  const getRegions = async () => {
    try {
      let res = await axios.get("/api/seller/product-regions");
      if (res.data) {
        if (res.data.success) {
          setRegions(res.data.success);
        }
      }
    } catch (error) {
      setRegions([]);
    }
  };
  // GET ALL AVAILABLE COUNTRIES FROM THE SERVER
  const getCountries = async () => {
    try {
      let res = await axios.get("/api/seller/product-countries");
      if (res.data) {
        if (res.data.success) {
          setCountries(res.data.success);
        }
      }
    } catch (error) {
      setCountries([]);
    }
  };
  // GET ALL MINIMUM QUALIFICATIONS FROM THE SERVER
  const getMinQualification = async () => {
    try {
      let res = await axios.get("/api/seller/minimum-qualifications");
      if (res.data) {
        if (res.data.success) {
          setMinimumQualifications(res.data.success);
        }
      }
    } catch (error) {
      setMinimumQualifications([]);
    }
  };

  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getYearsOfExperience = async () => {
    try {
      let res = await axios.get("/api/seller/years-of-experience");
      if (res.data) {
        if (res.data.success) {
          setYearsOfExperience(res.data.success);
        }
      }
    } catch (error) {
      setYearsOfExperience([]);
    }
  };

  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getBusinessIndustries = async () => {
    try {
      let res = await axios.get("/api/seller/business-industries");
      if (res.data) {
        if (res.data.success) {
          setBusinessIndustries(res.data.success);
        }
      }
    } catch (error) {
      setBusinessIndustries([]);
    }
  };
  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getBusinessFunctions = async () => {
    try {
      let res = await axios.get("/api/seller/business-functions");
      if (res.data) {
        if (res.data.success) {
          setBusinessFunctions(res.data.success);
        }
      }
    } catch (error) {
      setBusinessFunctions([]);
    }
  };
  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getJobTypes = async () => {
    try {
      let res = await axios.get("/api/seller/job-types");
      if (res.data) {
        if (res.data.success) {
          setJobTypes(res.data.success);
        }
      }
    } catch (error) {
      setJobTypes([]);
    }
  };

  // GET ALL PORDUCT CATEGORIES FROM SERVER
  const jobCategories = async () => {
    try {
      let res = await axios.get("/api/seller/product-categories");
      if (res.data) {
        if (res.data.success) {
          setCategories(res.data.success);
        }
      }
    } catch (error) {
      setCategories([]);
    }
  };

  // GET ALL PRODUCT SUBCATEGORIES FROM SERVER

  const jobSubcategories = async (catname) => {
    console.log("job sub category should be on now na ")
    const fromCat = categories.filter((cat, key) => cat.name === catname);
    setSubCategories(fromCat[0].subCategories);
    if (Array.isArray(fromCat)) {
      if (fromCat.length > 0) {
        jobCxt.setValue({ post_subcategory: fromCat[0].subCategories[0] });
      }
    }
  };

  const getPost = () => {
    if (postid && authCxt.token) {
      let xhr = new XMLHttpRequest();
      xhr.open("GET", `/api/seller/jobs/${postid}`, true);
      xhr.onload = () => {
        if (xhr.status === 200 && xhr.readyState === 4) {
          if (xhr.responseText) {
            if (JSON.parse(xhr.responseText)) {
              let res = JSON.parse(xhr.responseText).success;
              if (res) {
                if (Object.keys(res).length > 0) {
                  let salaries = {};
                  salaries = JSON.parse(res.job_salary);
                  console.log(res.job_salary);
                  let advanceListing = JSON.parse(res.advance_listing);
                  let post = {
                    ...res,
                    advance_listing: [...advanceListing],
                    job_salary: salaries,
                  };
                  jobCxt.setValue({ ...post });
                  // console.log(post, " the post for the response text")
                }
              }
            }
          }
        }
      };
      xhr.setRequestHeader("x-auth-user", authCxt.token);
      xhr.send();
    }
  };

  // Set advance listing down here

  const setAdvanceListing = (name) =>{
    let adv = jobCxt.state.advance_listing
    if (Array.isArray(adv)) {
      let isIn = adv.some((ad)=>ad==name)
      if (isIn) {
        let adf = []     
        for (let i = 0; i < adv.length; i++) {
          let al = adv[i];
          if (al !== name) {
            adf.push(al)
          }
        }                       
        return jobCxt.setValue({
          advance_listing: [
            ...adf,
          ]});
      }else{
        return jobCxt.setValue({
          advance_listing: [
            ...jobCxt.state.advance_listing,
            name,
          ]
        });
      }
    }
  }
  const validateForm = () => {
    // return console.log(jobCxt.state, ' all the fxxk states ')
    if (jobCxt.state.post_category) {
      if (jobCxt.state.post_subcategory) {
        if (jobCxt.state.jobImages || jobCxt.state.post_images) {
          if (jobCxt.state.post_region) {
            if (jobCxt.state.post_country) {
              if (jobCxt.state.post_title) {
                if (jobCxt.state.post_description) {
                  if (jobCxt.state.business_industry) {
                    if (jobCxt.state.job_type) {
                      if (jobCxt.state.business_function) {
                        if (jobCxt.state.job_role) {
                          if (jobCxt.state.job_qualification) {
                            if (jobCxt.state.years_of_experience) {
                              if (jobCxt.state.job_education_specialization) {
                                if (jobCxt.state.skills) {
                                  if (jobCxt.state.max_age) {
                                    if (jobCxt.state.gender) {
                                      if (jobCxt.state.job_salary.from) {
                                        if (jobCxt.state.job_salary.to) {
                                          if (
                                            jobCxt.state
                                              .job_application_deadline
                                          ) {
                                            if (
                                              jobCxt.state.advance_listing
                                                .length > 0
                                            ) {
                                              submitPost();
                                            } else {
                                              errorBorder(
                                                "#job-advance-listing",
                                                "Select job advance listing"
                                              );
                                            }
                                          } else {
                                            errorBorder(
                                              "#job-application-deadline",
                                              "Enter amount in distribution"
                                            );
                                          }
                                        } else {
                                          errorBorder(
                                            "#job-salary-to",
                                            "Enter maximum salary you are can pay for this position"
                                          );
                                        }
                                      } else {
                                        errorBorder(
                                          "#job-salary-from",
                                          "Enter minimum salary you want to pay for this position"
                                        );
                                        console.log(
                                          "enter product description here "
                                        );
                                      }
                                    } else {
                                      errorBorder(
                                        "#job-gender",
                                        "Select for this job position"
                                      );
                                      // errline.appendChild(para)
                                    }
                                  } else {
                                    errorBorder(
                                      "#job-max-age",
                                      "Maximum applicant age"
                                    );
                                    // errline.appendChild(para)
                                  }
                                } else {
                                  errorBorder(
                                    "#job-skills",
                                    "What skills are you looking for"
                                  );
                                  // errline.appendChild(para)
                                }
                              } else {
                                errorBorder(
                                  "#job-specialization",
                                  "enter enter product title "
                                );
                                // errline.appendChild(para)
                              }
                            } else {
                              errorBorder(
                                "#job-years-of-experience",
                                "Select job years of experiece"
                              );
                              // errline.appendChild(para)
                            }
                          } else {
                            errorBorder(
                              "#job-min-qualification",
                              "enter enter product title "
                            );
                            // errline.appendChild(para)
                          }
                        } else {
                          errorBorder("#job-role", "Enter job role");
                          // errline.appendChild(para)
                        }
                      } else {
                        errorBorder(
                          "#job-business-function",
                          "Select the type of job you are posting "
                        );
                        // errline.appendChild(para)
                      }
                    } else {
                      errorBorder("#job-type", "enter enter product title ");
                      // errline.appendChild(para)
                    }
                  } else {
                    errorBorder(
                      "#job-business-industry",
                      "Select job business industry "
                    );
                    // errline.appendChild(para)
                  }
                } else {
                  errorBorder("#description", "Enter job description ");
                  // errline.appendChild(para)
                }
              } else {
                errorBorder("#title", "Enter job title ");
                // errline.appendChild(para)
              }
            } else {
              errorBorder("#job-country", "Select country to place your job");
              // errline.appendChild(para)
            }
          } else {
            errorBorder("#job-region", "Select region ");
            // errline.appendChild(para)
          }
        } else {
          errorBorder(".preview-zone", "Please select image for your post");
        }
      } else {
        // errline.appendChild(para)
        errorBorder("#job-subcategory", "Select job subcategory");
      }
    } else {
      errorBorder("#job-category", "Select job category ");
      // errline.appendChild(para)
    }
  };
  // SUBMIT POST TO BACKEND SERVER
  const submitPost = () => {
    const dataForm = new FormData();
    dataForm.append("jobcat", jobCxt.state.post_category);
    dataForm.append("jobsubcat", jobCxt.state.post_subcategory);
    dataForm.append("jobregion", jobCxt.state.post_region);
    dataForm.append("jobcountry", jobCxt.state.post_country);
    dataForm.append("jobtitle", jobCxt.state.post_title);
    dataForm.append("jobdesc", jobCxt.state.post_description);
    dataForm.append("jobbizindustry", jobCxt.state.business_industry);
    dataForm.append("jobtype", jobCxt.state.job_type);
    dataForm.append("jobbizfxn", jobCxt.state.business_function);
    dataForm.append("jobrole", jobCxt.state.job_role);
    dataForm.append("jobminqualify", jobCxt.state.job_qualification);
    dataForm.append("jobyearsofexp", jobCxt.state.years_of_experience);
    dataForm.append("jobspec", jobCxt.state.job_education_specialization);
    dataForm.append("jobskills", jobCxt.state.skills);
    dataForm.append("jobmaxage", jobCxt.state.max_age);
    dataForm.append("jobgenderpref", jobCxt.state.gender);
    dataForm.append(
      "jobsalary",
      JSON.stringify(jobCxt.state.job_salary)
    );
    dataForm.append("jobappxndeadline", jobCxt.state.job_application_deadline);
    dataForm.append(
      "jobadvncelisting",
      JSON.stringify(jobCxt.state.advance_listing)
    );
    dataForm.append("jimg", jobCxt.state.jobImages);

    let xhr = new XMLHttpRequest();
    xhr.open("POST", `/api/seller/my-product/joblistings/edit/${jobCxt.state._id}`, true);
    xhr.onload = () => {
      console.log(xhr.response, "job listing product");
      if (xhr.status == 200 && xhr.readyState == 4) {
        if (xhr.responseText) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            console.log(xhr.responseText, " the job listing here ");
            jobCxt.setValue({ isSubmitted: true });
            // isSubmitted(true)
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  return !jobCxt.state._id ? (
    <Loader />
  ) : jobCxt.state.isSubmitted ? <ProductSubmitted /> :(
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="create-product-job">
          <div className="content-top">
            <h5 className="header">Update Job listing post</h5>
            <h6 className="looking">Looking for Talent?</h6>
            <p className="header-text">
              Tinkoko market place also gives you access to find the best fit
              for your organisation. You can use our advanced tools to
              streamline your hiring process or create a job listing with the
              basic options.
            </p>
          </div>
          <div className="create-product-job-form">
            <form action="">
              <h6 className="note">
                Note that Buy Now products must be fulfilled and customer
                satisfied before payment is released to a seller.
              </h6>
              <div className="form-row">
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-category"
                      onChange={(el) => {
                        jobSubcategories(el.target.value);
                        jobCxt.setValue({ post_category: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {categories.map((cat, key) => (
                        <option
                          key={key}
                          value={cat.name}
                          onClick={() =>
                            jobCxt.setValue({ post_category: cat.name })
                          }
                          selected={
                            cat.name === jobCxt.state.post_category && true
                          }
                        >
                          {cat.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Category*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-subcategory"
                      onChange={(el) =>
                        jobCxt.setValue({ post_subcategory: el.target.value })
                      }
                    >
                      <option value=""></option>
                      {subCategories.map((subcat, key) => (
                        <option
                          key={key}
                          selected={
                            subcat == jobCxt.state.post_subcategory && true
                          }
                          value={subcat}
                        >
                          {subcat}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Subcategory*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
              </div>
              {/* <!-- JOB IMAGE UPLOAD --> */}
              <div className="image-upload mb-5">
                <h6>Add photos (Maximum of 1 photo, 3mb)</h6>
                <div className="choose-image">
                  {!jobCxt.state.post_images[0] && (
                    <a onClick={chooseImage} className="choose">
                      <i className="fa fa-plus"></i>
                    </a>
                  )}
                  <input
                    type="file"
                    id="job-photo"
                    onInput={(event) => readImage(event)}
                  />

                  <div className="preview-zone">
                    <div className="image-container">
                      {jobCxt.state.post_images[0] && (
                        <a className="change" onClick={chooseImage}>
                          <i className="fa fa-redo-alt"></i>
                        </a>
                      )}
                      {jobCxt.state.post_images[0] && (
                        <>
                          <img
                            id="preview-image"
                            src={jobCxt.state.post_images[0]}
                            className="img-fluid"
                          />
                          <div
                            id="remove-image"
                            className="close"
                            onClick={() => cancelImage(jobCxt.state.post_images[0])}
                          >
                            X
                          </div>
                        </>
                      )}
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-row">
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-region"
                      onChange={(el) => {
                        jobCxt.setValue({ post_region: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {regions.map((reg, key) => (
                        <option
                          key={key}
                          value={reg.name}
                          selected={
                            reg.name === jobCxt.state.post_region && true
                          }
                        >
                          {" "}
                          {reg.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Region*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-country"
                      onChange={(el) => {
                        jobCxt.setValue({ post_country: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {countries.map((c, key) => (
                        <option
                          value={c.name}
                          key={key}
                          selected={
                            c.name === jobCxt.state.post_country && true
                          }
                        >
                          {c.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Country*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
              </div>
              {/* <!-- TITLE FIELD --> */}
              <div className="title-field">
                <div className="d-flex justify-content-end">
                  <span className="short">Keep it short</span>
                  <span className="count">
                    <span id="title-count">{tlengthCounter}</span> / 60
                  </span>
                </div>
                <input
                  type="text"
                  name="title"
                  id="title"
                  onChange={(e) => {
                    setTlengthCounter(e.target.value.length);
                    jobCxt.setValue({ post_title: e.target.value });
                    // setTitle(e.target.value);
                  }}
                  defaultValue={jobCxt.state.post_title}
                  placeholder="Title*"
                  maxLength="60"
                />
              </div>
              {/* <!-- JOB DESCRIPTION --> */}
              <div className="description-field">
                <label
                  htmlFor="description"
                  className="d-flex justify-content-between"
                >
                  Job description*
                  <span className="count">
                    <span id="description-count">{dlengthCounter}</span> / 2000
                  </span>
                </label>
                <textarea
                  name="description"
                  id="description"
                  cols="30"
                  rows="8"
                  defaultValue={jobCxt.state.post_description}
                  onChange={(e) => {
                    setDlengthCounter(e.target.value.length);
                    // setDescription(e.target.value);
                    jobCxt.setValue({ post_description: e.target.value });
                  }}
                ></textarea>
              </div>
              {/* <!-- WORK INFO AND PREFERENCES --> */}
              <div className="work-info-and-preferences">
                <p className="text">Work info and Candidate preferences</p>
                <div className="form-row">
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-business-industry"
                        onChange={(el) =>
                          jobCxt.setValue({
                            business_industry: el.target.value,
                          })
                        }
                      >
                        <option value=""></option>
                        {businessIndustries &&
                          businessIndustries.map((bizind, key) => (
                            <option
                              key={key}
                              value={bizind.name}
                              selected={
                                bizind.name == jobCxt.state.business_industry &&
                                true
                              }
                            >
                              {bizind.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Business Industry*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-type"
                        onChange={(el) =>
                          jobCxt.setValue({ job_type: el.target.value })
                        }
                      >
                        <option value=""></option>
                        {jobTypes &&
                          jobTypes.map((j, key) => (
                            <option
                              value={j.name}
                              key={key}
                              selected={
                                j.name === jobCxt.state.job_type && true
                              }
                            >
                              {j.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Job type*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-business-function"
                        onChange={(el) =>
                          jobCxt.setValue({
                            business_function: el.target.value,
                          })
                        }
                      >
                        <option value=""></option>
                        {businessFunctions &&
                          businessFunctions.map((bizfn, key) => (
                            <option
                              key={key}
                              value={bizfn.name}
                              selected={
                                bizfn.name === jobCxt.state.business_function
                              }
                            >
                              {bizfn.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Business function*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-role"
                        onInput={(el) =>
                          jobCxt.setValue({ job_role: el.target.value })
                        }
                        defaultValue={jobCxt.state.job_role}
                      />
                      <label htmlFor="">Role / Designation (optional)</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-min-qualification"
                        onChange={(el) =>
                          jobCxt.setValue({
                            job_qualification: el.target.value,
                          })
                        }
                      >
                        <option value=""></option>
                        {minimumQualifications.map((min, key) => (
                          <option
                            value={min.name}
                            key={key}
                            selected={
                              min.name == jobCxt.state.job_qualification && true
                            }
                          >
                            {min.name}
                          </option>
                        ))}
                      </select>
                      <label htmlFor="">Minimum Qualification (optional)</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-years-of-experience"
                        onChange={(el) =>
                          jobCxt.setValue({
                            years_of_experience: el.target.value,
                          })
                        }
                      >
                        <option value=""></option>
                        {yearsOfExperience.map((yoe, key) => (
                          <option
                            key={key}
                            value={yoe.name}
                            selected={
                              yoe.name == jobCxt.state.years_of_experience &&
                              true
                            }
                          >
                            {yoe.name}
                          </option>
                        ))}
                      </select>
                      <label htmlFor="">Required years of experience*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-specialization"
                        onInput={(el) =>
                          jobCxt.setValue({
                            job_education_specialization: el.target.value,
                          })
                        }
                        defaultValue={jobCxt.state.job_education_specialization}
                      />
                      <label htmlFor="">
                        Educational specialization (optional)
                      </label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-skills"
                        onInput={(el) =>
                          jobCxt.setValue({ skills: el.target.value })
                        }
                        defaultValue={jobCxt.state.skills}
                      />
                      <label htmlFor="">Skills*</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="number"
                        name=""
                        id="job-max-age"
                        onInput={(el) =>
                          jobCxt.setValue({ max_age: el.target.value })
                        }
                        defaultValue={jobCxt.state.max_age}
                      />
                      <label htmlFor="">Maximum age (optional)</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-gender"
                        onChange={(el) =>
                          jobCxt.setValue({ gender: el.target.value })
                        }
                      >
                        {["Any", "Male", "Female"].map((g, key) => (
                          <option
                            value={g}
                            key={key}
                            selected={g == jobCxt.state.gender && true}
                          >
                            {g}
                          </option>
                        ))}
                      </select>
                      <label htmlFor="">Gender preference (optional)</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="salary-field">
                      <p>(&#8358;) Salary (per month) (optional)</p>
                      <input
                        type="number"
                        name="salaryFrom"
                        id="job-salary-from"
                        placeholder="From"
                        onInput={(el) =>
                          jobCxt.setValue({
                            job_salary: {...jobCxt.state.job_salary, from: el.target.value },
                          })
                        }
                        defaultValue={
                          jobCxt.state.job_salary &&
                          jobCxt.state.job_salary.from
                        }
                      />
                      <input
                        type="number"
                        name=""
                        id="job-salary-to"
                        onInput={(el) =>
                          jobCxt.setValue({
                            job_salary: {...jobCxt.state.job_salary, to: el.target.value },
                          })
                        }
                        defaultValue={
                          jobCxt.state.job_salary && jobCxt.state.job_salary.to
                        }
                        placeholder="To"
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="date"
                        name=""
                        id="job-application-deadline"
                        onInput={(el) =>
                          jobCxt.setValue({
                            job_application_deadline: el.target.value,
                          })
                        }
                        defaultValue={jobCxt.state.job_application_deadline}
                      />
                      <label htmlFor="">Application deadline</label>
                      <i className="fa fa-calendar-alt icon"></i>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- ADVANCED JOB LISTING TOOLS --> */}
              <div className="advanced-listing-tools">
                <div
                  className="checkbox-field green-checkbox"
                  id="job-advanced-listing"
                >
                  <input
                    type="checkbox"
                    name="advanced"
                    id=""
                    className="checkbox"
                    onInput={(el) =>
                      jobCxt.setValue({
                        advance_listing: ["Use Advance Listing"],
                      })
                    }
                    defaultChecked={
                      jobCxt.state.advance_listing.length > 0 && true
                    }
                  />
                  <label htmlFor="advanced" className="label">
                    Use advanced job listing tools
                  </label>
                  <p>
                    Note: All applicants will be able to apply via our platform.
                    Select the information you wish to receive from each
                    applicant.
                  </p>
                </div>
                <div className="all-other-options">
                  {[
                    "Full Name",
                    "Email",
                    "Date of Birth",
                    "Location",
                    "Experience",
                    "Current Salary",
                    "Highest education level",
                    "Current business function",
                    "Attached CV",
                  ].map((ad, key) => (
                    <div className="checkbox-field" key={key}>
                      <input
                        type="checkbox"
                        className="checkbox"
                        data-name={ad}
                        id={`a${key}`}
                        onChange={(el) => {
                          setAdvanceListing(el.target.dataset.name)
                        }}
                        
                        defaultChecked={
                          Array.isArray(jobCxt.state.advance_listing) &&
                          jobCxt.state.advance_listing.some((ba) => ba == ad) &&
                          true
                        }
                      />
                      <label htmlFor={`a${key}`} className="label">
                        {ad}
                      </label>
                    </div>
                  ))}
                </div>
                <p className="pl-4 mt-2">
                  Tip: Promote this ad to reach more people.
                </p>
              </div>
              {/* <!-- SUBMIT --> */}
              <div className="submit-field text-center">
                <button type="button" onClick={validateForm}>
                  Submit Post
                </button>
                <p>
                  By clicking on submit post, you accept the Terms of use,
                  confirm that you will abide by the safety tips, and declare
                  that posting does not include any prohibited items.
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditJoblistingPost;
