import React, { useEffect, useState, useContext } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { AuthContext } from "../../../context/authContext/AuthState";
import { errorBorder } from "../../../utils/errorBorder";
import { JoblistingContext } from "./CreateJoblistingProduct";

const JoblistingDetail = () => {
  const authCxt = useContext(AuthContext);
  const { setJstate, jState } = useContext(JoblistingContext);

  const [countries, setCountries] = useState([]);
  const [regions, setRegions] = useState([]);
  const [previewImages, setPreviewImages] = useState(null);
  const [categories, setCategories] = useState([]);
  const [subCategories, setSubCategories] = useState([]);
  const [tlengthCounter, setTlengthCounter] = useState(0);
  const [dlengthCounter, setDlengthCounter] = useState(0);
  const [minimumQualifications, setMinimumQualifications] = useState([]);
  const [yearsOfExperience, setYearsOfExperience] = useState([]);
  const [businessFunctions, setBusinessFunctions] = useState([]);
  const [businessIndustries, setBusinessIndustries] = useState([]);
  const [jobTypes, setJobTypes] = useState([]);

  useEffect(() => {
    jobCategories();
    getRegions();
    getCountries();
    getMinQualification();
    getYearsOfExperience();
    getBusinessIndustries();
    getBusinessFunctions();
    getJobTypes();
    // console.log(jState, " all the product state");
  }, [jState]);

  // EXTRACT IMAGE FROM THE FILE INPUT FORM TO SET THE PREVIEW IMAGE STATE
  function readImage(event) {
    if (window.File && window.FileList && window.FileReader) {
      var files = event.target.files; //FileList object
      var file = files[0];
      if (file.type.match("image")) {
        setJstate({ jobImages: file  });
        console.log(file.type)
        //   setjobImages([...jobImages, { file }]);
        var picReader = new FileReader();
        picReader.addEventListener("load", function (event) {
          var picFile = event.target;
          setPreviewImages(picFile.result);
        });
        picReader.readAsDataURL(file);
        
        event.target.value = "";
      }
    } else {
      console.log("Browser not support");
    }
  }

  // CANCLE AN IMAGE
  function cancelImage(img, key) {
    setPreviewImages(null);
    setJstate({
      jobImages: null,
    });
  }

  const chooseImage = () => {
    let d = document.querySelector("#job-photo");
    d.click();
  };

  // GET ALL AVAILABLE REGION FROM THE SERVER
  const getRegions = async () => {
    try {
      let res = await axios.get("/api/seller/product-regions");
      if (res.data) {
        if (res.data.success) {
          setRegions(res.data.success);
        }
      }
    } catch (error) {
      setRegions([]);
    }
  };
  // GET ALL AVAILABLE COUNTRIES FROM THE SERVER
  const getCountries = async () => {
    try {
      let res = await axios.get("/api/seller/product-countries");
      if (res.data) {
        if (res.data.success) {
          setCountries(res.data.success);
        }
      }
    } catch (error) {
      setCountries([]);
    }
  };
  // GET ALL MINIMUM QUALIFICATIONS FROM THE SERVER
  const getMinQualification = async () => {
    try {
      let res = await axios.get("/api/seller/minimum-qualifications");
      if (res.data) {
        if (res.data.success) {
          setMinimumQualifications(res.data.success);
        }
      }
    } catch (error) {
      setMinimumQualifications([]);
    }
  };

  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getYearsOfExperience = async () => {
    try {
      let res = await axios.get("/api/seller/years-of-experience");
      if (res.data) {
        if (res.data.success) {
          setYearsOfExperience(res.data.success);
        }
      }
    } catch (error) {
      setYearsOfExperience([]);
    }
  };

  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getBusinessIndustries = async () => {
    try {
      let res = await axios.get("/api/seller/business-industries");
      if (res.data) {
        if (res.data.success) {
          setBusinessIndustries(res.data.success);
        }
      }
    } catch (error) {
      setBusinessIndustries([]);
    }
  };
  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getBusinessFunctions = async () => {
    try {
      let res = await axios.get("/api/seller/business-functions");
      if (res.data) {
        if (res.data.success) {
          setBusinessFunctions(res.data.success);
        }
      }
    } catch (error) {
      setBusinessFunctions([]);
    }
  };
  // GET ALL YEARS OF EXPERIENCE  FROM THE SERVER
  const getJobTypes = async () => {
    try {
      let res = await axios.get("/api/seller/job-types");
      if (res.data) {
        if (res.data.success) {
          setJobTypes(res.data.success);
        }
      }
    } catch (error) {
      setJobTypes([]);
    }
  };

  // GET ALL PORDUCT CATEGORIES FROM SERVER
  const jobCategories = async () => {
    try {
      let res = await axios.get("/api/seller/product-categories");
      if (res.data) {
        if (res.data.success) {
          setCategories(res.data.success);
        }
      }
    } catch (error) {
      setCategories([]);
    }
  };

  // GET ALL PRODUCT SUBCATEGORIES FROM SERVER

  const jobSubcategories = async (catname) => {
    const fromCat = categories.filter((cat, key) => cat.name === catname);
    setSubCategories(fromCat[0].subCategories);
    if (typeof fromCat == "array") {
      if (fromCat.length > 0) {
        // setJstate({ subCategory: fromCat[0].subCategories[0] });
      }
    }
  };
  const validateForm = () => {
    // return console.log(jState, ' all the fxxk states ')
    if (jState.category) {
      if (jState.subCategory) {
        if (jState.jobImages) {
          if (jState.region) {
            if (jState.country) {
              if (jState.title) {
                if (jState.description) {
                  if (jState.businessIndustry) {
                    if (jState.type) {
                      if (jState.businessFunction) {
                        if (jState.role) {
                          if (jState.minQualification) {
                            if (jState.yearsOfExperience) {
                              if (jState.specialization) {
                                if (jState.skills) {
                                  if (jState.maxAge) {
                                    if (jState.preferredGender) {
                                      if (jState.salaryFrom) {
                                        if (jState.salaryTo) {
                                          if (jState.applicationDealine) {
                                            if (
                                              jState.advanceListing.length > 0
                                            ) {
                                              submitPost();
                                            } else {
                                              errorBorder(
                                                "#job-advance-listing",
                                                "Select job advance listing"
                                              );
                                            }
                                          } else {
                                            errorBorder(
                                              "#job-application-deadline",
                                              "Enter amount in distribution"
                                            );
                                          }
                                        } else {
                                          errorBorder(
                                            "#job-salary-to",
                                            "Enter maximum salary you are can pay for this position"
                                          );
                                        }
                                      } else {
                                        errorBorder(
                                          "#job-salary-from",
                                          "Enter minimum salary you want to pay for this position"
                                        );
                                        console.log(
                                          "enter product description here "
                                        );
                                      }
                                    } else {
                                      errorBorder(
                                        "#job-gender",
                                        "Select for this job position"
                                      );
                                      // errline.appendChild(para)
                                    }
                                  } else {
                                    errorBorder(
                                      "#job-max-age",
                                      "Maximum applicant age"
                                    );
                                    // errline.appendChild(para)
                                  }
                                } else {
                                  errorBorder(
                                    "#job-skills",
                                    "What skills are you looking for"
                                  );
                                  // errline.appendChild(para)
                                }
                              } else {
                                errorBorder(
                                  "#job-specialization",
                                  "enter enter product title "
                                );
                                // errline.appendChild(para)
                              }
                            } else {
                              errorBorder(
                                "#job-years-of-experience",
                                "Select job years of experiece"
                              );
                              // errline.appendChild(para)
                            }
                          } else {
                            errorBorder(
                              "#job-min-qualification",
                              "enter enter product title "
                            );
                            // errline.appendChild(para)
                          }
                        } else {
                          errorBorder("#job-role", "Enter job role");
                          // errline.appendChild(para)
                        }
                      } else {
                        errorBorder(
                          "#job-business-function",
                          "Select the type of job you are posting "
                        );
                        // errline.appendChild(para)
                      }
                    } else {
                      errorBorder("#job-type", "enter enter product title ");
                      // errline.appendChild(para)
                    }
                  } else {
                    errorBorder(
                      "#job-business-industry",
                      "Select job business industry "
                    );
                    // errline.appendChild(para)
                  }
                } else {
                  errorBorder("#description", "Enter job description ");
                  // errline.appendChild(para)
                }
              } else {
                errorBorder("#title", "Enter job title ");
                // errline.appendChild(para)
              }
            } else {
              errorBorder("#job-country", "Select country to place your job");
              // errline.appendChild(para)
            }
          } else {
            errorBorder("#job-region", "Select region ");
            // errline.appendChild(para)
          }
        } else {
          errorBorder(".preview-zone", "Please select image for your post");
        }
      } else {
        // errline.appendChild(para)
        errorBorder("#job-subcategory", "Select job subcategory");
      }
    } else {
      errorBorder("#job-category", "Select job category ");
      // errline.appendChild(para)
    }
  };
  // SUBMIT POST TO BACKEND SERVER
  const submitPost = () => {
    const dataForm = new FormData();
    dataForm.append("jobcat", jState.category);
    dataForm.append("jobsubcat", jState.subCategory);
    dataForm.append("jobregion", jState.region);
    dataForm.append("jobcountry", jState.country);
    dataForm.append("jobtitle", jState.title);
    dataForm.append("jobdesc", jState.description);
    dataForm.append("jobbizindustry", jState.businessIndustry);
    dataForm.append("jobtype", jState.type);
    dataForm.append("jobbizfxn", jState.businessFunction);
    dataForm.append("jobrole", jState.role);
    dataForm.append("jobminqualify", jState.minQualification);
    dataForm.append("jobyearsofexp", jState.yearsOfExperience);
    dataForm.append("jobspec", jState.specialization);
    dataForm.append("jobskills", jState.skills);
    dataForm.append("jobmaxage", jState.maxAge);
    dataForm.append("jobgenderpref", jState.preferredGender);
    dataForm.append(
      "jobsalary",
      JSON.stringify({ "from": jState.salaryFrom, "to": jState.salaryTo })
    );
    dataForm.append("jobappxndeadline", jState.applicationDealine);
    dataForm.append("jobadvncelisting", JSON.stringify(jState.advanceListing));
    dataForm.append("jimg", jState.jobImages);
    for (let i = 0; i < jState.jobImages.length; i++) {
    }

    let xhr = new XMLHttpRequest();
    xhr.open("POST", "/api/seller/my-product/joblistings", true);
    xhr.onload = () => {
      console.log(xhr.response, "job listing product");
      if (xhr.status == 200 && xhr.readyState == 4) {
        if (xhr.responseText) {
          let res = JSON.parse(xhr.responseText);
          if (res.success) {
            // console.log(xhr.responseText," the job listing here ")
            setJstate({ isSubmitted: true });
            // isSubmitted(true)
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataForm);
  };

  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="create-product-job">
          <div className="content-top">
            <h5 className="header">Create a Job listing post</h5>
            <h6 className="looking">Looking for Talent?</h6>
            <p className="header-text">
              Tinkoko market place also gives you access to find the best fit
              for your organisation. You can use our advanced tools to
              streamline your hiring process or create a job listing with the
              basic options.
            </p>
          </div>
          <div className="create-product-job-form">
            <form action="">
              <h6 className="note">
                Note that Buy Now products must be fulfilled and customer
                satisfied before payment is released to a seller.
              </h6>
              <div className="form-row">
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-category"
                      onChange={(el) => {
                        jobSubcategories(el.target.value);
                        setJstate({ category: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {categories.map((cat, key) => (
                        <option
                          key={key}
                          value={cat.name}
                          onClick={() => setJstate({ category: cat.name })}
                        >
                          {cat.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Category*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-subcategory"
                      onChange={(el) =>
                        setJstate({ subCategory: el.target.value })
                      }
                    >
                      <option value=""></option>
                      {subCategories.map((subcat, key) => (
                        <option key={key} value={subcat}>
                          {subcat}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Subcategory*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
              </div>
              {/* <!-- JOB IMAGE UPLOAD --> */}
              <div className="image-upload mb-5">
                <h6>Add photos (Maximum of 1 photo, 3mb)</h6>
                <div className="choose-image">
                  {!previewImages && (
                    <a onClick={chooseImage} className="choose">
                      <i className="fa fa-plus"></i>
                    </a>
                  )}
                  <input
                    type="file"
                    id="job-photo"
                    onInput={(event) => readImage(event)}
                  />

                  <div className="preview-zone">
                    <div className="image-container">
                      {previewImages && (
                        <a className="change" onClick={chooseImage}>
                          <i className="fa fa-redo-alt"></i>
                        </a>
                      )}
                      {previewImages && (
                        <>
                          <img
                            id="preview-image"
                            src={previewImages}
                            className="img-fluid"
                          />
                          <div
                            id="remove-image"
                            className="close"
                            onClick={() => cancelImage(previewImages)}
                          >
                            X
                          </div>
                        </>
                      )}
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-row">
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-region"
                      onChange={(el) => {
                        setJstate({ region: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {regions.map((reg, key) => (
                        <option key={key} value={reg.name}>
                          {" "}
                          {reg.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Region*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="create-floating-label">
                    <select
                      name=""
                      id="job-country"
                      onChange={(el) => {
                        setJstate({ country: el.target.value });
                      }}
                    >
                      <option value=""></option>
                      {countries.map((c, key) => (
                        <option value={c.name} key={key}>
                          {c.name}
                        </option>
                      ))}
                    </select>
                    <label htmlFor="">Country*</label>
                    <i className="fa fa-caret-down arrow-down"></i>
                  </div>
                </div>
              </div>
              {/* <!-- TITLE FIELD --> */}
              <div className="title-field">
                <div className="d-flex justify-content-end">
                  <span className="short">Keep it short</span>
                  <span className="count">
                    <span id="title-count">{tlengthCounter}</span> / 60
                  </span>
                </div>
                <input
                  type="text"
                  name="title"
                  id="title"
                  onChange={(e) => {
                    setTlengthCounter(e.target.value.length);
                    setJstate({ title: e.target.value });
                    // setTitle(e.target.value);
                  }}
                  placeholder="Title*"
                  maxLength="60"
                />
              </div>
              {/* <!-- JOB DESCRIPTION --> */}
              <div className="description-field">
                <label
                  htmlFor="description"
                  className="d-flex justify-content-between"
                >
                  Job description*
                  <span className="count">
                    <span id="description-count">{dlengthCounter}</span> / 2000
                  </span>
                </label>
                <textarea
                  name="description"
                  id="description"
                  cols="30"
                  rows="8"
                  onChange={(e) => {
                    setDlengthCounter(e.target.value.length);
                    // setDescription(e.target.value);
                    setJstate({ description: e.target.value });
                  }}
                ></textarea>
              </div>
              {/* <!-- WORK INFO AND PREFERENCES --> */}
              <div className="work-info-and-preferences">
                <p className="text">Work info and Candidate preferences</p>
                <div className="form-row">
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-business-industry"
                        onChange={(el) =>
                          setJstate({ businessIndustry: el.target.value })
                        }
                      >
                        <option value=""></option>
                        {businessIndustries &&
                          businessIndustries.map((bizind, key) => (
                            <option key={key} value={bizind.name}>
                              {bizind.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Business Industry*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-type"
                        onChange={(el) => setJstate({ type: el.target.value })}
                      >
                        <option value=""></option>
                        {jobTypes &&
                          jobTypes.map((j, key) => (
                            <option value={j.name} key={key}>
                              {j.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Job type*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-business-function"
                        onChange={(el) =>
                          setJstate({ businessFunction: el.target.value })
                        }
                      >
                        <option value=""></option>
                        {businessFunctions &&
                          businessFunctions.map((bizfn, key) => (
                            <option key={key} value={bizfn.name}>
                              {bizfn.name}
                            </option>
                          ))}
                      </select>
                      <label htmlFor="">Business function*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-role"
                        onInput={(el) => setJstate({ role: el.target.value })}
                      />
                      <label htmlFor="">Role / Designation (optional)</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-min-qualification"
                        onChange={(el) =>
                          setJstate({ minQualification: el.target.value })
                        }
                      >
                        <option value=""></option>
                        {minimumQualifications.map((min, key) => (
                          <option value={min.name} key={key}>
                            {min.name}
                          </option>
                        ))}
                      </select>
                      <label htmlFor="">Minimum Qualification (optional)</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-years-of-experience"
                        onChange={(el) =>
                          setJstate({ yearsOfExperience: el.target.value })
                        }
                      >
                        <option value=""></option>
                        {yearsOfExperience.map((yoe, key) => (
                          <option key={key} value={yoe.name}>
                            {yoe.name}
                          </option>
                        ))}
                      </select>
                      <label htmlFor="">Required years of experience*</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-specialization"
                        onInput={(el) =>
                          setJstate({ specialization: el.target.value })
                        }
                      />
                      <label htmlFor="">
                        Educational specialization (optional)
                      </label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="text"
                        name=""
                        id="job-skills"
                        onInput={(el) => setJstate({ skills: el.target.value })}
                      />
                      <label htmlFor="">Skills*</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="number"
                        name=""
                        id="job-max-age"
                        onInput={(el) => setJstate({ maxAge: el.target.value })}
                      />
                      <label htmlFor="">Maximum age (optional)</label>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <select
                        name=""
                        id="job-gender"
                        onChange={(el) =>
                          setJstate({ preferredGender: el.target.value })
                        }
                      >
                        <option value="Any">Any</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                      </select>
                      <label htmlFor="">Gender preference (optional)</label>
                      <i className="fa fa-caret-down arrow-down"></i>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="salary-field">
                      <p>(&#8358;) Salary (per month) (optional)</p>
                      <input
                        type="number"
                        name="salaryFrom"
                        id="job-salary-from"
                        placeholder="From"
                        onInput={(el) =>
                          setJstate({ salaryFrom: el.target.value })
                        }
                      />
                      <input
                        type="number"
                        name=""
                        id="job-salary-to"
                        onInput={(el) =>
                          setJstate({ salaryTo: el.target.value })
                        }
                        placeholder="To"
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="create-floating-label">
                      <input
                        type="date"
                        name=""
                        id="job-application-deadline"
                        onInput={(el) =>
                          setJstate({ applicationDealine: el.target.value })
                        }
                      />
                      <label htmlFor="">Application deadline</label>
                      <i className="fa fa-calendar-alt icon"></i>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- ADVANCED JOB LISTING TOOLS --> */}
              <div className="advanced-listing-tools">
                <div
                  className="checkbox-field green-checkbox"
                  id="job-advanced-listing"
                >
                  <input
                    type="checkbox"
                    name="advanced"
                    id=""
                    className="checkbox"
                    defaultChecked
                    onInput={(el) =>
                      setJstate({ advanceListing: ["Use Advance Listing"] })
                    }
                  />
                  <label htmlFor="advanced" className="label">
                    Use advanced job listing tools
                  </label>
                  <p>
                    Note: All applicants will be able to apply via our platform.
                    Select the information you wish to receive from each
                    applicant.
                  </p>
                </div>
                <div className="all-other-options">
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="fullname"
                      id="fullname"
                      className="checkbox"
                      data-name="Full Name"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="fullname" className="label">
                      Full Name
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="email"
                      id="email"
                      className="checkbox"
                      data-name="Email "
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="email" className="label">
                      Email
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="dob"
                      id="dob"
                      className="checkbox"
                      data-name="Date of birth"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="dob" className="label">
                      Date of Birth
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="location"
                      id="location"
                      className="checkbox"
                      data-name="Location "
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="location" className="label">
                      Location
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="experience"
                      id="experience"
                      className="checkbox"
                      data-name="Experience"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="experience" className="label">
                      Experience
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="currentsalary"
                      id="currentsalary"
                      className="checkbox"
                      data-name="Current Salary"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="currentsalary" className="label">
                      Current Salary
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="hel"
                      id="hel"
                      className="checkbox"
                      data-name="Highest Education Level"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="hel" className="label">
                      Highest education level
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="cbf"
                      id="cbf"
                      className="checkbox"
                      data-name="Current business function"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="cbf" className="label">
                      Current business function
                    </label>
                  </div>
                  <div className="checkbox-field">
                    <input
                      type="checkbox"
                      name="acv"
                      id="acv"
                      className="checkbox"
                      data-name="Attachec CV"
                      onChange={(el) => {
                        setJstate({
                          advanceListing: [
                            ...jState.advanceListing,
                            el.target.dataset.name,
                          ],
                        });
                      }}
                    />
                    <label htmlFor="acv" className="label">
                      Attached CV
                    </label>
                  </div>
                </div>
                <p className="pl-4 mt-2">
                  Tip: Promote this ad to reach more people.
                </p>
              </div>
              {/* <!-- SUBMIT --> */}
              <div className="submit-field text-center">
                <button type="button" onClick={validateForm}>
                  Submit Post
                </button>
                <p>
                  By clicking on submit post, you accept the Terms of use,
                  confirm that you will abide by the safety tips, and declare
                  that posting does not include any prohibited items.
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default JoblistingDetail;
