import React from "react";
import { useContext, useState, useEffect } from "react";
import getMoreViewsImg from "../../../images/get-more-views.PNG";
import sellFastImg from "../../../images/sell-fast.PNG";

import { AuthContext } from "../../context/authContext/AuthState";
import { PromoContext } from "./PromoState";

const MyPromotions = () => {
  const authCxt = useContext(AuthContext);
  const promoCxt = useContext(PromoContext);
  const { setValue, state } = promoCxt;

  useEffect(() => {
    getPromotions();
  }, [authCxt.token]);
  const getPromotions = () => {
    if (!authCxt.token) return;
    const xhr = new XMLHttpRequest();
    xhr.open("GET", `/api/seller/my-promotions`, true);
    xhr.onload = () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            let res = JSON.parse(xhr.responseText).success;
            if (Array.isArray(res)) {
              setValue({ myPromotions: [...res] });
            }
          }
        }
      }
    };
    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send();
  };
  return (
    <>
      <div className="col-md-9">
        <div className="dashboard-main-content">
          <div className="content-header">
            <h4>My Promotions</h4>
            <a href="#" className="learn-more">
              Learn more about promotions
            </a>
          </div>

          <div className="promotions-header mb-5">
            <div className="row">
              <div className="col-md-6">
                <div className="media">
                  <img className="img-fluid" src={getMoreViewsImg} alt="" />
                  <div className="media-body">
                    <h6>Get more views</h6>
                    <p>
                      Promotions are designed to get more people to see your
                      posts. By promoting your posts, you save time and effort
                      in marketing your products to a large target audience. You
                      also save time and effort, and still get more engagement
                      to your posts.
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-6">
                <div className="media">
                  <img className="img-fluid" src={sellFastImg} alt="" />
                  <div className="media-body">
                    <h6>Sell fast for a good price</h6>
                    <p>
                      Increase engagement and visibility of your products,
                      services, job posts with promotion campaigns. Get more
                      leads, interests and better conversion rates.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="create-new-promotion">
            <div className="row">
              <div className="col-md-6">
                <a href="/seller/my-promotions/new">New product promotion</a>
              </div>
              <div className="col-md-4 offset-md-2">
                <form action="">
                  <div className="form-row align-items-baseline">
                    <div className="col-md-12">
                      <div className="input-container">
                        <i className="fa fa-search icon"></i>
                        <input
                          type="search"
                          name=""
                          id=""
                          className="input-field"
                        />
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>

          <div className="table-container">
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">Advert ID</th>
                    <th scope="col">Type of Advert</th>
                    <th scope="col">Date Started</th>
                    <th scope="col">Ends</th>
                    <th scope="col">Status</th>
                    <th scope="col">Action</th>
                  </tr>
                </thead>
                <tbody>
                  {state.myPromotions.map((promo, key) => (
                    <tr key={key}>
                      <td>{promo._id}</td>
                  <td>{promo.type}</td>
                      <td>
                        {promo.date_created}<span className="d-block">03:56pm</span>
                      </td>
                      <td>
                        11 Jun 2020 <span className="d-block">03:56pm</span>
                      </td>
                      <td className="active-advert">Active</td>
                      <td>
                        <button
                          className="view"
                          data-toggle="modal"
                          data-target="#view-statistics"
                        >
                          <i className="fa fa-eye"></i> View Statistics
                        </button>
                      </td>
                      <div
                        className="modal view-statistics fade"
                        id="view-statistics"
                        tabindex="-1"
                        role="dialog"
                        aria-hidden="true"
                      >
                        <button
                          type="button"
                          className="close"
                          data-dismiss="modal"
                          aria-label="Close"
                        >
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <div
                          className="modal-dialog modal-lg modal-dialog-centered"
                          role="document"
                        >
                          <div className="modal-content">
                            <div className="modal-body">
                              <div className="promotion-details">
                                <h6>CID1223232434</h6>
                                <p>
                                  Date:{" "}
                                  <span>
                                    3 Jul 2020, 00:00 - 7 Jul 2020, 00:00
                                  </span>
                                </p>
                              </div>
                              <h6 className="campaign-name">
                                Campaign:{" "}
                                <span>Product name goes here.....</span>
                              </h6>
                              <div className="statistics-box">
                                <div className="box visitors">
                                  <h6>110</h6>
                                  <p>Visitors</p>
                                </div>
                                <div className="box phone-views">
                                  <h6>18</h6>
                                  <p>Phone view</p>
                                </div>
                                <div className="box chats">
                                  <h6>5</h6>
                                  <p>Chats</p>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          <div className="pagination-number-of-items d-flex justify-content-between align-content-center align-items-baseline flex-wrap">
            <div className="pagination d-flex align-items-center align-content-center flex-md-row flex-sm-column flex-wrap">
              <div>
                <label for="page" className="grey-text">
                  Go to Page:
                </label>
                <input type="number" name="page" id="page" placeholder="Page" />
              </div>
              <ul>
                <li>
                  <a href="#">
                    <i className="fa fa-step-backward"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play rotate-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-play play-icon"></i>
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i className="fa fa-step-forward"></i>
                  </a>
                </li>
              </ul>
            </div>
            <div className="number-per-page promotion-arrow">
              <label for="">Items per page:</label>
              <select name="" id="">
                <option value="10">10</option>
              </select>
              <i className="fa fa-chevron-down arrow-down"></i>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default MyPromotions;
