import React from "react";
import { Redirect } from "react-router-dom";
import { useContext, useEffect } from "react";
import { SupportContext } from "./SupportState";
import { AuthContext } from "../../context/authContext/AuthState";
import { errorBorder } from "../../utils/errorBorder";

const CreateSupport = () => {
  const authCxt = useContext(AuthContext);
  const supportCxt = useContext(SupportContext);
  const { setValue, state } = supportCxt;
  useEffect(() => {}, [authCxt.token]);

  function readImage(event) {
    if (window.File && window.FileList && window.FileReader) {
      var files = event.target.files; //FileList object
      for (let i = 0; i < files.length; i++) {
        if (i <= 10) {
          var file = files[i];
          if (!file.type.match("image")) continue;
          setValue({ files: [...state.files, { file }] });
        }
      }

      event.target.value = "";
    } else {
      console.log("Browser not support");
    }
  }

  const validateForm = () => {
    if (state.issue) {
      if (state.comment) {
        if (authCxt.token) {
          submitSupport();
        } else {
          return <Redirect to="/login" />;
        }
      } else {
        return errorBorder(
          "#detailed-comment",
          "You can not leave the comment field empty"
        );
      }
    } else {
      return errorBorder(
        "#contact-regarding",
        "Kindly select a the reason for this support"
      );
    }
  };
  const submitSupport = () => {
    let dataform = new FormData();
    dataform.append("reason", state.issue);
    dataform.append("comment", state.comment);
    for (let i = 0; i < state.files.length; i++) {
      dataform.append("screenshots", state.files[i].file);
    }
    let xhr = new XMLHttpRequest();
    xhr.open("POST", "/api/seller/supports/open-ticket", true);
    xhr.onload = () => {
      if (xhr.readyState === 4 && xhr.status === 200) {
        if (xhr.responseText) {
          if (JSON.parse(xhr.responseText)) {
            if (Object.keys(JSON.parse(xhr.responseText)).length > 0) {
              let res = JSON.parse(xhr.responseText).success;
              if (res) {
                console.log(res, "the respnose text from backend ");
              }
            }
          }
        }
      }
    };

    xhr.setRequestHeader("x-auth-user", authCxt.token);
    xhr.send(dataform);
  };
  return (
    <div className="col-md-9">
      <div className="dashboard-main-content">
        <div className="content-header adverts-top">
          <h4 className="">
            <a href="claim-feedback-all.html">
              <i className="fa fa-arrow-alt-circle-left"></i>
              Support Ticket
            </a>
          </h4>
        </div>

        <div className="row justify-content-center">
          <div className="col-md-8 ">
            <div className="create-claim-form">
              <h5>Tinkoko Sellers Contact Form</h5>
              <p className="please-use">
                Please use this form to raise any enquiries or claims related to
                your seller partnership with Tinkoko.
              </p>
              <form
                action=""
                enctype="multipart/form-data"
                onSubmit={(e) => {
                  e.preventDefault();
                  validateForm();
                }}
              >
                <p className="red-text">
                  Make sure you choose carefully the appropriate reason for your
                  claim and properly fill out every required field: the more
                  detailed your submission, the less time required to solve it.
                </p>
                <div className="form-field">
                  <label for="contact-regarding">
                    I am contacting Tinkoko regarding
                  </label>
                  <select
                    name="contact-regarding"
                    id="contact-regarding"
                    onChange={(el) => {
                      setValue({ issue: el.target.value });
                    }}
                  >
                    <option value="Withdrawals & Payments">
                      Withdrawals and payments
                    </option>
                    <option value="Order and delivery">
                      Order and delivery
                    </option>
                  </select>
                  <i className="fa fa-chevron-down arrow-down"></i>
                </div>
                <div className="form-field">
                  <h6>Upload a screenshot or document (Max of 2 files)</h6>
                  <p>
                    Please ensure that uploaded documents and photographs are of
                    high quality.
                  </p>
                  <label
                    for="screenshot-or-document"
                    className="custom-file-upload"
                  >
                    <i className="fa fa-plus"></i>
                    Upload File (Max size 3mb)
                  </label>
                  <input
                    type="file"
                    name="screenshot-or-document"
                    id="screenshot-or-document"
                    accept="image/*"
                    multiple
                    onInput={(el) => {
                      readImage(el);
                    }}
                  />
                  <div id="target"></div>
                </div>
                <div className="form-field">
                  <label for="detailed-comment">Detailed Comment</label>
                  <textarea
                    name="detailed-comment"
                    id="detailed-comment"
                    cols="30"
                    rows="6"
                    onChange={(el) => {
                      setValue({ comment: el.target.value });
                    }}
                  ></textarea>
                </div>
                <div className="proceed">
                  <button type="submit" className="proceed-to-payment">
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateSupport;
